import Icon from "./icon";
import FileInput from "./form/FileInput.vue";
import Button from "./form/Button.vue";
import Checkbox from "./form/Checkbox.vue";
import Radio from "./form/Radio.vue";
import NumberInput from "./form/NumberInput.vue";
import Select from "./form/Select.vue";
import Tabs from "./tab/Tabs.vue";
import Tab from "./tab/Tab.vue";
import Accordion from "./accordion/Accordion.vue";
import AccordionItem from "./accordion/AccordionItem.vue";
import Pagination from "./pagination/Pagination.vue";
import Table from "./table/table.vue";
import Tooltip from "./tooltip/Tooltip";
import Popover from "./tooltip/Popover.vue";
import ClosePopover from "./tooltip/ClosePopover";

export default {
  install(Vue) {
    Vue.component("icon", Icon);
    Vue.component("wpt-file-input", FileInput);
    Vue.component("wpt-button", Button);
    Vue.component("wpt-checkbox", Checkbox);
    Vue.component("wpt-radio", Radio);
    Vue.component("wpt-number-input", NumberInput);
    Vue.component("wpt-select", Select);
    Vue.component("wpt-tabs", Tabs);
    Vue.component("wpt-tab", Tab);
    Vue.component("wpt-accordion", Accordion);
    Vue.component("wpt-accordion-item", AccordionItem);
    Vue.component("wpt-pagination", Pagination);
    Vue.component("wpt-table", Table);
    Vue.component("v-popover", Popover);
    Vue.directive("tooltip", Tooltip);
    Vue.directive("close-popover", ClosePopover);
  }
};



