import WptConstants from "~/WptConstants";

export default {
  setScreenSize({ commit, rootState }, { width, height }) {
    commit("setWidth", width);
    commit("setHeight", height);
    if (width < rootState.options.smallScreenBreakpoint) {
      commit("setScreenSize", WptConstants.SIZE_SMALL);
    } else if (width <= rootState.options.mediumScreenBreakpoint) {
      commit("setScreenSize", WptConstants.SIZE_MEDIUM);
    } else {
      commit("setScreenSize", WptConstants.SIZE_LARGE);
    }
  },

  loadingMessage({ commit }, message) {
    commit("setMessage", { text: message, loading: true });
  },

  clearMessage({ commit }) {
    commit("setMessage", {});
  },

  errorMessage({ commit }, error) {
    commit("setMessage", {
      text: (error.response && error.response.text) || error.message || error
    });
  }
};



