<template>
  <div>
    <div
      v-if="uiFlags.sheetExpandCollapse && pivotDataReady"
      class="wpt-flex-row"
    >
      <wpt-button
        v-tooltip="{content: $t('Lbl_ExpandRows'), container: popoverContainer}"
        type="plain"
        icon="expand-rows"
        @click="() => expandCollapseAll(1, true)"
      />
      <wpt-button
        v-tooltip="{content: $t('Lbl_CollapseRows'), container: popoverContainer}"
        type="plain"
        icon="collapse-rows"
        @click="() => expandCollapseAll(1, false)"
      />
      <wpt-button
        v-tooltip="{content: $t('Lbl_ExpandColumns'), container: popoverContainer}"
        type="plain"
        icon="expand-columns"
        @click="() => expandCollapseAll(0, true)"
      />
      <wpt-button
        v-tooltip="{content: $t('Lbl_CollapseColumns'), container: popoverContainer}"
        type="plain"
        icon="collapse-columns"
        @click="() => expandCollapseAll(0, false)"
      />
    </div>

    <div
      v-if="source.mode === WptConstants.MODE_MEMORY && pivotDataReady"
      class="wpt-flex-column"
    >
      <wpt-checkbox v-model="showRowTotals">{{
        $t("Lbl_ShowTotalRows")
      }}</wpt-checkbox>
      <wpt-checkbox v-model="showColTotals">{{
        $t("Lbl_ShowTotalColumns")
      }}</wpt-checkbox>
      <wpt-checkbox v-model="showRowSubtotals">{{
        $t("Lbl_ShowSubtotalRows")
      }}</wpt-checkbox>
      <wpt-checkbox v-model="showColSubtotals">{{
        $t("Lbl_ShowSubtotalColumns")
      }}</wpt-checkbox>
    </div>

  </div>
</template>

<script>
export default {
  computed: {
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    source() {
      return this.$store.state.wpt.source;
    },
    sheet() {
      return this.$store.state.wpt.sheet;
    },
    options() {
      return this.$store.state.options;
    },
    uiFlags() {
      return this.options.uiFlags;
    },
    highLightColor() {
      return this.$store.state.options.styles.highLightColor;
    },
    pivotDataReady() {
      const sheetData = this.sheet.sheetData;
      return sheetData && sheetData.pivotData && sheetData.pivotData.valueObj;
    },
    sheetOptions() {
      return this.options.wpt.sheet;
    },

    showRowTotals: {
      get() {
        return !!this.sheetOptions.showRowTotals;
      },
      set(value) {
        this.changeSheetOptions({ showRowTotals: value ? 1 : 0 }, true);
      }
    },
    showColTotals: {
      get() {
        return !!this.sheetOptions.showColTotals;
      },
      set(value) {
        this.changeSheetOptions({ showColTotals: value ? 1 : 0 }, true);
      }
    },
    showRowSubtotals: {
      get() {
        return !!this.sheetOptions.showRowSubtotals;
      },
      set(value) {
        this.changeSheetOptions({ showRowSubtotals: value ? 1 : 0 }, true);
      }
    },
    showColSubtotals: {
      get() {
        return !!this.sheetOptions.showColSubtotals;
      },
      set(value) {
        this.changeSheetOptions({ showColSubtotals: value ? 1 : 0 }, true);
      }
    }
  },

  methods: {
    expandCollapseAll(axis, expand) {
      this.$store.dispatch("wpt/expandCollapseAll", {
        pivot: this.sheet.pivot,
        sheetOptions: this.sheetOptions,
        collapsed: this.sheet.collapsed,
        source: this.source,
        axis,
        expand
      });
    },

    changeSheetOptions(option, updateLayoutData) {
      this.$store.commit("options/changeSheetOptions", option);
      if (updateLayoutData) {
        this.$store.dispatch("wpt/updateLayoutData", {
          pivot: this.sheet.pivot,
          sheetOptions: this.sheetOptions,
          collapsed: this.sheet.collapsed,
          source: this.source,
          pivotData: this.sheet.sheetData.pivotData
        });
      }
    }
  }
};
</script>

<style></style>



