<template>
  <div 
    class="wpt-flex-row" 
    style="margin: 3px;">
    <wpt-button
      v-tooltip="{ content: $t('Btn_Refresh'), container: popoverContainer }"
      icon="refresh"
      type="plain"
      @click="refresh"
    />

    <wpt-button
      v-if="source.mode === WptConstants.MODE_OLAP"
      :label="$t('Btn_Mdx')"
      type="plain"
      @click="openMdx"
    />
  </div>
</template>

<script>
export default {
  props: {
    sheet: {
      type: Object,
      required: true
    },
    source: {
      type: Object,
      required: true
    }
  },
  computed: {
    popoverContainer() {
      return "." + this.$store.state.system.containerClass;
    }
  },

  methods: {
    openMdx() {
      this.$store.commit("dialog/open", {
        type: "SHEET_MDX_STATEMENT",
        title: this.$t("Btn_Mdx"),
        width: 500,
        payload: {
          sheet: this.sheet,
          source: this.source
        }
      });
    },
    refresh() {
      this.$store.dispatch("wpt/generatePivotData", {
        pivot: this.sheet.pivot
      });
    }
  }
};
</script>

<style></style>



