import Worker from "simple-web-worker";

import WptConstants from "~/WptConstants";
import { mergeRecursive } from "~/services/CommonUtils";
import i18n from "~/i18n";
import { executeMdx, generateOlapPivotData } from "~/services/OlapService";
import {
  getFilteredData,
  generateMemoryPivotData
} from "~/services/MemoryService";
import { generateMdxString } from "~/services/SheetService";
import { generateSheetLayoutData } from "~/services/SheetLayoutService";

const replaceSheetData = (store, sheetData) => {
  store.commit("replaceSheetData", { sheetData });
};

const updateSheetData = (store, sheetData) => {
  store.commit("updateSheetData", { sheetData });
};

export default {
  updateLayoutData(
    store,
    { pivot, sheetOptions, collapsed, source, pivotData }
  ) {
    const limit = store.rootState.options.limit;
    const layoutData = generateSheetLayoutData(
      source.mode,
      pivotData,
      pivot,
      limit,
      sheetOptions,
      collapsed
    );
    replaceSheetData(store, { pivotData, layoutData });
  },

  generatePivotData(store, { pivot }) {
    const sheet = store.rootState.wpt.sheet;
    const source = store.rootState.wpt.source;
    const sheetData = sheet.sheetData;

    if (!sheetData || !sheetData.loading) {
      replaceSheetData(store, { loading: true });
      let start = new Date();

      const sheetOptions = store.rootState.options.wpt.sheet;
      const collapsed = sheet.collapsed;

      if (
        pivot.rows.length === 0 &&
        pivot.columns.length === 0 &&
        pivot.values.length === 0
      ) {
        store.dispatch("updateLayoutData", {
          pivot,
          sheetOptions,
          collapsed,
          source,
          pivotData: {}
        });
        return;
      }

      try {
        switch (source.mode) {
          case WptConstants.MODE_MEMORY: {
            store.dispatch(
              "system/loadingMessage",
              i18n.t("Msg_GeneratingPivotData"), {root: true}
            );

            const filteredData = getFilteredData(pivot, source, WptConstants);

            Worker.run(generateMemoryPivotData, [
              pivot,
              filteredData,
              WptConstants
            ])
              .then(pivotData => {
                store.dispatch("updateLayoutData", {
                  pivot,
                  sheetOptions,
                  collapsed,
                  source,
                  pivotData
                });
                store.dispatch("system/clearMessage", null, {root: true});
              })
              .catch(error => {
                store.dispatch("system/errorMessage", error, {root: true});
                replaceSheetData(store, { loading: false, error });
              });
            break;
          }

          case WptConstants.MODE_OLAP:
            if (!source.fields) {
              store.dispatch(
                "wpt/getOlapCubeFields",
                { source },
                { root: true }
              );
            } else {
              const mdx = generateMdxString(pivot, source);

              if (mdx) {
                const serverOptions = store.rootState.options.server;

                store.dispatch(
                  "system/loadingMessage",
                  i18n.t("Msg_GeneratingPivotData"), {root: true}
                );
                executeMdx(serverOptions, mdx, source, (error, response) => {
                  // console.log("Execute mdx takes:", new Date()- start);
                  if (error) {
                    store.dispatch("system/errorMessage", error, {root: true});
                    replaceSheetData(store, { loading: false, error });
                  } else {
                    start = new Date();
                    const pivotData = generateOlapPivotData(response);
                    store.dispatch("updateLayoutData", {
                      pivot,
                      sheetOptions,
                      collapsed,
                      source,
                      pivotData
                    });
                    store.dispatch("system/clearMessage", null, {root: true});
                  }
                });
              } else {
                const pivotData = {
                  colTree: [],
                  rowTree: [],
                  valueObj: {}
                };
                store.dispatch("updateLayoutData", {
                  pivot,
                  sheetOptions,
                  collapsed,
                  source,
                  pivotData
                });
              }
            }
            break;

          default:
        }
      } catch (error) {
        store.dispatch("system/errorMessage", error, {root: true});
        replaceSheetData(store, { loading: false, error });
      }
    }
  },

  generatePivotDataFromMdx(store, { sheet, source, mdx }) {
    const pivot = sheet.pivot;
    const sheetData = sheet.sheetData;

    if (!sheetData || !sheetData.loading) {
      replaceSheetData(store, { loading: true });
      let start = new Date();

      const sheetOptions = store.rootState.options.wpt.sheet;
      const collapsed = sheet.collapsed;

      try {
        const serverOptions = store.rootState.options.server;

        store.dispatch(
          "system/loadingMessage",
          i18n.t("Msg_GeneratingPivotData"), {root: true}
        );
        executeMdx(serverOptions, mdx, source, (error, response) => {
          // console.log("Execute mdx takes:", new Date()- start);
          if (error) {
            store.dispatch("system/errorMessage", error, {root: true});
            replaceSheetData(store, { loading: false, error });
          } else {
            start = new Date();
            const pivotData = generateOlapPivotData(response);
            store.dispatch("updateLayoutData", {
              pivot,
              sheetOptions,
              collapsed,
              source,
              pivotData
            });
            store.dispatch("system/clearMessage", null, {root: true});
          }
        });
      } catch (error) {
        store.dispatch("system/errorMessage", error, {root: true});
        replaceSheetData(store, { loading: false, error });
      }
    }
  },

  switchTreeItemExpanded(store, { id }) {
    const sheet = store.state.sheet;
    const pivotData = sheet.sheetData.pivotData;
    const source = store.state.source;
    const pivot = sheet.pivot;
    const sheetOptions = store.rootState.options.wpt.sheet;
    const collapsed = sheet.collapsed;
    const itemId =
      source.mode === WptConstants.MODE_MEMORY ? id.replace(/V[0-9]*/, "") : id;
    if (collapsed[itemId]) {
      delete collapsed[itemId];
    } else {
      collapsed[itemId] = true;
    }

    store.commit("updateSheetCollapsed", { collapsed });
    store.dispatch("updateLayoutData", {
      pivot,
      sheetOptions,
      collapsed,
      source,
      pivotData
    });
  },

  expandCollapseAll(store, { pivot, sheetOptions, collapsed, axis, expand }) {
    const sheet = store.state.sheet;
    const source = store.state.source;
    const pivotData = { ...sheet.sheetData.pivotData };
    const tree = axis === 0 ? pivotData.colTree : pivotData.rowTree;

    if (source.mode === WptConstants.MODE_MEMORY) {
      const expandCollapseItem = item => {
        if (item.children) {
          item.children.forEach(child => {
            expandCollapseItem(child);
          });
        }
        if (expand) {
          delete collapsed[item.id.replace(/V[0-9]*/, "")];
        } else {
          collapsed[item.id.replace(/V[0-9]*/, "")] = true;
        }
      };
      expandCollapseItem(tree);
    } else if (source.mode === WptConstants.MODE_OLAP) {
      tree.forEach(item => {
        item.fields.forEach((field, index) => {
          if (expand) {
            delete collapsed[field.id + "-" + index];
          } else {
            collapsed[field.id + "-" + index] = true;
          }
        });
      });
    }

    store.commit("updateSheetCollapsed", { collapsed });
    store.dispatch("updateLayoutData", {
      pivot,
      sheetOptions,
      collapsed,
      source,
      pivotData
    });
  }
};



