<template>
  <div>
    <wpt-button
      :style="'background-color: ' + (pageIndex === 1 ? 'orange' : '')"
      label="1"
      type="plain"
      @click.stop.prevent="jumpPage(1)"
    />

    <wpt-button
      v-tooltip="' ' + showPagingCount + ' '"
      v-if="showJumpPrev"
      :disabled="pageIndex === 1"
      label="..."
      type="plain"
      @click.stop.prevent="jumpPage(pageIndex - showPagingCount)"
    />

    <wpt-button
      v-for="num in pagingCounts"
      :key="num"
      :style="'background-color: ' + (pageIndex === num ? 'orange' : '')"
      :label="'' + num"
      type="plain"
      @click.stop.prevent="jumpPage(num)"
    />

    <wpt-button
      v-tooltip="' ' + showPagingCount + ' '"
      v-if="showJumpNext"
      label="..."
      type="plain"
      @click.stop.prevent="jumpPage(pageIndex + showPagingCount)"
    />

    <wpt-button
      v-if="pageCount > 1"
      :style="'background-color: ' + (pageIndex === pageCount ? 'orange' : '')"
      :label="'' + pageCount"
      type="plain"
      @click.stop.prevent="jumpPage(pageCount)"
    />
  </div>
</template>

<script>
export default {
  props: {
    pageCount: {
      type: Number,
      default: 0
    },
    pageIndex: {
      type: Number,
      default: 0
    },
    showPagingCount: {
      type: Number,
      default: 0
    },
    jumpPageHandler: {
      type: Function,
      default: () => {}
    }
  },
  computed: {
    numOffset() {
      return Math.floor((this.showPagingCount + 2) / 2) - 1;
    },

    showJumpPrev() {
      if (this.pageCount > this.showPagingCount + 2) {
        if (this.pageIndex > this.showPagingCount) {
          return true;
        }
      }
      return false;
    },

    showJumpNext() {
      if (this.pageCount > this.showPagingCount + 2) {
        //if (this.pageIndex < this.pageCount - this.numOffset) {
        if (this.pageIndex <= this.pageCount - this.showPagingCount) {
          return true;
        }
      }
      return false;
    },

    // 
    pagingCounts() {
      let vm = this,
        startNum,
        result = [],
        showJumpPrev = vm.showJumpPrev,
        showJumpNext = vm.showJumpNext;

      if (showJumpPrev && !showJumpNext) {
        startNum = vm.pageCount - vm.showPagingCount;
        for (let i = startNum; i < vm.pageCount; i++) {
          result.push(i);
        }
      } else if (!showJumpPrev && showJumpNext) {
        for (let i = 2; i < vm.showPagingCount + 2; i++) {
          result.push(i);
        }
      } else if (showJumpPrev && showJumpNext) {
        for (
          let i = vm.pageIndex - vm.numOffset;
          i <= vm.pageIndex + vm.numOffset;
          i++
        ) {
          result.push(i);
        }
      } else {
        for (let i = 2; i < vm.pageCount; i++) {
          result.push(i);
        }
      }

      return result;
    }
  },
  methods: {
    jumpPage(pageIndex) {
      this.jumpPageHandler(pageIndex);
    }
  }
};
</script>



