<template>
  <div :class="card_classes">
    <div 
      class="card-header" 
      @click="toggleCollapsed">
      <p class="card-header-title">
        <slot name="title" />
      </p>
      <p class="card-header-icon">
        <icon :name="isOpen ? 'collapse-rows' : 'expand-rows'" />
      </p>
    </div>
    <div 
      v-show="isOpen" 
      ref="body" 
      class="accordion-body">
      <slot name="content" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isOpen: false
    };
  },
  computed: {
    card_classes() {
      return {
        card: true,
        "card-active": this.isOpen
      };
    }
  },
  mounted() {
    this.$parent.$on("toggle", this.collapse);
  },
  destroyed() {
    this.$parent.$off("toggle");
  },
  methods: {
    collapse() {
      this.isOpen = false;
    },
    toggleCollapsed() {
      if (!this.isOpen) {
        this.$parent.$emit("toggle");
      }
      this.isOpen = !this.isOpen;
    }
  }
};
</script>

<style scoped>
.accordion-body {
  overflow: hidden;
  transition: all 700ms ease;
  background-color: aliceblue;
}

.card-header {
  display: flex;
  flex-direction: row;
}
.card-header-title {
  flex: 1;
}
</style>



