<template>
  <div>
    <div class="wpt-section wpt-flex-row">
      <div style="width: 150px;">
        {{ $t("Lbl_CalculatedField") }}
      </div>
      <div style="flex: 1">
        <input
          v-model="name" 
          placeholder="Please input" >
      </div>
      <wpt-button
        v-tooltip="{content: $t('Lbl_RemoveCalculatedField'), container: popoverContainer}"
        v-if="field"
        type="plain"
        style="padding: 3px;"
        icon="remove"
        @click="removeCalculatedField"
      />
    </div>

    <div class="wpt-section wpt-flex-column">
      <div>{{ $t("Lbl_Formula") }}</div>
      <textarea
        :rows="2" 
        v-model="formula" />
    </div>

    <div>
      {{ $t("Msg_FormatExpression") }}
    </div>

    <div>
      {{ $t("Lbl_InsertField") }}
    </div>
    <div
      :style="{width: width+'px', height: (height - 150) +'px', overflow: 'auto'}"
      class="wpt-list-container"
    >
      <div
        v-for="option of fieldsOptions"
        :key="option.value"
        class="wpt-list-item"
        @click="
          () => {
            insertField(option);
          }
        "
      >
        {{ option.label }}
      </div>
    </div>

    <div class="wpt-flex-row">
      <div
        style="flex: 1"
        class="wpt-message">
        <div v-if="dialog.message">
          {{ dialog.message.text }}
        </div>
      </div>
      <wpt-button
        :label="$t('Btn_OK')"
        @click="handleConfirm" />
    </div>

  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },
  data: function() {
    const field = this.$store.state.dialog.payload.field;
    return {
      name: field ? field.name : this.$t("Lbl_CalculatedField"),
      formula: field ? field.formula : ""
    };
  },

  computed: {
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    options() {
      return this.$store.state.options;
    },
    messageStyle() {
      return "color: " + this.options.styles.messageColor;
    },
    dialog() {
      return this.$store.state.dialog;
    },
    payload() {
      return this.dialog.payload;
    },

    sheet() {
      return this.payload.sheet;
    },

    field() {
      return this.payload.field;
    },

    source() {
      return this.$store.state.wpt.source;
    },

    fieldsOptions() {
      return this.source.fields.map(column => ({
        value: "{" + column.name + "}",
        label: column.name
      }));
    }
  },

  methods: {
    close() {
      this.$store.commit("dialog/close");
    },
    insertField(option) {
      this.formula = this.formula + option.value;
    },

    removeCalculatedField() {
      this.$store.dispatch("wpt/removeCalculatedField", {
        sheet: this.sheet,
        source: this.source,
        field: this.field
      });
    },

    handleConfirm() {
      if (!this.name.trim().length) {
        this.$store.dispatch("system/errorMessage", "please enter a name");
        return;
      }

      if (!this.formula.trim().length) {
        this.$store.dispatch("system/errorMessage", "please enter a formula");
        return;
      }

      if (this.field) {
        this.$store.dispatch("wpt/changeCalculatedField", {
          sheet: this.sheet,
          source: this.source,
          field: this.field,
          name: this.name,
          formula: this.formula
        });
      } else {
        this.$store.dispatch("wpt/newCalculatedField", {
          sheet: this.sheet,
          source: this.source,
          name: this.name,
          formula: this.formula
        });
      }
      this.close();
    }
  }
};
</script>

<style></style>



