<template>
  <wpt-button
    v-if="cloudDriveSupport"
    :label="$t('Btn_GotoCloudDrive')"
    @click="gotoCloudDrive"
  />
</template>

<script>
import * as filestack from "filestack-js";

export default {
  props: {
    accept: {
      type: Array,
      required: false,
      default: () => []
    },
    onSuccess: {
      type: Function,
      required: false,
      default: () => {}
    },
    onError: {
      type: Function,
      required: false,
      default: () => {}
    }
  },

  computed: {
    options() {
      return this.$store.state.options;
    },

    cloudDriveSupport() {
      return this.options.filestack.enabled && this.options.filestack.key;
    }
  },

  methods: {
    gotoCloudDrive() {
      const client = filestack.init(this.options.filestack.key);
      const pickOptions = {
        maxFiles: 1,
        accept: this.accept || ["text/*", "application/*"],
        fromSources: [
          "googledrive",
          "dropbox",
          "box",
          "github",
          "gmail",
          "onedrive",
          "onedriveforbusiness",
          "clouddrive"
        ],
        lang: this.$store.state.options.locale || "en",
        onUploadDone: response => {
          console.log("response", response);
          if (!response.filesUploaded || response.filesUploaded.length === 0) {
            this.onError(new Error("No file uploaded"));
          } else {
            this.onSuccess(response.filesUploaded[0].url);
          }
        },
        onFileUploadFailed: (file, error) => {
          if (this.onError) {
            this.onError(error);
          }
        }
      };
      client.picker(pickOptions).open();
    }
  }
};
</script>

<style></style>



