<template>
  <div>
    <div class="wpt-section">
      <span>{{ $t("Lbl_ChartWidth") }}</span>
      <wpt-number-input
        v-model="chartWidth"
        :min="0"
        :max="10000"
        :step="100"
        inline
        controls
        size="small"
      />
    </div>
    <div class="wpt-section">
      <span>{{ $t("Lbl_ChartHeight") }}</span>
      <wpt-number-input
        v-model="chartHeight"
        :min="0"
        :max="10000"
        :step="100"
        inline
        controls
        size="small"
      />
    </div>

    <div class="wpt-section">
      <span>{{ $t("Lbl_ChartTheme") }}</span>
      <wpt-radio
        v-for="item in themes"
        v-model="theme"
        :key="item.value"
        :value="item.value"
        :checked="theme === item.value"
        name="theme"
      >
        {{ item.label }}
      </wpt-radio>
    </div>
    <div class="wpt-section">
      <span>{{ $t("Lbl_ChartType") }}</span>
      <wpt-radio
        v-for="item in types"
        v-model="chartType"
        :key="item.value"
        :value="item.value"
        :checked="chartType === item.value"
        name="chartType"
      >
        <icon :name="item.icon" />
        {{ item.label }}
      </wpt-radio>
    </div>

    <div class="wpt-section">
      <span>{{ $t("Lbl_ChartStacked") }}</span>
      <wpt-radio
        v-for="item in [
          {
            label: $t('Lbl_NoStacked'),
            value: null
          },
          {
            label: $t('Lbl_ValueStacked'),
            value: 'normal'
          },
          {
            label: $t('Lbl_PercentStacked'),
            value: 'percent'
          }
        ]"
        v-model="stacking"
        :key="item.value"
        :value="item.value"
        :checked="stacking === item.value"
        name="stacking"
      >
        {{ item.label }}
      </wpt-radio>
    </div>

    <div class="wpt-section">
      <wpt-checkbox v-model="options3d">
        {{ $t("Lbl_3DView") }}
      </wpt-checkbox>
    </div>

    <div 
      v-if="options3d" 
      style="padding-left: 20px;">
      <div>
        <span>{{ $t("Lbl_Alpha") }}</span>
        <wpt-number-input
          v-model="alpha"
          :min="0"
          :max="360"
          :step="15"
          inline
          controls
          size="small"
        />
      </div>
      <div>
        <span>{{ $t("Lbl_Beta") }}</span>
        <wpt-number-input
          v-model="beta"
          :min="0"
          :max="360"
          :step="15"
          inline
          controls
          size="small"
        />
      </div>
      <div>
        <span>{{ $t("Lbl_Depth") }}</span>
        <wpt-number-input
          v-model="depth"
          :min="0"
          :max="300"
          :step="10"
          inline
          controls
          size="small"
        />
      </div>
      <div>
        <span>{{ $t("Lbl_ViewDistance") }}</span>
        <wpt-number-input
          v-model="viewDistance"
          :min="0"
          :max="300"
          :step="10"
          inline
          controls
          size="small"
        />
      </div>
    </div>

    <div class="wpt-section">
      <wpt-checkbox v-model="legend">
        {{ $t("Lbl_Legend") }}
      </wpt-checkbox>
    </div>

    <div 
      v-if="legend" 
      style="padding-left:20px;">
      <div>
        <wpt-checkbox v-model="floating">
          {{ $t("Lbl_Floating") }}
        </wpt-checkbox>
        <wpt-checkbox v-model="reversed">
          {{ $t("Lbl_Reverse") }}
        </wpt-checkbox>

        <wpt-radio
          v-for="item in [
            {
              label: $t('Lbl_LegendHorizontal'),
              value: 'horizontal'
            },
            {
              label: $t('Lbl_LegendVertical'),
              value: 'vertical'
            }
          ]"
          v-model="layout"
          :key="item.value"
          :value="item.value"
          :checked="layout === item.value"
          name="layout"
        >
          {{ item.label }}
        </wpt-radio>
      </div>
      <div class="wpt-section">
        <wpt-radio
          v-for="item in alignData"
          v-model="align"
          :key="item.value"
          :value="item.value"
          :checked="align === item.value"
          name="align"
        >
          <icon :name="item.icon" />
          {{ item.label }}
        </wpt-radio>

        <wpt-radio
          v-for="item in verticalAlignData"
          v-model="verticalAlign"
          :key="item.value"
          :value="item.value"
          :checked="verticalAlign === item.value"
          name="verticalAlign"
        >
          <icon :name="item.icon" />
          {{ item.label }}
        </wpt-radio>
      </div>
    </div>

    <div class="wpt-section">
      <wpt-checkbox v-model="labels">
        {{ $t("Lbl_ShowLabels") }}
      </wpt-checkbox>
    </div>
    <div 
      v-if="labels" 
      style="padding-left: 20px;">
      <wpt-radio
        v-for="item in alignData"
        v-model="labelsAlign"
        :key="item.value"
        :value="item.value"
        :checked="labelsAlign === item.value"
        name="labelsAlign"
      >
        <icon :name="item.icon" />
        {{ item.label }}
      </wpt-radio>

      <span>{{ $t("Lbl_Rotation") }}</span>
      <wpt-number-input
        v-model="labelsRotation"
        :min="0"
        :max="360"
        :step="15"
        inline
        controls
        size="small"
      />
    </div>

    <div>
      <wpt-checkbox v-model="dataLabels">
        {{ $t("Lbl_ShowDataLabels") }}
      </wpt-checkbox>
    </div>
    <div 
      v-if="dataLabels" 
      style="padding-left: 20px;">
      <wpt-radio
        v-for="item in alignData"
        v-model="dataLabelsAlign"
        :key="item.value"
        :value="item.value"
        :checked="dataLabelsAlign === item.value"
        name="dataLabelsAlign"
      >
        <icon :name="item.icon" />
        {{ item.label }}
      </wpt-radio>

      <span>{{ $t("Lbl_Rotation") }}</span>
      <wpt-number-input
        v-model="dataLabelsRotation"
        :min="0"
        :max="360"
        :step="15"
        inline
        controls
        size="small"
      />
    </div>
  </div>
</template>

<script>
import HighchartsThemes from "~/services/HighchartsThemes";

export default {
  computed: {
    source() {
      return this.$store.state.wpt.source;
    },
    sheet() {
      return this.$store.state.wpt.sheet;
    },
    themes() {
      return HighchartsThemes;
    },
    types() {
      return [
        {
          value: "column",
          icon: "chart-column",
          label: this.$t("Lbl_ColumnChart")
        },
        {
          value: "line",
          icon: "chart-line",
          label: this.$t("Lbl_LineChart")
        },
        {
          value: "pie",
          icon: "chart-pie",
          label: this.$t("Lbl_PieChart")
        },
        {
          value: "bar",
          icon: "chart-bar",
          label: this.$t("Lbl_BarChart")
        },
        {
          value: "spline",
          icon: "chart-spline",
          label: this.$t("Lbl_SplineChart")
        },
        {
          value: "area",
          icon: "chart-area",
          label: this.$t("Lbl_AreaChart")
        },
        {
          value: "areaspline",
          icon: "chart-areaspline",
          label: this.$t("Lbl_AreaSplineChart")
        }
      ];
    },
    alignData() {
      return [
        {
          value: "left",
          icon: "align-left",
          label: this.$t("Lbl_AlignLeft")
        },
        {
          value: "center",
          icon: "align-center",
          label: this.$t("Lbl_AlignCenter")
        },
        {
          value: "right",
          icon: "align-right",
          label: this.$t("Lbl_AlignRight")
        }
      ];
    },
    verticalAlignData() {
      return [
        {
          value: "top",
          icon: "vertical-align-top",
          label: this.$t("Lbl_AlignTop")
        },
        {
          value: "middle",
          icon: "vertical-align-middle",
          label: this.$t("Lbl_AlignMiddle")
        },
        {
          value: "bottom",
          icon: "vertical-align-bottom",
          label: this.$t("Lbl_AlignBottom")
        }
      ];
    },

    options() {
      return this.$store.state.options;
    },
    toolbarStyle() {
      return "background-color: " + this.options.styles.toolBarColor;
    },
    uiFlags() {
      return this.options.uiFlags;
    },

    chartOptions() {
      return this.options.wpt.sheet.chart;
    },

    chartWidth: {
      get() {
        return this.chartOptions.width;
      },
      set(value) {
        this.changeChartOptions({ width: value });
      }
    },
    chartHeight: {
      get() {
        return this.chartOptions.height;
      },
      set(value) {
        this.changeChartOptions({ height: value });
      }
    },
    theme: {
      get() {
        return this.chartOptions.high.theme;
      },
      set(value) {
        this.changeChartOptions({ high: { theme: value } });
      }
    },
    chartType: {
      get() {
        return this.chartOptions.high.chart.type;
      },
      set(value) {
        this.changeChartOptions({ high: { chart: { type: value } } });
      }
    },
    stacking: {
      get() {
        return this.chartOptions.high.plotOptions.series.stacking;
      },
      set(value) {
        this.changeChartOptions({
          high: { plotOptions: { series: { stacking: value } } }
        });
      }
    },
    options3d: {
      get() {
        return this.chartOptions.high.chart.options3d.enabled;
      },
      set(value) {
        this.changeChartOptions({
          high: { chart: { options3d: { enabled: value } } }
        });
      }
    },
    alpha: {
      get() {
        return this.chartOptions.high.chart.options3d.alpha;
      },
      set(value) {
        this.changeChartOptions({
          high: { chart: { options3d: { alpha: value } } }
        });
      }
    },
    beta: {
      get() {
        return this.chartOptions.high.chart.options3d.beta;
      },
      set(value) {
        this.changeChartOptions({
          high: { chart: { options3d: { beta: value } } }
        });
      }
    },
    depth: {
      get() {
        return this.chartOptions.high.chart.options3d.depth;
      },
      set(value) {
        this.changeChartOptions({
          high: { chart: { options3d: { depth: value } } }
        });
      }
    },
    viewDistance: {
      get() {
        return this.chartOptions.high.chart.options3d.viewDistance;
      },
      set(value) {
        this.changeChartOptions({
          high: { chart: { options3d: { viewDistance: value } } }
        });
      }
    },

    legend: {
      get() {
        return this.chartOptions.high.legend.enabled;
      },
      set(value) {
        this.changeChartOptions({ high: { legend: { enabled: value } } });
      }
    },
    floating: {
      get() {
        return this.chartOptions.high.legend.floating;
      },
      set(value) {
        this.changeChartOptions({ high: { legend: { floating: value } } });
      }
    },
    reversed: {
      get() {
        return this.chartOptions.high.legend.reversed;
      },
      set(value) {
        this.changeChartOptions({ high: { legend: { reversed: value } } });
      }
    },
    layout: {
      get() {
        return this.chartOptions.high.legend.layout;
      },
      set(value) {
        this.changeChartOptions({ high: { legend: { layout: value } } });
      }
    },
    align: {
      get() {
        return this.chartOptions.high.legend.align;
      },
      set(value) {
        this.changeChartOptions({ high: { legend: { align: value } } });
      }
    },
    verticalAlign: {
      get() {
        return this.chartOptions.high.legend.verticalAlign;
      },
      set(value) {
        this.changeChartOptions({ high: { legend: { verticalAlign: value } } });
      }
    },

    labels: {
      get() {
        return this.chartOptions.high.xAxis.labels.enabled;
      },
      set(value) {
        this.changeChartOptions({
          high: { xAxis: { labels: { enabled: value } } }
        });
      }
    },
    labelsAlign: {
      get() {
        return this.chartOptions.high.xAxis.labels.align;
      },
      set(value) {
        this.changeChartOptions({
          high: { xAxis: { labels: { align: value } } }
        });
      }
    },
    labelsRotation: {
      get() {
        return this.chartOptions.high.xAxis.labels.rotation;
      },
      set(value) {
        this.changeChartOptions({
          high: { xAxis: { labels: { rotation: value } } }
        });
      }
    },

    dataLabels: {
      get() {
        return this.chartOptions.high.plotOptions.series.dataLabels.enabled;
      },
      set(value) {
        this.changeChartOptions({
          high: { plotOptions: { series: { dataLabels: { enabled: value } } } }
        });
      }
    },
    dataLabelsAlign: {
      get() {
        return this.chartOptions.high.plotOptions.series.dataLabels.align;
      },
      set(value) {
        this.changeChartOptions({
          high: { plotOptions: { series: { dataLabels: { align: value } } } }
        });
      }
    },
    dataLabelsRotation: {
      get() {
        return this.chartOptions.high.plotOptions.series.dataLabels.rotation;
      },
      set(value) {
        this.changeChartOptions({
          high: { plotOptions: { series: { dataLabels: { rotation: value } } } }
        });
      }
    }
  },

  methods: {
    changeTheme(value) {
      console.log("clicked", value);
      this.changeChartOptions({ high: { theme: value } });
    },
    changeChartOptions(option) {
      this.$store.commit("options/changeSheetOptions", { chart: option });
    }
  }
};
</script>

<style></style>



