<template>
  <div>
    <div class="wpt-section">
      <wpt-checkbox v-model="noFixedColumns">
        {{ "No fixed header and Columns" }}
      </wpt-checkbox>
    </div>

    <div class="wpt-section">
      <span>{{ $t("Lbl_RowHeadWidth") }}</span>
      <wpt-number-input
        v-model="rowHeaderWidth"
        :min="0"
        :max="1000"
        :step="50"
        inline
        controls
        size="small"
      />
    </div>
    <div class="wpt-section">
      <span>{{ $t("Lbl_CellWidth") }}</span>
      <wpt-number-input
        v-model="cellWidth"
        :min="0"
        :max="1000"
        :step="50"
        inline
        controls
        size="small"
      />
    </div>
    <div class="wpt-section">
      <span>{{ $t("Lbl_CellHeight") }}</span>
      <wpt-number-input
        v-model="cellHeight"
        :min="0"
        :max="100"
        :step="10"
        inline
        controls
        size="small"
      />
    </div>

    <div 
      v-if="source.mode === WptConstants.MODE_MEMORY" 
      class="wpt-section">
      <wpt-radio
        v-model="compactForm"
        :value="1"
        :checked="!!compactForm"
        name="compact"
      >{{ $t("Lbl_CompactForm") }}</wpt-radio
      >
      <wpt-radio
        v-model="compactForm"
        :value="0"
        :checked="!compactForm"
        name="compact"
      >{{ $t("Lbl_TabularForm") }}</wpt-radio
      >
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    source() {
      return this.$store.state.wpt.source;
    },
    sheet() {
      return this.$store.state.wpt.sheet;
    },
    options() {
      return this.$store.state.options;
    },
    toolbarStyle() {
      return "background-color: " + this.options.styles.toolBarColor;
    },
    uiFlags() {
      return this.options.uiFlags;
    },

    gridOptions() {
      return this.options.wpt.sheet.grid;
    },

    noFixedColumns: {
      get() {
        return this.gridOptions.noFixedColumns;
      },
      set(value) {
        this.changeGridOptions({ noFixedColumns: value });
      }
    },

    compactForm: {
      get() {
        return this.gridOptions.compactForm;
      },
      set(value) {
        this.changeGridOptions({ compactForm: value });
      }
    },
    rowHeaderWidth: {
      get() {
        return this.gridOptions.rowHeaderWidth;
      },
      set(value) {
        this.changeGridOptions({ rowHeaderWidth: value });
      }
    },
    cellWidth: {
      get() {
        return this.gridOptions.cellWidth;
      },
      set(value) {
        this.changeGridOptions({ cellWidth: value });
      }
    },
    cellHeight: {
      get() {
        return this.gridOptions.cellHeight;
      },
      set(value) {
        this.changeGridOptions({ cellHeight: value });
      }
    }
  },

  methods: {
    changeGridOptions(option) {
      this.$store.commit("options/changeSheetOptions", { grid: option });
    }
  }
};
</script>

<style></style>



