<template>
  <select v-if="type === 'plain'">
    <option
      v-for="option in options"
      :key="option[valueProp]"
      :value="option[valueProp]"
    >
      {{ option[labelProp] }}
    </option>
  </select>

  <div 
    v-else-if="type === 'standard'" 
    class="btn-group">
    <li
      v-if="selectedOption[valueProp] !== undefined"
      class="dropdown-toggle"
      @click="toggleMenu()"
    >
      {{ selectedOption[labelProp] }}
      <span class="caret" />
    </li>

    <li
      v-if="selectedOption[valueProp] === undefined"
      class="dropdown-toggle"
      @click="toggleMenu()"
    >
      {{ placeholder }}
      <span class="caret" />
    </li>

    <ul 
      v-if="showMenu" 
      class="dropdown-menu">
      <li 
        v-for="option in options" 
        :key="option[valueProp]">
        <a 
          href="javascript:void(0)" 
          @click="updateOption(option)">
          {{ option[labelProp] }}
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      required: false,
      default: () => "standard"
    },
    options: {
      type: Array,
      required: false,
      default: () => []
    },
    placeholder: {
      type: String,
      required: false,
      default: () => "Please select..."
    },
    selected: {
      type: Object,
      required: false,
      default: () => ({})
    },
    labelProp: {
      type: String,
      required: false,
      default: () => "label"
    },
    valueProp: {
      type: String,
      required: false,
      default: () => "value"
    }
  },
  data() {
    return {
      selectedOption: {
        name: ""
      },
      showMenu: false
    };
  },
  mounted() {
    this.selectedOption = this.selected;
  },
  methods: {
    updateOption(option) {
      this.selectedOption = option;
      this.showMenu = false;
      this.$emit("change", this.selectedOption[this.valueProp]);
    },
    toggleMenu() {
      this.showMenu = !this.showMenu;
    }
  }
};
</script>

<style>
.btn-group {
  min-width: 160px;
  height: 40px;
  position: relative;
  margin: 10px 1px;
  display: inline-block;
  vertical-align: middle;
}
.btn-group a:hover {
  text-decoration: none;
}
.dropdown-toggle {
  color: #636b6f;
  min-width: 160px;
  padding: 10px;
  text-transform: none;
  font-weight: 300;
  margin-bottom: 7px;
  border: 0;
  background-image: linear-gradient(#009688, #009688),
    linear-gradient(#d2d2d2, #d2d2d2);
  background-size: 0 2px, 100% 1px;
  background-repeat: no-repeat;
  background-position: center bottom, center calc(100% - 1px);
  background-color: transparent;
  transition: background 0s ease-out;
  float: none;
  box-shadow: none;
  border-radius: 0;
}
.dropdown-toggle:hover {
  background: #e1e1e1;
  cursor: pointer;
}
.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  float: left;
  min-width: 160px;
  padding: 5px 0;
  margin: 2px 0 0;
  list-style: none;
  font-size: 14px;
  text-align: left;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  background-clip: padding-box;
}
.dropdown-menu > li > a {
  padding: 10px 30px;
  display: block;
  clear: both;
  font-weight: normal;
  line-height: 1.6;
  color: #333333;
  white-space: nowrap;
  text-decoration: none;
}
.dropdown-menu > li > a:hover {
  background: #efefef;
  color: #409fcb;
}
.dropdown-menu > li {
  overflow: hidden;
  width: 100%;
  position: relative;
  margin: 0;
}
.caret {
  display: relative;
  width: 0;
  position: relative;
  top: 10px;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-top: 4px dashed;
  border-top: 4px solid \9;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  float: right;
}
li {
  list-style: none;
}
</style>



