<template>
  <div class="wpt-side-buttons">
    <wpt-button
      :class="navPanel === 'FIELDS' ? 'wpt-selected' : ''"
      :label="$t('Lbl_FieldsList')"
      :vertical="true"
      type="plain"
      @click="changeNavPanel('FIELDS')"
    />

    <template v-if="source.mode === WptConstants.MODE_MEMORY">
      <v-popover
        :container="popoverContainer"
        placement="right-end"
      >
        <wpt-button 
          icon="export" 
          type="plain" />
        <template slot="popover">
          <div 
            class="wpt-menu-item" 
            @click="exportToCsv">
            <icon name="export-csv" />
            <span style="margin-left:3px">{{ $t("Lbl_ExportToCsv") }}</span>
          </div>
          <div 
            class="wpt-menu-item" 
            @click="exportToExcel">
            <icon name="export-excel" />
            <span style="margin-left:3px">{{ $t("Lbl_ExportToExcel") }}</span>
          </div>
          <div 
            class="wpt-menu-item" 
            @click="print">
            <icon name="print" />
            <span style="margin-left:3px">{{ $t("Lbl_Print") }}</span>
          </div>
        </template>
      </v-popover>
    </template>
  </div>
</template>

<script>
import {
  exportDataSetToExcel,
  exportDataSetToCsv,
  printContent
} from "~/services/ExportService";

export default {
  props: {
    navPanel: {
      type: String,
      default: null
    },
    changeNavPanel: {
      type: Function,
      default: () => {}
    }
  },
  computed: {
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    source() {
      return this.$store.state.wpt.source;
    },
    options() {
      return this.$store.state.options;
    },
    uiFlags() {
      return this.options.uiFlags;
    }
  },

  methods: {
    exportToExcel() {
      const data = [
        this.source.fields.map(field => field.label),
        ...this.source.data
      ];
      exportDataSetToExcel(
        this.$t("Lbl_Source") + this.source.id + ".xlsx",
        this.source.name,
        data
      );
    },

    exportToCsv() {
      const data = [
        this.source.fields.map(field => field.label),
        ...this.source.data
      ];
      exportDataSetToCsv(
        this.$t("Lbl_Source") + this.source.id + ".csv",
        ",",
        data
      );
    },

    print() {
      const data = [
        this.source.fields.map(field => field.label),
        ...this.source.data
      ];
      printContent(JSON.stringify(data));
    },

    toggleNavPanel(panel) {
      this.changeSourceOptions({
        options: {
          navigationPanel: {
            show: this.navigationPanel.show === panel ? null : panel
          }
        }
      });
    },

    changeSourceOptions(option) {
      this.$store.dispatch("wpt/changeSourceOptions", {
        source: this.source,
        option
      });
    }
  }
};
</script>

<style></style>



