<template>
  <div class="wpt-side-buttons">
    <wpt-button
      :class="navPanel === 'FIELDS' ? 'wpt-selected' : ''"
      :label="$t('Lbl_FieldsList')"
      :vertical="true"
      type="plain"
      @click="changeNavPanel('FIELDS')"
    />

    <wpt-button
      :class="navPanel === 'OPTIONS' ? 'wpt-selected' : ''"
      :label="$t('Lbl_Setting')"
      :vertical="true"
      type="plain"
      @click="changeNavPanel('OPTIONS')"
    />

    <div 
      style="padding-top: 30px;">
      <wpt-button
        v-tooltip="{content: item.label, container: popoverContainer, placement: 'right'}"
        v-for="item in pivotLayoutOptions"
        :key="item.value"
        :icon="item.iconName"
        :style="
          'background-color: ' +
            (pivotLayout === item.value ? highLightColor : 'initial')
        "
        type="plain"
        @click="() => setPivotLayout(item.value)"
      />

      <v-popover
        :container="popoverContainer"
        placement="right-end"
      >
        <wpt-button 
          icon="export" 
          type="plain" />
        <template slot="popover">
          <div
            v-if="pivotLayout === WptConstants.PIVOT_LAYOUT_GRID_ONLY"
            class="wpt-menu-item"
            @click="exportToExcel"
          >
            <icon name="export-excel" />
            <span style="margin-left:3px">{{ $t("Lbl_ExportToExcel") }}</span>
          </div>
          <div 
            class="wpt-menu-item" 
            @click="exportToPdf">
            <icon name="export-pdf" />
            <span style="margin-left:3px">{{ $t("Lbl_ExportToPdf") }}</span>
          </div>
          <div 
            class="wpt-menu-item" 
            @click="exportToHtml">
            <icon name="export-html" />
            <span style="margin-left:3px">{{ $t("Lbl_ExportToHtml") }}</span>
          </div>
          <div 
            class="wpt-menu-item" 
            @click="print">
            <icon name="print" />
            <span style="margin-left:3px">{{ $t("Lbl_Print") }}</span>
          </div>
        </template>
      </v-popover>
    </div>
  </div>
</template>

<script>
import {
  exportHtmlToExcel,
  exportHtmlToPdf,
  exportContentToHtmlFile,
  printContent
} from "~/services/ExportService";

export default {
  props: {
    navPanel: {
      type: String,
      default: null
    },
    changeNavPanel: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      pivotLayoutOptions: [
        {
          value: this.WptConstants.PIVOT_LAYOUT_GRID_ONLY,
          label: this.$t("Lbl_GridOnly"),
          iconName: "grid-only"
        },
        {
          value: this.WptConstants.PIVOT_LAYOUT_CHART_ONLY,
          label: this.$t("Lbl_ChartOnly"),
          iconName: "chart-only"
        }
      ]
    };
  },

  computed: {
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    source() {
      return this.$store.state.wpt.source;
    },
    sheet() {
      return this.$store.state.wpt.sheet;
    },
    options() {
      return this.$store.state.options;
    },
    toolbarStyle() {
      return "background-color: " + this.options.styles.toolBarColor;
    },
    highLightColor() {
      return this.$store.state.options.styles.highLightColor;
    },
    sheetOptions() {
      return this.options.wpt.sheet;
    },
    pivotLayout() {
      return this.sheetOptions.pivotLayout;
    },
    uiFlags() {
      return this.options.uiFlags;
    },
    exportId() {
      return this.sheetOptions.pivotLayout ===
        this.WptConstants.PIVOT_LAYOUT_GRID_ONLY
        ? "pivot-grid-" + this.sheet.id
        : this.sheetOptions.pivotLayout ===
          this.WptConstants.PIVOT_LAYOUT_CHART_ONLY
        ? "pivot-chart-" + this.sheet.id
        : "";
    }
  },

  methods: {
    setPivotLayout(value) {
      this.$store.commit("options/changeSheetOptions", { pivotLayout: value });
    },

    exportToExcel() {
      exportHtmlToExcel(this.exportId, this.exportId);
    },

    exportToPdf() {
      exportHtmlToPdf(this.exportId, this.exportId);
    },

    exportToHtml() {
      const element = document.getElementById(this.exportId);
      const html = `<!DOCTYPE html>
          <html>
          <head>
          <meta charset='utf-8'>
          <style type='text/css'>
            .wpt-table-views table,
            .wpt-table-views table td,
            .wpt-table-views table th {
              border: 0;
              margin: 0;
              padding: 0;
              border-collapse: collapse;
            }

            .wpt-table-views,
            .wpt-table-views *,
            .wpt-table-views *:before,
            .wpt-table-views *:after {
              -webkit-box-sizing: border-box;
              -moz-box-sizing: border-box;
              box-sizing: border-box;
            }

            .wpt-table-views {
              position: relative;
              overflow: hidden;
              border: 1px solid #888;
              background-color: #fff;
              padding: 0;
            }

            .wpt-table-header-cell,
            .wpt-table-body-cell {
              padding: 0 3px;
              margin: 0;
              vertical-align: middle;
              white-space: nowrap;
              word-wrap: normal;
              overflow: hidden;
              text-overflow: ellipsis;
              border-width: 0;
              border-right-width: 1px;
              border-bottom-width: 1px;
              border-style: solid;
              border-color: #888;
            }

            .wpt-table-header-cell:before {
              content: "";
              display: inline-block;
              height: 100%;
              vertical-align: middle;
            }

          </style>
          </head>
          <body>
           ${element.outerHTML}
          </body>
          </html>`;

      exportContentToHtmlFile(html, this.exportId);
    },

    print() {
      const element = document.getElementById(this.exportId);
      printContent(element.outerHTML);
    }
  }
};
</script>

<style></style>



