<template>
  <div>
    <div
      v-for="field in controlFields"
      :key="field.id"
      draggable="true"
      @dragstart="evt => dragstart(evt, field)"
      @dragend="dragend"
    >
      <ControlItem
        :sheet="sheet"
        :source="source"
        :field="field"
        :control-type="controlType"
      />

      <div 
        v-if="field.children" 
        class="wpt-indent">
        <control-list
          :sheet="sheet"
          :source="source"
          :control-fields="field.children"
          :control-type="controlType"
        />
      </div>
    </div>
  </div>
</template>

<script>
import ControlItem from "./ControlItem.vue";

export default {
  name: "ControlList",

  components: {
    ControlItem
  },

  props: {
    sheet: {
      type: Object,
      required: true
    },
    source: {
      type: Object,
      required: true
    },
    controlFields: {
      type: Array,
      required: true
    },
    controlType: {
      type: String,
      required: true
    }
  },

  methods: {
    dragstart(evt, field) {
      //https://stackoverflow.com/questions/19055264/why-doesnt-html5-drag-and-drop-work-in-firefox
      evt.dataTransfer.setData("text/html", "anything");
      this.$store.commit("system/setDragData", {
        type: this.controlType,
        item: field
      });
    },
    dragend() {
      this.$store.commit("system/setDragData", {});
    }
  }
};
</script>

<style></style>



