<template>
  <div>
    <div class="wpt-section">
      <span style="padding:5px;">{{ $t("Lbl_Delimiter") }}</span>
      <wpt-select
        v-model="delimiter"
        :options="delimiterOptions"
        type="plain"
      />
    </div>

    <div class="wpt-section">
      <wpt-file-input
        :label="$t('Lbl_SelectLocalFile')"
        :on-file-change="onFileChange"
      />
      <file-picker
        :accept="['text/*']"
        :on-success="readCsvFileUrl"
        :on-error="errorHandler"
      />
    </div>

    <div class="wpt-section">
      <span>{{ $t("Lbl_EnterFileUrl") }}</span>
      <input
        v-model="fileUrl"
        :placeholder="$t('Msg_InputFileUrl')"
        type="text"
      >
      <wpt-button
        :disabled="!fileUrl"
        :label="$t('Btn_Load')"
        @click="readCsvFileUrl(fileUrl)"
      />
    </div>

    <div class="wpt-section">
      <div>{{ $t("Lbl_Samples") }}</div>

      <div 
        v-for="item in fileLinks" 
        :key="item.url">
        <wpt-button
          :label="item.label"
          type="plain"
          @click="readCsvFileUrl(item.url)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { readLocalFile, readFileUrl } from "~/services/FileService";
import FilePicker from "../FilePicker.vue";

export default {
  components: {
    FilePicker
  },
  data() {
    return {
      fileUrl: "",
      delimiterOptions: [
        { value: ",", label: "Comma(,)" },
        { value: "\u0009", label: "Tab" },
        { value: ";", label: "Semi-colon(;)" },
        { value: "^", label: "Caret(^)" },
        { value: "|", label: "Pipe(|)" }
      ],
      delimiter: ","
    };
  },

  computed: {
    options() {
      return this.$store.state.options;
    },

    fileLinks() {
      return this.options.fileLinks.filter(
        item => item.type === this.WptConstants.FORMAT_CSV
      );
    }
  },

  methods: {
    onFileChange(e) {
      const files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      readLocalFile(files[0], 'csv', this.$store, this.$t.bind(this), this.delimiter);
    },
    errorHandler(error) {
      this.$store.dispatch("system/errorMessage", error);
    },
    readCsvFileUrl(fileUrl) {
      readFileUrl(fileUrl, 'csv', this.$store, this.$t.bind(this), this.delimiter);
    }
  }
};
</script>

<style></style>



