<template>
  <div :style="'width: ' + width + 'px;height: ' + height + 'px;'">
    <wpt-pagination
      :total="source.data.length"
      :page-size="pageSize"
      @page-change="pageChange"
      @page-size-change="pageSizeChange"
    />
    <wpt-table
      :height="height - 30"
      :width="width"
      :columns="columns"
      :title-rows="titleRows"
      :table-data="tableData"
      :paging-index="(pageIndex - 1) * pageSize"
      column-width-drag
      even-bg-color="#f2f2f2"
    />
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },

  data() {
    return {
      pageIndex: 1,
      pageSize: 20,
      tableData: [],
      columns: [
        {
          field: "custome",
          width: 50,
          titleAlign: "center",
          columnAlign: "center",
          formatter: function(rowData, index, pagingIndex) {
            return index + pagingIndex + 1;
          },
          isFrozen: true
        },
        ...this.$store.state.wpt.source.fields.map(field => ({
          field: field.name,
          title: field.name,
          width: 100,
          columnAlign: "center",
          titleAlign: "center",
          isFrozen: false
        })),
        ...this.$store.state.wpt.source.fields.map(field => ({
          field: field.name + 1,
          title: field.name,
          width: 100,
          columnAlign: "center",
          titleAlign: "center",
          isFrozen: false
        }))
      ],
      titleRows: [
        [
          {
            fields: ["custome"],
            title: "",
            titleAlign: "center",
            rowspan: 3
          },
          {
            fields: [
              ...this.$store.state.wpt.source.fields.map(field => field.name),
              ...this.$store.state.wpt.source.fields.map(
                field => field.name + 1
              )
            ],
            title: "ALL",
            titleAlign: "center",
            colspan: 2 * this.$store.state.wpt.source.fields.length
          }
        ],
        [
          {
            fields: this.$store.state.wpt.source.fields.map(
              field => field.name
            ),
            title: "",
            titleAlign: "center",
            colspan: this.$store.state.wpt.source.fields.length
          },
          {
            fields: this.$store.state.wpt.source.fields.map(
              field => field.name + 1
            ),
            title: "1",
            titleAlign: "center",
            colspan: this.$store.state.wpt.source.fields.length
          }
        ],
        [
          ...this.$store.state.wpt.source.fields.map(field => ({
            fields: [field.name],
            title: field.name
          })),
          ...this.$store.state.wpt.source.fields.map(field => ({
            fields: [field.name + 1],
            title: field.name + "1"
          }))
        ]
      ]
    };
  },

  computed: {
    source() {
      return this.$store.state.wpt.source;
    }
  },

  created() {
    this.getTableData();
  },
  methods: {
    getTableData() {
      this.tableData = this.source.data
        .slice(
          (this.pageIndex - 1) * this.pageSize,
          this.pageIndex * this.pageSize
        )
        .map(row => {
          const newRow = {};
          this.source.fields.forEach(field => {
            newRow[field.name] = row[field.cellIndex];
          });
          return newRow;
        });
    },
    pageChange(pageIndex) {
      this.pageIndex = pageIndex;
      this.getTableData();
    },
    pageSizeChange(pageSize) {
      this.pageIndex = 1;
      this.pageSize = pageSize;
      this.getTableData();
    }
  }
};
</script>

<style></style>



