<template>
  <div :style="'width: ' + width + 'px;height: ' + height + 'px;'" />
</template>

<script>
export default {
  components: {},
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },
  data() {
    return {};
  },

  computed: {},
  methods: {}
};
</script>

<style></style>



