<template>
  <div>
    <div style="padding:5px 10px;">
      <div class="wpt-section">
        <div>{{ $t("Lbl_XmlaServerUrl") }}</div>
        <div>
          <input
            v-model="xmlaUrl"
            :placeholder="$t('Msg_InputXmlaServerUrl')"
          >
        </div>
      </div>
      <div class="wpt-section">
        <wpt-button
          :disabled="!xmlaUrl"
          :label="$t('Btn_GetCubes')"
          @click="getCubes"
        />
      </div>

      <div style="padding:5px;">
        <div
          v-if="
            dialogData &&
              dialogData.content &&
              dialogData.content.dataSources &&
              dialogData.content.dataSources.length > 0
          "
          class="wpt-section"
        >
          <div>{{ $t("Lbl_DataSource") }}</div>
          <wpt-select
            :placeholder="$t('Msg_SelectDataSource')"
            :value="dialogData.content.dataSourceInfo"
            :options="
              dialogData.content.dataSources.map(item => ({
                value:
                  item.DataSourceInfo ||
                  'Provider=' +
                  item.ProviderName +
                  ';DataSource=' +
                  item.DataSourceName +
                  ';',
                label: item.DataSourceDescription || item.DataSourceName
              }))
            "
            style="width: 300px;"
            @change="changeOlapDataSourceInfo"
          />
        </div>

        <div
          v-if="
            dialogData &&
              dialogData.content &&
              dialogData.content.catalogs &&
              dialogData.content.catalogs.length > 0
          "
          class="wpt-section"
        >
          <div>{{ $t("Lbl_Catalog") }}</div>
          <wpt-select
            :placeholder="$t('Msg_SelectCatalog')"
            :value="dialogData.content.catalog"
            :options="
              dialogData.content.catalogs.map(item => ({
                value: item.CATALOG_NAME,
                label: item.CATALOG_NAME
              }))
            "
            style="width: 300px;"
            @change="changeOlapCatalog"
          />
        </div>

        <div
          v-if="
            dialogData &&
              dialogData.content &&
              dialogData.content.cubes &&
              dialogData.content.cubes.length > 0
          "
          class="wpt-section"
        >
          <div>{{ $t("Lbl_CubeName") }}</div>
          <wpt-select
            :placeholder="$t('Msg_SelectCubeName')"
            :value="dialogData.content.cubeName"
            :options="
              dialogData.content.cubes.map(item => ({
                value: item.CUBE_NAME,
                label: item.CUBE_CAPTION
              }))
            "
            style="width: 300px;"
            @change="changeOlapCube"
          />
        </div>
      </div>

      <div class="wpt-section">
        <div>{{ $t("Lbl_Samples") }}</div>

        <div
          v-for="item in fileLinks"
          :key="item.url"
        >
          <wpt-button
            :label="item.label"
            type="plain"
            @click="readXmlaFileUrl(item.url)"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  discoverDatasource,
  discoverCatalog,
  discoverCube,
  getCubeFields
} from "~/services/OlapService";

export default {
  props: {
    createSource: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      xmlaUrl: ""
    };
  },

  computed: {
    options() {
      return this.$store.state.options;
    },
    dialog() {
      return this.$store.state.dialog;
    },
    dialogData() {
      return this.$store.state.dialog.data;
    },
    fileLinks() {
      return this.$store.state.options.fileLinks.filter(
        item => item.type === this.WptConstants.FORMAT_OLAP
      );
    }
  },
  methods: {
    getCubes() {
      this.readXmlaFileUrl(this.xmlaUrl);
    },

    readXmlaFileUrl(xmlaUrl) {
      const data = {
        dataSources: [],
        dataSource: "",
        catalogs: [],
        catalog: "",
        cubes: [],
        cubeName: ""
      };

      const options = {
        xmlaUrl,
        serverOptions: this.$store.state.options.server
      };

      this.$store.dispatch(
        "system/loadingMessage",
        this.$t("Msg_DiscoveringDataSources")
      );
      discoverDatasource(options, (error, result) => {
        if (error) {
          this.$store.dispatch("system/errorMessage", error);
          return;
        }
        data.dataSources = result;
        this.$store.commit("dialog/setData", {
          type: this.WptConstants.FORMAT_OLAP,
          selectedXmlaUrl: xmlaUrl,
          content: data,
          newSource: null
        });
        this.$store.dispatch("system/clearMessage");
      });
    },
    changeOlapDataSourceInfo(value) {
      const options = {
        xmlaUrl: this.dialogData.selectedXmlaUrl,
        serverOptions: this.options.server,
        dataSourceInfo: value
      };

      this.$store.dispatch(
        "system/loadingMessage",
        this.$t("Msg_DiscoveringCatalogs")
      );
      discoverCatalog(options, (error, result) => {
        if (error) {
          this.$store.dispatch("system/errorMessage", error);
          return;
        }
        this.$store.commit("dialog/updateData", {
          content: {
            dataSourceInfo: value,
            catalogs: result
          }
        });
        this.$store.dispatch("system/clearMessage");
      });
    },

    changeOlapCatalog(value) {
      const options = {
        xmlaUrl: this.dialogData.selectedXmlaUrl,
        serverOptions: this.options.server,
        dataSourceInfo: this.dialogData.content.dataSourceInfo,
        catalog: value
      };

      this.$store.dispatch(
        "system/loadingMessage",
        this.$t("Msg_DiscoveringCubes")
      );
      discoverCube(options, (error, result) => {
        if (error) {
          this.$store.dispatch("system/errorMessage", error);
          return;
        }
        this.$store.commit("dialog/updateData", {
          content: {
            catalog: value,
            cubes: result
          }
        });
        this.$store.dispatch("system/clearMessage");
      });
    },

    changeOlapCube(value) {
      const options = {
        xmlaUrl: this.dialogData.selectedXmlaUrl,
        serverOptions: this.options.server,
        dataSourceInfo: this.dialogData.content.dataSourceInfo,
        catalog: this.dialogData.content.catalog,
        cubeName: value
      };

      this.$store.dispatch(
        "system/loadingMessage",
        this.$t("Msg_LoadingCubeData")
      );
      getCubeFields(options, (error, result) => {
        if (error) {
          this.$store.dispatch("system/errorMessage", error);
          return;
        }
        this.$store.commit("dialog/updateData", {
          content: {
            cubeName: value
          },
          newSource: {
            name: this.$t("Lbl_Source") + 1,
            mode: this.WptConstants.MODE_OLAP,
            url: this.dialogData.selectedXmlaUrl,
            data: {
              xmlaUrl: this.dialogData.selectedXmlaUrl,
              dataSourceInfo: this.dialogData.content.dataSourceInfo,
              catalog: this.dialogData.content.catalog,
              cubeName: value
            },
            fields: result
          }
        });
        this.$store.dispatch("system/clearMessage");
        this.createSource();
      });
    }
  }
};
</script>

<style></style>



