<template>
  <div>
    <textarea
      v-model="mdxStatement"
      style="width:100%;"
      rows="8"
      cols="50"
    />

    <div>
      <wpt-button
        :label="$t('Btn_Run')"
        @click="handleConfirm" />
    </div>

  </div>
</template>

<script>
import { generateMdxString } from "~/services/SheetService";

export default {
  data() {
    const { sheet, source } = this.$store.state.dialog.payload;

    let mdxStatement = "";
    if (source.mode === this.WptConstants.MODE_OLAP) {
      mdxStatement = generateMdxString(sheet.pivot, source);
    }

    return {
      mdxStatement
    };
  },

  computed: {
    payload() {
      return this.$store.state.dialog.payload;
    }
  },

  methods: {
    close() {
      this.$store.commit("dialog/close");
    },

    handleConfirm() {
      this.$store.dispatch("wpt/generatePivotDataFromMdx", {
        sheet: this.payload.sheet,
        source: this.payload.source,
        mdx: this.mdxStatement
      });
      this.close();
    }
  }
};
</script>

<style></style>



