<template>
  <div :style="containerStyle">
    <template v-if="!noPivotDataYet">
      <pivot-grid
        v-if="sheetOptions.pivotLayout === WptConstants.PIVOT_LAYOUT_GRID_ONLY"
        :sheet-id="sheet.id"
        :layout-data="sheet.sheetData.layoutData"
        :grid-options="sheetOptions.grid"
        :pivot="sheet.pivot"
        :source-mode="source.mode"
        :source-fields="source.fields"
        :width="width"
        :height="height"
        :popover-container="popoverContainer"
      />

      <pivot-chart
        v-else-if="
          sheetOptions.pivotLayout === WptConstants.PIVOT_LAYOUT_CHART_ONLY
        "
        :sheet-id="sheet.id"
        :layout-data="sheet.sheetData.layoutData"
        :chart-options="sheetOptions.chart"
        :pivot="sheet.pivot"
        :source-mode="source.mode"
        :source-fields="source.fields"
      />
    </template>
  </div>
</template>

<script>
import PivotGrid from "./sheet-main/PivotGrid";
import PivotChart from "./sheet-main/PivotChart";

export default {
  components: {
    PivotGrid,
    PivotChart
  },
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },

  computed: {
    source() {
      return this.$store.state.wpt.source;
    },
    sheet() {
      return this.$store.state.wpt.sheet;
    },
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    noPivotDataYet() {
      const sheetData = this.sheet.sheetData;
      if (!sheetData || !sheetData.pivotData) {
        if (!sheetData || (!sheetData.loading && !sheetData.error)) {
          this.$store.dispatch("wpt/generatePivotData", {
            pivot: this.sheet.pivot
          });
        }
        return true;
      } else if (!sheetData.pivotData.valueObj) {
        return true;
      }
      return false;
    },

    sheetOptions() {
      return this.$store.state.options.wpt.sheet;
    },
    containerStyle() {
      return {
        width: this.width + "px",
        height: this.height + "px",
        overflow:
          this.sheetOptions.pivotLayout ===
            this.WptConstants.PIVOT_LAYOUT_GRID_ONLY &&
          !this.sheetOptions.grid.noFixedColumns
            ? "hidden"
            : "auto"
      };
    }
  }
};
</script>

<style></style>



