import Worker from "simple-web-worker";
import { mergeRecursive } from "~/services/CommonUtils";
import { getAttributeMembers, getCubeFields } from "~/services/OlapService";
import i18n from "~/i18n";
import { generateMemoryLookups } from "~/services/MemoryService";

const updateSourceData = (store, sourceData) => {
  store.commit("updateSourceData", { sourceData });
};

export default {
  getMemoryLookups(store, {}) {
    const source = store.state.source;
    store.dispatch("system/loadingMessage", "GenerateMemoryLookup", {root: true});
    try {
      Worker
        .run(generateMemoryLookups, [source])
        .then(fields => {
          store.dispatch("system/clearMessage", null, {root: true});
          updateSourceData(store, { fields });
        })
        .catch(error => {
          store.dispatch("system/errorMessage", error, {root: true});
        });
    } catch (error) {
      store.dispatch("system/errorMessage", error, {root: true});
    }
  },

  getOlapCubeFields(store) {
    const source = store.state.source;
    store.dispatch("system/loadingMessage", "getOlapCubeFields", {root: true});

    try {
      const serverOptions = store.rootState.options.server;

      const options = {
        serverOptions,
        xmlaUrl: source.data.xmlaUrl,
        dataSourceInfo: source.data.dataSourceInfo,
        catalog: source.data.catalog,
        cubeName: source.data.cubeName
      };

      getCubeFields(options, (error, result) => {
        if (error) {
          store.dispatch("system/errorMessage", error, {root: true});
          return;
        }
        store.dispatch("system/clearMessage", null, {root: true});
        updateSourceData(store, {
          fields: result
        });
      });
    } catch (error) {
      store.dispatch("system/errorMessage", error, {root: true});
    }
  },

  getOlapAttributeMembers(store, { field }) {
    const source = store.state.source;
    const fieldId = field.id;
    const sourceField = source.fields[fieldId];

    if (!sourceField.lookup && !sourceField.loading) {
      store.commit("updateSourceFieldData", {
        fieldId,
        sourceFieldData: { loading: true }
      });

      store.dispatch("system/loadingMessage", i18n.t("Msg_RetrievingMembers"), {root: true});

      const serverOptions = store.rootState.options.server;
      const sourceField = source.fields[fieldId];

      const { xmlaUrl, dataSourceInfo, catalog, cubeName } = source.data;

      try {
        getAttributeMembers(
          {
            xmlaUrl,
            serverOptions,
            dataSourceInfo,
            catalog,
            cubeName,
            dimensionName: sourceField.xmlaData.DIMENSION_UNIQUE_NAME,
            hierarchyName: sourceField.xmlaData.HIERARCHY_UNIQUE_NAME,
            levelName: sourceField.xmlaData.LEVEL_UNIQUE_NAME
          },
          function(error, response) {
            if (error) {
              store.dispatch("system/errorMessage", error, {root: true});
              store.commit("updateSourceFieldData", {
                fieldId,
                sourceFieldData: { loading: false, error }
              });
            } else {
              store.dispatch("system/clearMessage", null, {root: true});
              store.commit("updateSourceFieldData", {
                fieldId,
                sourceFieldData: { lookup: response, loading: false }
              });
            }
          }
        );
      } catch (error) {
        store.dispatch("system/errorMessage", error, {root: true});
        store.commit("updateSourceFieldData", {
          fieldId,
          sourceFieldData: { loading: false, error }
        });
      }
    }
  }
};



