<template>
  <div v-if="!noLookupsYet">
    <div v-if="source.mode === WptConstants.MODE_MEMORY">
      <div class="wpt-section wpt-flex-row">
        <div style="width: 100px;">
          {{ $t("Lbl_OriginalName") }}
        </div>
        <div style="flex: 1">
          {{ field.name }}
        </div>
      </div>
      <div class="wpt-section wpt-flex-row">
        <div style="width: 100px;">
          {{ $t("Lbl_CustomName") }}
        </div>
        <div style="flex: 1">
          <el-input v-model="customName" />
        </div>
      </div>
    </div>

    <div class="wpt-flex-row">
      <el-input
        :placeholder="$t('Btn_Search')"
        v-model="lookupsSearch"
        style="width:100px;"
        size="mini"
      />
    </div>

    <div 
      class="wpt-list-container" 
      style="height:300px; padding: 5px;">
      <div 
        v-for="item in data" 
        :key="item.id">
        {{
          item.label +
            (source.mode === WptConstants.MODE_OLAP ? " [" + item.id + "]" : "")
        }}
      </div>
    </div>
    <el-pagination
      :current-page.sync="currentPage"
      :page-size="pageSize"
      :total="lookup.length"
      small
      layout="prev, pager, next, total"
    />
  </div>
</template>

<script>
export default {
  data() {
    const { field } = this.$store.state.dialog.payload;

    return {
      currentPage: 1,
      pageSize: 100,
      lookupsSearch: "",
      customName: field.name
    };
  },

  computed: {
    highLightColor() {
      return this.$store.state.options.styles.highLightColor;
    },

    payload() {
      return this.$store.state.dialog.payload;
    },

    field() {
      return this.payload.field;
    },

    source() {
      return this.payload.source;
    },

    noLookupsYet() {
      switch (this.source.mode) {
        case this.WptConstants.MODE_MEMORY:
          if (this.field.type !== this.WptConstants.FIELD_CALCULATED) {
            if (!this.field.lookup) {
              this.$store.dispatch("wpt/getMemoryLookups", {
                source: this.source
              });
              return true;
            }
          }
          return false;

        case this.WptConstants.MODE_OLAP:
          if (this.field.type === this.WptConstants.FIELD_LEVEL) {
            const sourceField = this.source.fields[this.field.id];
            if (!sourceField || !sourceField.lookup) {
              if (!sourceField.loading && !sourceField.error) {
                this.$store.dispatch("wpt/getOlapAttributeMembers", {
                  source: this.source,
                  field: this.field
                });
                return true;
              }
            }
          }
          return false;
        default:
      }
      return false;
    },

    lookup() {
      let lookup = [];
      switch (this.source.mode) {
        case this.WptConstants.MODE_MEMORY:
          for (let key in this.field.lookup) {
            lookup.push({ id: key, label: key });
          }
          break;

        case this.WptConstants.MODE_OLAP:
          if (this.field.type === this.WptConstants.FIELD_LEVEL) {
            const fieldLookup = this.source.fields[this.field.id].lookup;
            for (let key in fieldLookup) {
              lookup.push({ id: key, label: fieldLookup[key] });
            }
          }
          break;

        default:
      }

      if (this.lookupsSearch) {
        lookup = lookup.filter(item =>
          item.label.match(new RegExp(this.lookupsSearch.trim(), "i"))
        );
      }
      return lookup;
    },

    data() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.lookup.slice(start, end);
    }
  },

  methods: {
    close() {
      this.$store.commit("dialog/close");
    },

    handleConfirm() {
      let newField = { id: this.field.id };

      if (this.customName !== this.field.name) {
        newField.name = this.customName;
      }

      // this.$store.dispatch('wpt/changeFieldSetting', {
      //   newField,
      //   source: this.source,
      // });

      this.close();
    }
  }
};
</script>

<style></style>



