<template>
  <div
    :style="'width: ' + width + 'px; height: ' + height + 'px;overflow: auto; padding:5px;'"
  >
    <pivot-sheet-options />
    <br >
    <pivot-grid-options
      v-if="pivotLayout === WptConstants.PIVOT_LAYOUT_GRID_ONLY"
    />
    <pivot-chart-options
      v-if="pivotLayout === WptConstants.PIVOT_LAYOUT_CHART_ONLY"
    />
  </div>
</template>

<script>
import PivotSheetOptions from "./sheet-options/PivotSheetOptions.vue";
import PivotGridOptions from "./sheet-options/PivotGridOptions.vue";
import PivotChartOptions from "./sheet-options/PivotChartOptions.vue";

export default {
  components: {
    PivotSheetOptions,
    PivotGridOptions,
    PivotChartOptions
  },

  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },

  computed: {
    sheet() {
      return this.$store.state.wpt.sheet;
    },
    pivotLayout() {
      return this.$store.state.options.wpt.sheet.pivotLayout;
    }
  }
};
</script>

<style></style>



