<template>
  <wpt-tabs>
    <wpt-tab
      v-if="uiFlags.saveToLocal"
      :name="$t('Lbl_SaveToLocal')"
    >
      <div style="padding: 5px;">
        <div class="wpt-section">
          <div>{{ $t("Lbl_FileName") }}</div>
          <div>
            <input
              v-model="fileName" 
              style="width:200px;" >
          </div>
        </div>

        <div class="wpt-section">
          <wpt-checkbox v-model="ignoreData">{{
            $t("Lbl_IgnoreData")
          }}</wpt-checkbox>
        </div>

        <div class="wpt-section">
          <wpt-button
            :label="$t('Btn_Save')"
            @click="saveWptToLocal"
          />
        </div>
      </div>
    </wpt-tab>

    <wpt-tab
      v-if="uiFlags.saveToServer"
      :name="$t('Lbl_SaveToServer')"
    />
  </wpt-tabs>
</template>

<script>
import { exportContentToTxtFile } from "~/services/ExportService";

export default {
  data() {
    return {
      fileName: "file",
      ignoreData: false
    };
  },

  computed: {
    uiFlags() {
      return this.$store.state.options.uiFlags;
    }
  },

  methods: {
    saveWptToLocal() {
      try {
        this.$store.dispatch("wpt/generateWptForSave", {
          ignoreData: this.ignoreData,
          successCB: wptString => {
            exportContentToTxtFile(wptString, this.fileName + ".wpt");
          }
        });
        this.$store.commit("dialog/close");
      } catch (error) {
        this.$store.dispatch("system/errorMessage", error);
      }
    }
  }
};
</script>

<style></style>



