<template>
  <div :style="'width: ' + width + 'px; height: ' + height + 'px;'">
    <div
      :style="'width: ' + mainWidth + 'px; height: ' + height + 'px;'"
      class="wpt-main"
    >
      <div v-if="modal.type">
        <template
          v-if="modal.type === WptConstants.MODAL_DRILLTHROUGH"
        >
          <div class="wpt-modal-header">
            <wpt-button
              :label="$t('Lbl_Sheet')"
              type="plain"
              @click="closeModal"
            />
            <div style="margin: 6px;"> > </div>
            <div style="margin: 6px;" >
              {{ $t("Lbl_DrillThrough") }}
            </div>
          </div>
          <drill-main
            v-if="!noDrillThroughDataYet"
            :width="mainWidth"
            :height="height - 30"
          />
        </template>

        <template
          v-else-if="modal.type === WptConstants.MODAL_REFINE_DATA"
        >
          <div class="wpt-modal-header">
            <wpt-button
              :label="$t('Lbl_Sheet')"
              type="plain"
              @click="refineOpenSheetView"
            />
            <div style="margin: 6px;"> > </div>
            <wpt-button
              :label="$t('Lbl_Source')"
              type="plain"
              @click="refineOpenSourceView"
            />
            <div style="margin: 6px;"> > </div>
            <div style="margin: 6px;"> {{ $t("Lbl_RefineData") }}</div>
          </div>
          <refine-main
            :width="mainWidth"
            :height="height - 30"
          />
        </template>

        <template v-else />
      </div>

      <template
        v-else-if="!modal.type && view === WptConstants.VIEW_SHEET"
      >
        <div v-if="!source || !sheet || noFieldsYet" />
        <sheet-main
          v-else
          :width="mainWidth"
          :height="height"
        />
      </template>

      <template
        v-else-if="!modal.type && view === WptConstants.VIEW_SOURCE"
      >
        <div v-if="!source" />
        <div v-else>
          <div class="wpt-modal-header">
            <wpt-button
              :label="$t('Lbl_Sheet')"
              type="plain"
              @click="openSheetView"
            />
            <div style="margin: 6px;">></div>
            <div style="margin: 6px;">{{ $t("Lbl_Source") }}</div>
            <div style="flex:1" />
            <wpt-button
              v-tooltip="{content: $t('Lbl_RefineData'), container: popoverContainer}"
              v-if="source.mode === WptConstants.MODE_MEMORY && uiFlags.sourceRefine"
              icon="refine"
              type="plain"
              @click="refineData"
            />
          </div>
          <source-main
            :width="mainWidth"
            :height="height - 30"
          />
        </div>
      </template>
    </div>

    <div
      v-if="drawer.show"
      :style="'width: ' + (drawer.width + 30) + 'px; height: ' + height + 'px;'"
      class="wpt-drawer">

      <div v-if="modal.type">
        <template
          v-if="modal.type === WptConstants.MODAL_DRILLTHROUGH"
        >
          <drill-drawer
            v-if="!noDrillThroughDataYet"
            :width="drawer.width + 30"
            :height="height"
          />
        </template>
        <template
          v-else-if="modal.type === WptConstants.MODAL_REFINE_DATA"
        >
          <refine-drawer
            :width="drawer.width + 30"
            :height="height"
          />
        </template>
      </div>

      <template
        v-else-if="!modal.type && view === WptConstants.VIEW_SHEET"
      >
        <div v-if="!source || !sheet || noFieldsYet" />
        <sheet-drawer
          v-else
          :width="drawer.width + 30"
          :height="height"
        />
      </template>

      <template
        v-else-if="!modal.type && view === WptConstants.VIEW_SOURCE"
      >
        <div v-if="!source" />
        <source-drawer
          v-else
          :width="drawer.width + 30"
          :height="height"
        />
      </template>

    </div>
  </div>
</template>

<script>
import SheetMain from "./sheet/SheetMain.vue";
import SheetDrawer from "./sheet/SheetDrawer.vue";
import SourceMain from "./source/SourceMain.vue";
import SourceDrawer from "./source/SourceDrawer.vue";
import DrillMain from "./drill-through/DrillMain.vue";
import DrillDrawer from "./drill-through/DrillDrawer.vue";
import RefineMain from "./refine-data/RefineMain.vue";
import RefineDrawer from "./refine-data/RefineDrawer.vue";

export default {
  components: {
    SheetMain,
    SheetDrawer,
    SourceMain,
    SourceDrawer,
    DrillMain,
    DrillDrawer,
    RefineMain,
    RefineDrawer
  },
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },
  computed: {
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    modal() {
      return this.$store.state.modal;
    },
    options() {
      return this.$store.state.options;
    },
    view() {
      return this.options.wpt.view;
    },
    uiFlags() {
      return this.options.uiFlags;
    },
    drawer() {
      return this.options.wpt.drawer;
    },
    mainWidth() {
      return this.drawer.show && !this.drawer.floating ? this.width - this.drawer.width - 30 : this.width;
    },
    source() {
      return this.$store.state.wpt.source;
    },
    sheet() {
      return this.$store.state.wpt.sheet;
    },
    noFieldsYet() {
      switch (this.source.mode) {
        case this.WptConstants.MODE_MEMORY:
          if (!this.source.fields[0].lookup) {
            this.$store.dispatch("wpt/getMemoryLookups", {});
            return true;
          }
          return false;

        case this.WptConstants.MODE_OLAP:
          if (!this.source.fields) {
            this.$store.dispatch("wpt/getOlapCubeFields", {});
            return true;
          }
          return false;

        default:
      }
      return false;
    },
    drillThrough() {
      return this.$store.state.modal.data;
    },

    noDrillThroughDataYet() {
      const drillThrough = this.drillThrough;
      if (!drillThrough || !drillThrough.data) {
        if (!drillThrough || (!drillThrough.loading && !drillThrough.error)) {
          this.$store.dispatch("modal/getDrillThroughData", {
            row: this.modal.payload.row,
            column: this.modal.payload.column,
            i18n: this.$t
          });
        }
        return true;
      }
      return false;
    }
  },

  methods: {
    openSheetView() {
      this.$store.commit("options/setView", this.WptConstants.VIEW_SHEET);
    },

    refineOpenSheetView() {
      this.$store.commit("modal/close");
      this.$store.commit("options/setView", this.WptConstants.VIEW_SHEET);
    },

    refineOpenSourceView() {
      this.$store.commit("modal/close");
    },

    refineData() {
      this.$store.commit("modal/open", {
        type: this.WptConstants.MODAL_REFINE_DATA,
        payload: {
          source: this.source
        }
      });
    },
    closeModal() {
      this.$store.commit("modal/close");
    }
  }
};
</script>

<style></style>



