<template>
  <svg 
    v-if="useSvg" 
    class="icon" 
    aria-hidden="true">
    <use :xlink:href="iconname" />
  </svg>
  <i 
    v-else 
    :class="iconname" 
    class="iconfont" />
</template>

<script>
export default {
  name: "Icon",
  props: {
    type: {
      type: String,
      default: "svg"
    },
    name: {
      type: String,
      default: "",
      required: true
    }
  },
  data() {
    return {
      msg: "Invalid icon"
    };
  },
  computed: {
    iconname() {
      let hash = this.type === "svg" ? "#" : "";
      return `${hash}icon-${this.name}`;
    },
    useSvg() {
      let type = this.type;
      return type === "svg";
    }
  }
};
</script>

<style>
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
</style>



