<template>
  <div style="padding: 5px;">
    <div class="wpt-section">
      <div style="width: 150px;">
        {{ $t("Lbl_Language") }}
      </div>
      <div style="flex: 1">
        <wpt-radio
          v-for="item in availableLocales"
          v-model="locale"
          :key="item.value"
          :value="item.value"
          :checked="locale === item.value"
          name="locale"
        >
          {{ $t(item.label) + item.attach }}
        </wpt-radio>
      </div>
    </div>

  </div>
</template>

<script>

export default {

  computed: {
    options() {
      return this.$store.state.options;
    },
    uiFlags() {
      return this.options.uiFlags;
    },
    locale: {
      get() {
        return this.options.locale;
      },
      set(value) {
        this.$store.dispatch("options/setLocale", value);
      }
    },
    availableLocales() {
      return this.options.availableLocales;
    },
    localeAbbr() {
      const locale = this.availableLocales.filter(
        item => item.value === this.locale
      )[0];
      return locale ? locale.abbr : this.locale.substring(0, 2);
    }
  }
};
</script>

<style></style>



