import Vue from "vue";

import vueCustomElement from "vue-custom-element";
import "document-register-element/build/document-register-element";
import UiWidget from "./ui-widgets";

import VueHighcharts from "vue-highcharts";
import Highcharts from "highcharts";
import HighchartsCustomEvents from "highcharts-custom-events";
import HighchartsGroupedCategories from "highcharts-grouped-categories";

import HighchartsMore from "highcharts/highcharts-more";
import Highcharts3D from "highcharts/highcharts-3d";
import HighchartsExporting from "highcharts/modules/exporting";

import "./i18n.js";

import WptConstants from "./WptConstants";
import WebPivotTable from "./components/WebPivotTable.vue";

import "./styles/wpt.css";
import "./styles/tooltip.scss";
import "./styles/table.css";


HighchartsCustomEvents(Highcharts);
HighchartsGroupedCategories(Highcharts);
HighchartsMore(Highcharts);
Highcharts3D(Highcharts);
HighchartsExporting(Highcharts);

Vue.use(vueCustomElement);
Vue.use(UiWidget);

Vue.use(VueHighcharts, { Highcharts });

const eventBus = new Vue();
Vue.prototype.$eventBus = eventBus;
Vue.prototype.WptConstants = WptConstants;

Vue.config.ignoredElements = ["web-pivot-table"];

window.wptExpiredWarning = function() {
  alert("WebPivotTable Component expired!");
};

window.wptDomainLockedWarning = function() {
  alert("WebPivotTable Component locked!");
};

Vue.customElement("web-pivot-table", WebPivotTable, {
  // 'constructorCallback' can be triggered multiple times when e.g. vue-router is used
  constructorCallback() {
    this.$eventBus = eventBus;
    this.WptConstants = WptConstants;
  },

  // element is mounted/inserted into document
  connectedCallback() {},

  // element is removed from document
  disconnectedCallback() {},

  // one of element's attributes (Vue instance props) is changed
  attributeChangedCallback(name, oldValue, value) {
    if (name === "options") {
      eventBus.$emit("setOptions", JSON.parse(value));
    }
  },

  // in case of using vue-custom-element with modals, we destroy  it after defined timeout
  destroyTimeout: 3000,

  // only needed when using lazy-loading - 'props' are not accessible on Custom Element registration so we have to provide them
  props: [],

  // you can set shadow root for element. Only works if native implementation is available.
  shadow: false,

  // you can set CSS that will be available in Shadow DOM.
  shadowCss: ""
});

export default Vue;



