import WptConstants from "~/WptConstants";
import {
  getControlType,
  getFieldIndexInControl
} from "~/services/SheetService";
import { generateId } from "~/services/CommonUtils";
import i18n from "~/i18n";

const updateValueFieldsInControl = (pivot, newPivot) => {
  let values = newPivot.values || pivot.values;
  let columns = newPivot.columns || pivot.columns;
  let valuesInAxis =
    "valuesInAxis" in newPivot ? newPivot.valuesInAxis : pivot.valuesInAxis;

  if (values.length <= 1) {
    newPivot.valuesInAxis = -1;
    newPivot.valuesIndex = -1;
  } else {
    if (valuesInAxis < 0) {
      newPivot.valuesInAxis = 0;
      newPivot.valuesIndex = columns.length;
    }
  }
};

const createNewValueField = (
  field,
  defaultToDistinctCount,
  defaultValueFormat
) => {
  return {
    id: generateId(),
    fieldId: field.id,
    name:
      field.name +
      "(" +
      (field.type === WptConstants.FIELD_NUMBER ||
      field.type === WptConstants.FIELD_CALCULATED
        ? i18n.t("Lbl_Stat_Sum")
        : defaultToDistinctCount
        ? i18n.t("Lbl_Stat_DistinctCount")
        : i18n.t("Lbl_Stat_Count")) +
      ")",
    stats:
      field.type === WptConstants.FIELD_NUMBER ||
      field.type === WptConstants.FIELD_CALCULATED
        ? WptConstants.VALUE_STATS_SUM
        : defaultToDistinctCount
        ? WptConstants.VALUE_STATS_DISTINCTCOUNT
        : WptConstants.VALUE_STATS_COUNT,
    format: defaultValueFormat
  };
};

const doSheetPivotChange = (store, sheet, source, newPivot) => {
  if (Object.keys(newPivot).length > 0) {
    store.commit("updateSheetPivot", { pivot: newPivot });
    store.commit("updateSheetCollapsed", { collapsed: {} });
    if (!store.rootState.options.wpt.sheet.deferLayoutUpdate) {
      store.dispatch("generatePivotData", {
        pivot: sheet.pivot
      });
    }
  }
};

export default {
  removeFieldFromControl(store, { sheet, pivot, source, field, from }) {
    const sourceMode = source.mode;
    const newPivot = {};
    if (field.type === WptConstants.FIELD_VALUES) {
      newPivot.values = [];
      updateValueFieldsInControl(pivot, newPivot);
    } else {
      switch (sourceMode) {
        case WptConstants.MODE_MEMORY:
          if (from !== "values") {
            newPivot[from] = pivot[from].filter(item => item !== field.id);
          } else {
            if (field.fieldId) {
              // if this is a value field, just remove it
              newPivot.values = pivot.values.filter(
                item => item.id !== field.id
              );
            } else {
              // this is a normal field, remove it from values means remove all fieldId === id
              newPivot.values = pivot.values.filter(
                item => item.fieldId !== field.id
              );
            }
            updateValueFieldsInControl(pivot, newPivot);
          }
          break;

        case WptConstants.MODE_OLAP:
          if (field.type === WptConstants.FIELD_MEASURE && from === "values") {
            newPivot.values = pivot.values.filter(item => item.id !== field.id);
            updateValueFieldsInControl(pivot, newPivot);
          } else if (
            field.type === WptConstants.FIELD_HIERARCHY &&
            from !== "values"
          ) {
            newPivot[from] = pivot[from].filter(item => item.id !== field.id);
          } else if (
            field.type === WptConstants.FIELD_LEVEL &&
            from !== "values"
          ) {
            let fields = [...pivot[from]];
            let hierarchies = fields.filter(item => item.id === field.parentId);

            if (hierarchies && hierarchies.length > 0) {
              let hierarchy = hierarchies[0];
              hierarchy.children = hierarchy.children.filter(
                item => item.id !== field.id
              );
              if (hierarchy.children.length === 0) {
                newPivot[from] = fields.filter(
                  item => item.id !== field.parentId
                );
              } else {
                newPivot[from] = fields;
              }
            }
          }
          break;

        default:
      }
    }
    doSheetPivotChange(store, sheet, source, newPivot);
  },

  moveFieldBetweenControls(
    store,
    { sheet, pivot, source, field, from, to }
  ) {
    if (field.type === WptConstants.FIELD_CALCULATED && to !== "values") {
      return;
    }

    const sourceMode = source.mode;
    const newPivot = {};
    if (field.type === WptConstants.FIELD_VALUES) {
      if (from === "columns" && to === "rows") {
        newPivot.valuesInAxis = 1;
        newPivot.valuesIndex = pivot.rows.length;
      } else if (from === "rows" && to === "columns") {
        newPivot.valuesInAxis = 0;
        newPivot.valuesIndex = pivot.columns.length;
      }
    } else {
      const controlType = getControlType(pivot, sourceMode, field);

      switch (sourceMode) {
        case WptConstants.MODE_MEMORY:
          if (to !== "values") {
            //move to rows/columns/filters
            if (from === "values") {
              // move from values
              let realField = pivot.fields[field.fieldId];
              if (
                !realField ||
                realField.type !== WptConstants.FIELD_CALCULATED
              ) {
                newPivot.values = pivot.values.filter(
                  item => item.id !== field.id
                );
                updateValueFieldsInControl(pivot, newPivot);
              }
              newPivot[to] = [...pivot[to], field.fieldId];
            } else {
              // move from other rows/columns/filters or fields
              if (!controlType) {
                // not in any rows/columns/filters yet
                newPivot[to] = [...pivot[to], field.id];
              } else {
                if (controlType !== to) {
                  newPivot[controlType] = pivot[controlType].filter(
                    item => item !== field.id
                  );
                  newPivot[to] = [...pivot[to], field.id];
                }
              }
            }
          } else {
            // move to values
            if (from !== "fields") {
              // move from rows/columns/filters
              newPivot[from] = pivot[from].filter(item => item !== field.id);
            }
            const defaultToDistinctCount =
              store.rootState.options.defaultToDistinctCount;
            const defaultValueFormat =
              store.rootState.options.defaultValueFormat;
            newPivot.values = [
              ...pivot.values,
              createNewValueField(
                field,
                defaultToDistinctCount,
                defaultValueFormat
              )
            ];
            updateValueFieldsInControl(pivot, newPivot);
          }
          break;

        case WptConstants.MODE_OLAP:
          if (field.type === WptConstants.FIELD_MEASURE && to === "values") {
            if (pivot.values.every(item => item.id !== field.id)) {
              newPivot.values = [...pivot.values, { id: field.id }];
              updateValueFieldsInControl(pivot, newPivot);
            }
          } else if (
            field.type === WptConstants.FIELD_HIERARCHY &&
            to !== "values"
          ) {
            if (controlType && controlType !== to) {
              newPivot[to] = [
                ...pivot[to],
                pivot[controlType].filter(item => item.id === field.id)[0]
              ];
              newPivot[controlType] = pivot[controlType].filter(
                item => item.id !== field.id
              );
            } else if (!controlType) {
              newPivot[to] = [
                ...pivot[to],
                {
                  id: field.id,
                  children: field.children.map(item => ({ id: item.id }))
                }
              ];
            }
          } else if (
            field.type === WptConstants.FIELD_LEVEL &&
            to !== "values"
          ) {
            const controlType = getControlType(pivot, sourceMode, field);

            if (controlType) {
              let control = [...pivot[controlType]];
              const hierarchy = control.filter(
                item => item.id === field.parentId
              )[0];
              if (hierarchy.children.every(item => item.id !== field.id)) {
                hierarchy.children.push({ id: field.id });
              }
              if (controlType === to) {
                newPivot[controlType] = control;
              } else {
                newPivot[to] = [...pivot[to], hierarchy];
                newPivot[controlType] = control.filter(
                  item => item.id !== field.parentId
                );
              }
            } else {
              newPivot.rows = [
                ...pivot.rows,
                {
                  id: field.parentId,
                  children: [{ id: field.id }]
                }
              ];
            }
          }

          break;

        default:
      }
    }
    doSheetPivotChange(store, sheet, source, newPivot);
  },

  moveFieldWithinSameControl(
    store,
    { sheet, pivot, source, field, controlType, direction }
  ) {
    const sourceMode = source.mode;
    const newPivot = {};

    let index = -1;

    if (controlType !== "values") {
      if (field.type === WptConstants.FIELD_VALUES) {
        switch (direction) {
          case "up":
            newPivot.valuesIndex = pivot.valuesIndex - 1;
            break;
          case "down":
            newPivot.valuesIndex = pivot.valuesIndex + 1;
            break;
          case "beginning":
            newPivot.valuesIndex = 0;
            break;
          case "end":
            newPivot.valuesIndex = pivot[controlType].length;
            break;
        }
      } else {
        index = getFieldIndexInControl(pivot, sourceMode, field, controlType);
        let controls = [...pivot[controlType]];
        controls.splice(index, 1);

        const valuesInControl =
          (controlType === "columns" && pivot.valuesInAxis === 0) ||
          (controlType === "rows" && pivot.valuesInAxis === 1);
        const valuesIndex = pivot.valuesIndex;

        const newField =
          sourceMode === WptConstants.MODE_MEMORY
            ? field.id
            : {
                id: field.id,
                children: field.children.map(item => ({ id: item.id }))
              };

        switch (direction) {
          case "up":
            if (valuesInControl && valuesIndex === index) {
              controls.splice(index, 0, newField);
              newPivot.valuesIndex = valuesIndex + 1;
            } else {
              controls.splice(index - 1, 0, newField);
            }
            break;
          case "down":
            if (valuesInControl && valuesIndex === index + 1) {
              controls.splice(index, 0, newField);
              newPivot.valuesIndex = valuesIndex - 1;
            } else {
              controls.splice(index + 1, 0, newField);
            }
            break;
          case "beginning":
            controls.splice(0, 0, field.id);
            if (valuesInControl && valuesIndex <= index) {
              newPivot.valuesIndex = valuesIndex + 1;
            }
            break;
          case "end":
            controls.splice(controls.length, 0, newField);
            if (valuesInControl && valuesIndex >= index) {
              newPivot.valuesIndex = valuesIndex - 1;
            }
            break;
        }
        newPivot[controlType] = controls;
      }
    } else {
      index = getFieldIndexInControl(pivot, sourceMode, field, controlType);

      const newField =
        sourceMode === WptConstants.MODE_MEMORY ? field : { id: field.id };

      let values = [...pivot.values];
      values.splice(index, 1);

      switch (direction) {
        case "up":
          values.splice(index - 1, 0, newField);
          break;
        case "down":
          values.splice(index + 1, 0, newField);
          break;
        case "beginning":
          values.splice(0, 0, newField);
          break;
        case "end":
          values.splice(values.length, 0, newField);
          break;
      }
      newPivot.values = values;
    }

    doSheetPivotChange(store, sheet, source, newPivot);
  },

  swapSheetAxis(store, { sheet, pivot, source }) {
    const newPivot = {};

    newPivot.rows = [...pivot.columns];
    newPivot.columns = [...pivot.rows];
    if (pivot.valuesInAxis >= 0) {
      newPivot.valuesInAxis = pivot.valuesInAxis === 0 ? 1 : 0;
    }

    doSheetPivotChange(store, sheet, source, newPivot);
  },

  clearSheetAllFilters(store, { sheet, pivot, source }) {
    const newPivot = {};

    let changed = false;

    let fields = Object.assign({}, pivot.fields);
    for (let id in fields) {
      const field = fields[id];
      if (field.filter) {
        delete field.filter;
        changed = true;
      }
    }
    if (changed) {
      newPivot.fields = fields;
      doSheetPivotChange(store, sheet, source, newPivot);
    }
  },

  clearSheetAllPivot(store, { sheet, pivot, source }) {
    const newPivot = {};

    let fields = Object.assign({}, pivot.fields);
    for (let id in fields) {
      const field = fields[id];
      if (field.type !== WptConstants.FIELD_CALCULATED) {
        delete fields[id];
      }
    }
    newPivot.fields = fields;

    newPivot.rows = [];
    newPivot.columns = [];
    newPivot.filters = [];
    newPivot.values = [];
    newPivot.valuesInAxis = -1;
    newPivot.valuesIndex = -1;

    newPivot.mdx = "";

    doSheetPivotChange(store, sheet, source, newPivot);
  },

  newCalculatedField(store, { sheet, source, name, formula }) {
    const newPivot = {};
    const id = generateId();

    const field = {
      id: id,
      name: name || id,
      formula: formula,
      type: WptConstants.FIELD_CALCULATED
    };

    newPivot.fields = Object.assign({}, sheet.pivot.fields, {
      [id]: field
    });

    const defaultToDistinctCount =
      store.rootState.options.defaultToDistinctCount;
    const defaultValueFormat = store.rootState.options.defaultValueFormat;
    newPivot.values = [
      ...sheet.pivot.values,
      createNewValueField(field, defaultToDistinctCount, defaultValueFormat)
    ];
    updateValueFieldsInControl(sheet.pivot, newPivot);
    doSheetPivotChange(store, sheet, source, newPivot);
  },

  changeCalculatedField(
    store,
    { sheet, source, field, name, formula }
  ) {
    const newPivot = {};

    newPivot.fields = Object.assign({}, sheet.pivot.fields);
    const cField = newPivot.fields[field.id];
    if (cField) {
      cField.name = name;
      cField.formula = formula;
    }

    doSheetPivotChange(store, sheet, source, newPivot);
  },

  removeCalculatedField(store, { sheet, source, field }) {
    const newPivot = {};

    if (sheet.pivot.values.some(item => item.fieldId === field.id)) {
      newPivot.values = sheet.pivot.values.filter(
        item => item.fieldId !== field.id
      );
      updateValueFieldsInControl(sheet.pivot, newPivot);
    }
    let fields = Object.assign({}, sheet.pivot.fields);
    delete fields[field.id];
    newPivot.fields = fields;

    doSheetPivotChange(store, sheet, source, newPivot);
  },

  changeValueFieldSetting(store, { sheet, newValueField, source }) {
    const newPivot = {};
    let values = [...sheet.pivot.values];
    let index = -1;
    for (let i = 0; i < values.length; i++) {
      if (values[i].id === newValueField.id) {
        index = i;
        break;
      }
    }
    if (index >= 0) {
      values[index] = newValueField;
    }
    newPivot.values = values;
    doSheetPivotChange(store, sheet, source, newPivot);
  },

  changeFieldSetting(store, { sheet, newField, source }) {
    const newPivot = {};
    let fields = Object.assign({}, sheet.pivot.fields);
    fields[newField.id] = newField;

    newPivot.fields = fields;
    doSheetPivotChange(store, sheet, source, newPivot);
  }
};



