import HighchartsThemes from "./HighchartsThemes";

import { mergeRecursive } from "./CommonUtils";
import { generateTooltipHtml } from "./SheetLayoutService";
import WptConstants from "~/WptConstants";

export const renderCharts = (
  h,
  i18n,
  sheetId,
  sourceMode,
  layoutData,
  chartOptions,
  pivot,
  sourceFields,
  switchTreeItemExpanded,
  drillThrough
) => {
  const charts = [];

  if (
    layoutData.rowHeaders.length === 0 ||
    layoutData.columnHeaders.length === 0
  ) {
    return [];
  }

  let pivotChartsData = [];
  let pivotChart;

  switch (sourceMode) {
    case WptConstants.MODE_MEMORY: {
      pivotChart = {
        categories: [],
        categoriesItems: [],
        series: [],
        seriesItems: []
      };

      const generateMemoryCategories = (rowHeaders, categoriesArray) => {
        const generateEmptyChildren = (index, array, levels) => {
          if (index < levels) {
            let newArrayItem = {
              name: " ",
              categories: []
            };
            generateEmptyChildren(index + 1, newArrayItem.categories, levels);
            array.push(newArrayItem);
          } else {
            array.push(" ");
          }
        };

        for (let row of rowHeaders) {
          if (row.children) {
            const newCategory = {
              name: row.label,
              rowItem: row,
              categories: []
            };
            generateMemoryCategories(row.children, newCategory.categories);
            categoriesArray.push(newCategory);
          } else {
            if (row.vSpan && row.vSpan > 1) {
              const newCategory = {
                name: row.label,
                rowItem: row,
                categories: []
              };
              generateEmptyChildren(0, newCategory.categories, row.vSpan - 2);
              categoriesArray.push(newCategory);
              pivotChart.categoriesItems.push(row);
            } else {
              categoriesArray.push(row.label);
              pivotChart.categoriesItems.push(row);
            }
          }
        }
      };

      const generateMemorySeries = (columnHeaders, labels) => {
        for (let column of columnHeaders) {
          labels.push(column.label);
          if (column.children) {
            generateMemorySeries(column.children, labels);
          } else {
            pivotChart.series.push({
              name: labels.join(" - "),
              data: pivotChart.categoriesItems.map(row =>
                row.data && row.data[column.id] ? row.data[column.id].value : 0
              )
            });
            pivotChart.seriesItems.push(column);
          }
          labels.pop();
        }
      };

      generateMemoryCategories(layoutData.rowHeaders, pivotChart.categories);

      generateMemorySeries(layoutData.columnHeaders, []);

      pivotChartsData.push(pivotChart);

      break;
    }

    case WptConstants.MODE_OLAP: {
      pivotChart = {
        categories: [],
        categoriesItems: [],
        series: [],
        seriesItems: []
      };

      const generateOlapCategories = (rowHeaders, categoriesArray) => {
        for (let row of rowHeaders) {
          if (row.children) {
            const newCategory = {
              name: row.label,
              rowItem: row,
              categories: []
            };
            generateOlapCategories(row.children, newCategory.categories);
            categoriesArray.push(newCategory);
          } else {
            categoriesArray.push({
              name: row.label,
              rowItem: row
            });
            pivotChart.categoriesItems.push(row);
          }
        }
      };

      generateOlapCategories(layoutData.rowHeaders, pivotChart.categories);

      const generateOlapSeries = (columnHeaders, labels) => {
        for (let column of columnHeaders) {
          labels.push(column.label);
          if (column.children) {
            generateOlapSeries(column.children, labels);
          } else {
            pivotChart.series.push({
              name: labels.join(" - "),
              data: pivotChart.categoriesItems.map(row =>
                row.data && row.data[column.id] ? row.data[column.id].value : 0
              )
            });
            pivotChart.seriesItems.push(column);
          }
          labels.pop();
        }
      };

      generateOlapSeries(layoutData.columnHeaders, []);

      pivotChartsData.push(pivotChart);

      break;
    }

    default:
  }

  let highOptions = chartOptions.high;

  let options = {};

  let theme = HighchartsThemes.filter(item => item.value === highOptions.theme);
  if (theme && theme.length > 0 && theme[0].theme) {
    options = mergeRecursive(options, theme[0].theme);
  }

  options = mergeRecursive(options, highOptions);

  options = mergeRecursive(options, {
    lang: {
      decimalPoint: ".", // wptOptions.decimalPoint,
      thousandsSep: ",", //wptOptions.thousandsSep,
      // months:[],
      // shortMonths:[],
      // weekdays: ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"],
      numericSymbols: ["K", "M", "B"],
      loading: i18n("Lbl_High_Loading"),
      contextButtonTitle: i18n("Lbl_High_ContextButtonTitle"),
      downloadJPEG: i18n("Lbl_High_DownloadJPEG"),
      downloadPDF: i18n("Lbl_High_DownloadPDF"),
      downloadPNG: i18n("Lbl_High_DownloadPNG"),
      downloadSVG: i18n("Lbl_High_DownloadSVG"),
      printChart: i18n("Lbl_High_PrintChart"),
      resetZoom: i18n("Lbl_High_ResetZoom"),
      resetZoomTitle: i18n("Lbl_High_ResetZoomTitle")
    },

    loading: {
      hideDuration: 1000,
      showDuration: 1000,
      labelStyle: {
        color: "white"
      },
      style: {
        backgroundColor: "gray"
      }
    },

    chart: {
      zoomType: "xy",
      width: chartOptions.width,
      height: chartOptions.height
    },
    title: {
      text: ""
    },
    xAxis: {
      title: {
        text: ""
      },
      labels: {}
    },
    yAxis: {
      title: {
        text: ""
      }
    },
    plotOptions: {
      series: {
        allowPointSelect: true,
        pointPadding: 0,
        groupPadding: 0.1,
        borderColor: "#303030",
        cursor: "pointer"
      }
    }
  });

  pivotChartsData.forEach(function(pChart) {
    if (highOptions.chart.type === "pie") {
      for (let i = 0, len = pChart.series.length; i < len; i++) {
        let pChartOptions = JSON.parse(JSON.stringify(options));

        pChartOptions.series = [pChart.series[i]];
        pChartOptions.tooltip = {
          formatter: (function(index, pChartItem) {
            return function() {
              return generateTooltipHtml(
                pChartItem.categoriesItems[this.point.x],
                pChartItem.seriesItems[index]
              );
            };
          })(i, pChart)
        };
        pChartOptions.subtitle = {
          text: pChart.series[i].name
        };
        pChartOptions.xAxis.categories = pChart.categories;

        pChartOptions.plotOptions.series.point = {
          events: {
            dblclick: (function(pChartItem, index) {
              return function(event) {
                drillThrough(
                  pChartItem.categoriesItems[this.index],
                  pChartItem.seriesItems[index],
                  event
                );
              };
            })(pChart, i)
          }
        };

        pChartOptions.plotOptions.pie = {
          allowPointSelect: true,
          cursor: "pointer",
          //showInLegend: true,
          dataLabels: {
            enabled: true,
            color: "#000000",
            connectorColor: "#000000",
            formatter: (function(pChartItem) {
              return function() {
                let rowLabel = "";

                if (sourceMode === WptConstants.MODE_MEMORY) {
                  rowLabel = pChartItem.categoriesItems[this.point.x].label;
                }

                return (
                  "<b>" +
                  rowLabel +
                  "</b>: " +
                  this.percentage.toFixed(2) +
                  " %"
                );
              };
            })(pChart)
          },
          depth: highOptions.chart.options3d.depth
        };

        charts.push(pChartOptions);
      }
    } else {
      let pChartOptions = JSON.parse(JSON.stringify(options));

      pChartOptions.series = pChart.series;
      pChartOptions.subtitle = {
        text: ""
      };
      pChartOptions.tooltip = {
        formatter: (function(pChartItem) {
          return function() {
            return generateTooltipHtml(
              pChartItem.categoriesItems[this.point.x],
              pChartItem.seriesItems[this.series.index]
            );
          };
        })(pChart)
      };
      pChartOptions.xAxis.categories = pChart.categories;

      pChartOptions.xAxis.labels.formatter = function(category) {
        if (
          this.isNew &&
          category &&
          category.rowItem &&
          (category.rowItem.collapsible || category.rowItem.expandable)
        ) {
          let item = category.rowItem;
          const id =
            sourceMode === WptConstants.MODE_MEMORY
              ? item.id
              : item.id + "-" + item.index;

          return (
            '<span style="cursor:pointer;color:blue;wpt_custom_id:' +
            id +
            ';">' +
            (item.collapsible ? "- " : "+ ") +
            this.value +
            "</span>"
          );
        } else {
          return this.value;
        }
      };

      pChartOptions.plotOptions.series.point = {
        events: {
          dblclick: (function(pChartItem) {
            return function(event) {
              drillThrough(
                pChartItem.categoriesItems[this.x],
                pChartItem.seriesItems[this.series.index],
                event
              );
            };
          })(pChart)
        }
      };

      charts.push(pChartOptions);
    }
  });

  return charts;
};



