<template>
  <div class="wpt-flex-row wpt-list-item">
    <span
      v-if="field.children"
      class="wpt-cursor-pointer"
      @click="switchFieldsItemExpanded"
    >
      <icon :name="field.expanded ? 'collapse' : 'expand'" />
    </span>
    <span
      v-else-if="treeLevel > 0 && field.type !== WptConstants.FIELD_MEASURE"
      class="wpt-indent"
    />

    <wpt-checkbox
      v-if="draggableFlag"
      v-model="fieldChecked"
      style="padding-left: 2px;"
      class="wpt-cursor-pointer"
    />

    <span 
      v-if="getTypeIconSrc" 
      style="padding: 0 2px;">
      <icon
        :name="getTypeIconSrc"
        :color="
          field.type === WptConstants.FIELD_MEASURES ? 'orangered' : 'black'
        "
      />
    </span>

    <div style="flex: 1;">
      <div 
        v-if="!draggableFlag" 
        :style="fieldStyle">
        {{
          field.type === WptConstants.FIELD_MEASURES
            ? $t("Lbl_Measures")
            : field.name
        }}
      </div>

      <v-popover
        v-else
        :container="popoverContainer"
        placement="right-end"
      >
        <span 
          :style="fieldStyle" 
          v-html="highlight(field.name)" />
        <span
          v-if="
            source.mode === WptConstants.MODE_MEMORY &&
              field.type !== WptConstants.FIELD_CALCULATED
          "
          class="wpt-badge"
        >
          {{ field.count }}
        </span>
        <template slot="popover">
          <div
            v-if="
              source.mode === WptConstants.MODE_MEMORY &&
                field.type === WptConstants.FIELD_CALCULATED
            "
            class="wpt-menu-item"
            @click="openSettingDialog"
          >
            <icon 
              name="edit" 
              color="black" />
            <span>{{ $t("Lbl_ChangeCalculatedField") + "..." }}</span>
          </div>

          <div
            v-if="
              source.mode === WptConstants.MODE_MEMORY &&
                field.type === WptConstants.FIELD_CALCULATED
            "
            class="wpt-menu-item"
            @click="removeCalculatedField"
          >
            <icon name="remove" />
            <span>{{ $t("Lbl_RemoveCalculatedField") }}</span>
          </div>

          <div
            v-if="moveToControlFlag && controlType !== 'filters'"
            class="wpt-menu-item"
            @click="addToFilters"
          >
            <icon name="filters" />
            <span>{{ $t("Lbl_AddToFilters") }}</span>
          </div>

          <div
            v-if="moveToControlFlag && controlType !== 'rows'"
            class="wpt-menu-item"
            @click="addToRows"
          >
            <icon name="rows" />
            <span>{{ $t("Lbl_AddToRows") }}</span>
          </div>

          <div
            v-if="moveToControlFlag && controlType !== 'columns'"
            class="wpt-menu-item"
            @click="addToColumns"
          >
            <icon name="columns" />
            <span>{{ $t("Lbl_AddToColumns") }}</span>
          </div>

          <div
            v-if="
              source.mode === WptConstants.MODE_MEMORY ||
                field.type === WptConstants.FIELD_MEASURE
            "
            class="wpt-menu-item"
            @click="addToValues"
          >
            <icon name="values" />
            <span>{{ $t("Lbl_AddToValues") }}</span>
          </div>
        </template>
      </v-popover>
    </div>

    <span v-if="fieldFiltered">
      <icon name="filter" />
    </span>

    <div
      v-if="
        source.mode === WptConstants.MODE_MEMORY &&
          field.type === WptConstants.FIELD_CALCULATED
      "
      class="wpt-cursor-pointer"
      @click.stop.prevent="removeCalculatedField"
    >
      <icon name="remove" />
    </div>

    <div
      v-if="
        source.mode === WptConstants.MODE_MEMORY ||
          field.type === WptConstants.FIELD_LEVEL
      "
      class="wpt-cursor-pointer"
      @click.stop.prevent="openSettingDialog"
    >
      <icon 
        :color="fieldChanged ? 'blue' : 'black'" 
        name="edit" />
    </div>
  </div>
</template>

<script>
import {
  getFieldCheckboxChecked,
  getControlType,
  isFieldFiltered
} from "~/services/SheetService";

import { getFieldTypeIconName } from "~/services/CommonUtils";

export default {
  name: "FieldsList",

  props: {
    sheet: {
      type: Object,
      required: true
    },
    source: {
      type: Object,
      required: true
    },
    field: {
      type: Object,
      required: true
    },
    treeLevel: {
      type: Number,
      required: true
    },
    draggableFlag: {
      type: Boolean,
      required: true
    }
  },

  computed: {
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    options() {
      return this.$store.state.options;
    },
    fieldsListFilter() {
      return this.options.wpt.sheet.fieldsListFilter;
    },

    pivot() {
      return this.sheet.pivot;
    },

    getTypeIconSrc() {
      return getFieldTypeIconName(this.field.type);
    },
    highLightColor() {
      return this.options.styles.highLightColor;
    },

    fieldChecked: {
      get() {
        return getFieldCheckboxChecked(
          this.pivot,
          this.source.mode,
          this.field
        );
      },
      set(value) {
        const sourceMode = this.source.mode;
        const field = this.field;
        const controlType = this.controlType;

        if (!value) {
          switch (sourceMode) {
            case this.WptConstants.MODE_MEMORY:
              if (controlType) {
                this.removeFieldFromControl(controlType);
              }
              if (this.pivot.values.some(item => item.fieldId === field.id)) {
                this.removeFieldFromControl("values");
              }
              break;
            case this.WptConstants.MODE_OLAP:
              this.removeFieldFromControl(controlType || "values");
              break;
            default:
          }
        } else {
          let to = "rows";
          switch (sourceMode) {
            case this.WptConstants.MODE_MEMORY:
              to =
                field.type === this.WptConstants.FIELD_NUMBER ||
                field.type === this.WptConstants.FIELD_CALCULATED
                  ? "values"
                  : "rows";
              break;
            case this.WptConstants.MODE_OLAP:
              to =
                field.type === this.WptConstants.FIELD_MEASURE
                  ? "values"
                  : controlType || "rows";
              break;
            default:
          }
          this.moveFieldBetweenControls("fields", to);
        }
      }
    },

    fieldFiltered() {
      return isFieldFiltered(this.pivot, this.source.mode, this.field);
    },

    fieldChanged() {
      return this.pivot.fields[this.field.id];
    },

    controlType() {
      return getControlType(this.pivot, this.source.mode, this.field);
    },

    moveToControlFlag() {
      return (
        (this.source.mode === this.WptConstants.MODE_MEMORY &&
          this.field.type !== this.WptConstants.FIELD_CALCULATED) ||
        (this.source.mode === this.WptConstants.MODE_OLAP &&
          this.field.type !== this.WptConstants.FIELD_MEASURE)
      );
    },

    fieldStyle() {
      const color =
        this.field.type === this.WptConstants.FIELD_MEASURES
          ? "orangered;"
          : this.fieldChecked
          ? "blue;"
          : "black;";
      // const fontWeight = this.fieldChecked ? "bold;" : "normal";
      // return "color: "+ color + "font-weight: " + fontWeight;
      return "color: " + color;
    }
  },

  methods: {
    highlight(name) {
      if (this.fieldsListFilter) {
        const search = new RegExp(
          this.fieldsListFilter.replace(/[.*+?^${}()|[\]\\]/g, "\\$&"),
          "gi"
        );
        return name.replace(
          search,
          '<span style="background-color: ' +
            this.highLightColor +
            ';">$&</span>'
        );
      }
      return name;
    },

    switchFieldsItemExpanded() {
      const newFields = {};

      for (let id in this.source.fields) {
        const field = Object.assign({}, this.source.fields[id]);
        if (field.id === this.field.id) {
          field.expanded = !field.expanded;
        }
        newFields[id] = field;
      }

      this.$store.commit("wpt/updateSourceData", {
        sourceId: this.source.id,
        sourceData: {
          fields: newFields
        }
      });
    },

    moveFieldBetweenControls(from, to) {
      this.$store.dispatch("wpt/moveFieldBetweenControls", {
        sheet: this.sheet,
        pivot: this.pivot,
        source: this.source,
        field: this.field,
        from,
        to
      });
    },

    removeFieldFromControl(from) {
      this.$store.dispatch("wpt/removeFieldFromControl", {
        sheet: this.sheet,
        pivot: this.pivot,
        source: this.source,
        field: this.field,
        from
      });
    },

    addToFilters() {
      this.moveFieldBetweenControls("fields", "filters");
    },

    addToColumns() {
      this.moveFieldBetweenControls("fields", "columns");
    },

    addToRows() {
      this.moveFieldBetweenControls("fields", "rows");
    },

    addToValues() {
      this.moveFieldBetweenControls("fields", "values");
    },

    removeCalculatedField() {
      this.$store.dispatch("wpt/removeCalculatedField", {
        sheet: this.sheet,
        source: this.source,
        field: this.field
      });
    },

    openSettingDialog() {
      if (this.field.type === this.WptConstants.FIELD_CALCULATED) {
        this.$store.commit("dialog/open", {
          type: "SHEET_CALCULATED_FIELD",
          title: this.field.name,
          width: 500,
          payload: {
            sheet: this.sheet,
            field: this.field
          }
        });
      } else {
        this.$store.commit("dialog/open", {
          type: "SHEET_FIELD_SETTING",
          title: this.field.name,
          width: 500,
          payload: {
            sheet: this.sheet,
            field: this.field
          }
        });
      }
    }
  }
};
</script>

<style></style>



