<template>
  <div>
    <div class="wpt-section">
      <div>{{ $t("Lbl_WebServiceUrl") }}</div>
      <div>
        <input 
          v-model="fileUrl" 
          :placeholder="$t('Msg_InputWebServiceUrl')" >
      </div>
    </div>
    <div class="wpt-section">
      <wpt-button
        :disabled="!fileUrl"
        :label="$t('Btn_Load')"
        @click="loadCsvFileUrl"
      />
    </div>

    <div class="wpt-section">
      <div>{{ $t("Lbl_Samples") }}</div>

      <div 
        v-for="item in fileLinks" 
        :key="item.url">
        <wpt-button
          :label="item.label"
          type="plain"
          @click="readCsvFileUrl(item.url)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: {
    createSource: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      fileUrl: ""
    };
  },

  computed: {
    options() {
      return this.$store.state.options;
    },

    fileLinks() {
      return this.options.fileLinks.filter(
        item => item.type === this.WptConstants.FORMAT_WSDATA
      );
    }
  },

  methods: {
    loadCsvFileUrl() {
      this.readCsvFileUrl(this.fileUrl);
    },

    readCsvFileUrl(fileUrl) {
      this.$store.commit("system/setMessage", {
        text: this.$t("Msg_LoadingFile"),
        loading: true
      });

      axios
        .post(
          this.options.server.fileProxy,
          {},
          {
            timeout: this.options.server.fileTimeout,
            responseType: "json",
            headers: { "x-file-url": fileUrl }
          }
        )
        .then(response => {
          const data = response.data;
          if (data.attrArray && data.dataArray) {
            const { attrArray, dataArray } = data;

            this.$store.dispatch("wpt/loadDataArray", {
              attrArray,
              dataArray,
              successHandler: response => {
                this.$store.commit("dialog/setData", {
                  type: this.WptConstants.FORMAT_WSDATA,
                  url: fileUrl,
                  content: null,
                  newSource: {
                    name: this.$t("Lbl_Source") + 1,
                    mode: this.WptConstants.MODE_MEMORY,
                    url: fileUrl,
                    data: dataArray,
                    fields: response
                  }
                });
                this.createSource();
              },
              errorHandler: null
            });
          } else {
            this.$store.commit("system/setMessage", {
              text: this.$t("Msg_WrongWSData")
            });
          }
        })
        .catch(error => {
          this.$store.commit("system/setMessage", {
            text: error.message || error
          });
        });
    }
  }
};
</script>

<style></style>



