import { mergeRecursive } from "~/services/CommonUtils";

export default {
  setSource(state, source) {
    state.source = source;
  },

  setSheet(state, sheet) {
    state.sheet = sheet;
  },

  updateSheetData(state, payload) {
    state.sheet = { ...mergeRecursive(state.sheet, payload.sheetData) };
  },

  replaceSheetData(state, payload) {
    state.sheet = Object.assign(state.sheet, { sheetData: payload.sheetData });
  },

  updateSheetPivot(state, payload) {
    state.sheet = Object.assign(state.sheet, {
      pivot: Object.assign(state.sheet.pivot, payload.pivot)
    });
  },

  updateSheetCollapsed(state, payload) {
    state.sheet = Object.assign(state.sheet, { collapsed: payload.collapsed });
  },

  updateSourceData(state, payload) {
    state.source = { ...mergeRecursive(state.source, payload.sourceData) };
  },

  updateSourceFieldData(state, payload) {
    state.source = {
      ...mergeRecursive(state.source, {
        fields: mergeRecursive(state.source.fields, {
          [payload.fieldId]: payload.sourceFieldData
        })
      })
    };
  }
};



