<template>
  <div :style="'height: ' + height + 'px;'">
    <div class="wpt-toolbar">
      <wpt-button
        v-tooltip="{content: $t('Lbl_Source'), container: popoverContainer}"
        icon="source"
        type="plain"
        @click="openSourceView"
      />

      <input
        :placeholder="$t('Btn_Search')"
        v-model="fieldsListFilter"
        style="margin: 2px 5px; width:70px;"
      >

      <wpt-button
        v-tooltip="{content: $t('Lbl_AddCalculatedField'), container: popoverContainer}"
        v-if="
          uiFlags.calculatedField && source.mode === WptConstants.MODE_MEMORY
        "
        icon="add"
        type="plain"
        @click="addCalculatedField"
      />

      <div style="flex:1;" />

      <wpt-button
        v-tooltip="{content: item.label, container: popoverContainer}"
        v-for="item in sortOptions"
        :key="item.value"
        :icon="item.iconName"
        :style="
          'background-color: ' +
            (fieldsListSort === item.value ? highLightColor : 'initial')
        "
        type="plain"
        @click="() => setFiltersListSort(item.value)"
      />
    </div>

    <div
      :style="'height: ' + (height - 32) + 'px; margin: 0 5px;'"
      class="wpt-list-container"
      @dragover="dragover"
      @drop="drop"
    >
      <fields-list
        :sheet="sheet"
        :source="source"
        :fields="fields"
        :tree-level="0"
      />
    </div>
  </div>
</template>

<script>
import FieldsList from "./FieldsList.vue";
import { generateOlapSheetFields } from "../../../../services/SheetService";

export default {
  components: {
    FieldsList
  },

  props: {
    sheet: {
      type: Object,
      required: true
    },
    source: {
      type: Object,
      required: true
    },
    height: {
      type: Number,
      required: false,
      default: 300
    }
  },

  data() {
    return {
      sortOptions: [
        { value: 0, label: this.$t("Lbl_NotSort"), iconName: "sort" },
        {
          value: 1,
          label: this.$t("Lbl_SortAscend"),
          iconName: "sort-alpha-asc"
        },
        {
          value: -1,
          label: this.$t("Lbl_SortDescend"),
          iconName: "sort-alpha-desc"
        }
      ]
    };
  },

  computed: {
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    options() {
      return this.$store.state.options;
    },
    toolbarStyle() {
      return "background-color: " + this.options.styles.toolBarColor;
    },
    uiFlags() {
      return this.options.uiFlags;
    },
    highLightColor() {
      return this.options.styles.highLightColor;
    },

    pivot() {
      return this.sheet.pivot;
    },

    fieldsListFilter: {
      get() {
        return this.options.wpt.sheet.fieldsListFilter;
      },
      set(value) {
        this.$store.commit("options/changeSheetOptions", { fieldsListFilter: value });
      }
    },

    fieldsListSort() {
      return this.options.wpt.sheet.fieldsListSort;
    },

    fields() {
      const sheetFields = this.pivot.fields;
      const fieldsListFilter = this.fieldsListFilter;
      const fieldsListSort = this.fieldsListSort;

      let fields = [];

      switch (this.source.mode) {
        case this.WptConstants.MODE_MEMORY:
          fields = [...this.source.fields];
          for (let id in sheetFields) {
            if (sheetFields[id].type === this.WptConstants.FIELD_CALCULATED) {
              fields.push(sheetFields[id]);
            }
          }
          fields = fields.filter(field =>
            field.name.match(new RegExp(fieldsListFilter.trim(), "i"))
          );
          if (fieldsListSort) {
            fields.sort((a, b) => {
              if (a.name > b.name) {
                return fieldsListSort < 0 ? -1 : 1;
              } else {
                return fieldsListSort < 0 ? 1 : -1;
              }
            });
          }
          break;

        case this.WptConstants.MODE_OLAP:
          fields = generateOlapSheetFields(
            this.source.fields,
            fieldsListFilter.trim(),
            fieldsListSort
          );
          break;

        default:
      }

      return fields;
    },
    dragData() {
      return this.$store.state.system.dragData;
    }
  },

  methods: {
    openSourceView() {
      this.$store.commit("options/setView", this.WptConstants.VIEW_SOURCE);
    },

    setFiltersListSort(value) {
      this.$store.commit("options/changeSheetOptions", { fieldsListSort: value });
    },

    addCalculatedField() {
      this.$store.commit("dialog/open", {
        type: "SHEET_CALCULATED_FIELD",
        title: this.$t("Lbl_AddCalculatedField"),
        width: 500,
        payload: {
          sheet: this.sheet
        }
      });
    },

    dragover(event) {
      if (this.dragData.type && this.dragData.type !== "fields") {
        event.preventDefault();
      }
    },
    drop(event) {
      event.preventDefault();
      if (this.dragData.type && this.dragData.type !== "fields") {
        this.$store.dispatch("wpt/removeFieldFromControl", {
          sheet: this.sheet,
          pivot: this.pivot,
          source: this.source,
          field: this.dragData.item,
          from: this.dragData.type
        });
      }
    }
  }
};
</script>

<style></style>



