export default {
  /*
     * 
     *
     * @method bind
     * @param  {dom||window}   elem        domwindow
     * @param  {String}        event       
     * @param  {Function}      handler     
     */
  bind(elem, event, handler) {
    if (elem && elem !== "undefined" && event && handler) {
      event =
        event === "mousewheel"
          ? document.onmousewheel !== undefined
            ? "mousewheel"
            : "DOMMouseScroll"
          : event;

      if (document.attachEvent) {
        //if IE (and Opera depending on user setting)

        elem.attachEvent("on" + event, handler);
      } else {
        //WC3 browsers

        elem.addEventListener(event, handler, false);
      }
    }
  },

  /*
     * 
     *
     * @method unbind
     * @param  {dom||window}   elem         domwindow
     * @param  {String}        event        
     * @param  {Function||Array<Function>}  handler    
     */
  unbind(elem, event, handler) {
    if (elem && elem !== "undefined" && event && handler) {
      event =
        event === "mousewheel"
          ? document.onmousewheel !== undefined
            ? "mousewheel"
            : "DOMMouseScroll"
          : event;

      var handlers = [];
      if (Array.isArray(handler) && handler.length > 0) {
        handlers = handler;
      } else {
        handlers.push(handler);
      }

      if (document.removeEventListener) {
        handlers.forEach(e => {
          elem.removeEventListener(event, e, false);
        });
      } else {
        handlers.forEach(e => {
          elem.removeEventListener("on" + event, e);
        });
      }
    }
  }
};



