import { renderTable } from "~/services/SheetGridService";
import tableScrollMixin from "~/ui-widgets/table/table-scroll-mixin.js";

export default {
  mixins: [tableScrollMixin],
  render: function(h) {
    // let start = new Date();
    const table = renderTable(
      h,
      this.$t.bind(this),
      this.sheetId,
      this.sourceMode,
      this.layoutData,
      this.gridOptions,
      this.pivot,
      this.sourceFields,
      this.switchTreeItemExpanded,
      this.drillThrough,
      this.openValueFieldSettingDialog,
      this.width,
      this.height,
      this.popoverContainer
    );
    // console.log("Render grid table takes: ", new Date() - start);
    return table;
  },

  props: [
    "sheetId",
    "layoutData",
    "gridOptions",
    "pivot",
    "sourceMode",
    "sourceFields",
    "width",
    "height",
    "popoverContainer"
  ],

  methods: {
    drillThrough(row, column, event) {
      const options = this.$store.state.options;
      if (this.wptEdition !== this.WptConstants.EDITION_FREE) {
        const sheet = this.$store.state.wpt.sheet;
        this.$eventBus.$emit("drillThrough", { sheet, row, column, event });
        if (options.customHandlers && options.customHandlers.drillThrough) {
          return;
        }
      }
      this.$store.commit("modal/open", {
        type: this.WptConstants.MODAL_DRILLTHROUGH,
        payload: {
          row,
          column
        }
      });
    },
    switchTreeItemExpanded(id) {
      this.$store.dispatch("wpt/switchTreeItemExpanded", {
        id
      });
    },

    openValueFieldSettingDialog(vIndex) {
      const sheet = this.$store.state.wpt.sheet;
      // const source = this.$store.state.wpt.sources[sheet.sourceId];
      const field = sheet.pivot.values[vIndex];

      this.$store.commit("dialog/open", {
        type: "SHEET_VALUE_FIELD_SETTING",
        title: field.name,
        width: 500,
        payload: {
          sheet: sheet,
          field: field
        }
      });
    }
  },

  beforeUpdate() {
    this.start = new Date();
  },

  updated() {
    // console.log("Grid updated", new Date() - this.start);
  },

  beforeMount() {
    this.start = new Date();
  },
  mounted() {
    // console.log("Grid mounted", new Date() - this.start);
  }
};



