import { renderCharts } from "../../../services/SheetChartService";

export default {
  render: function(h) {
    const charts = renderCharts(
      h,
      this.$t.bind(this),
      this.sheetId,
      this.sourceMode,
      this.layoutData,
      this.chartOptions,
      this.pivot,
      this.sourceFields,
      this.switchTreeItemExpanded,
      this.drillThrough
    );

    const hCharts = h(
      "div",
      {
        attrs: {
          id: "pivot-chart-" + this.sheetId
        }
      },
      charts.map(chart =>
        h("highcharts", {
          style: {
            float: "left",
            margin: "5px"
          },
          props: {
            options: chart
          }
        })
      )
    );
    // console.log("Render chart takes: ", new Date() - start);
    return hCharts;
  },

  props: [
    "sheetId",
    "layoutData",
    "chartOptions",
    "pivot",
    "sourceMode",
    "sourceFields"
  ],

  methods: {
    drillThrough(row, column, event) {
      const options = this.$store.state.options;
      if (this.wptEdition !== this.WptConstants.EDITION_FREE) {
        const sheet = this.$store.state.wpt.sheet;
        this.$eventBus.$emit("drillThrough", { sheet, row, column, event });
        if (options.customHandlers && options.customHandlers.drillThrough) {
          return;
        }
      }

      this.$store.commit("modal/open", {
        type: this.WptConstants.MODAL_DRILLTHROUGH,
        payload: {
          row,
          column
        }
      });
    },
    switchTreeItemExpanded(id) {
      this.$store.dispatch("wpt/switchTreeItemExpanded", {
        id
      });
    },
    hookClickEventForGroupLabel() {
      const switchTreeItemExpanded = this.switchTreeItemExpanded;
      Array.from(
        document.body.querySelectorAll(".highcharts-axis-labels text tspan")
      ).forEach(function(node) {
        node.onclick = function() {
          const arr = this.getAttribute("style").match(
            /wpt_custom_id:[A-Za-z0-9-]*;/
          );
          if (arr) {
            const id = arr[0].replace("wpt_custom_id:", "").replace(";", "");
            switchTreeItemExpanded(id);
          }
        };
      });
    }
  },

  beforeUpdate() {
    this.start = new Date();
  },

  updated() {
    // console.log("Chart updated", new Date() - this.start);
    this.hookClickEventForGroupLabel();
  },

  beforeMount() {
    this.start = new Date();
  },
  mounted() {
    // console.log("Chart mounted", new Date() - this.start);
    this.hookClickEventForGroupLabel();
  }
};



