<template>
  <div
    :style="'width: ' + width + 'px; height: ' + height + 'px;'"
    class="vld-parent"
    @dragover="dragover"
    @drop="drop"
  >
    <wpt-main
      :width="width"
      :height="height"
    />

    <wpt-message />
  </div>
</template>

<script>
import WptMain from "./WptMain.vue";
import WptMessage from "./WptMessage.vue";
import { readLocalFile } from "~/services/FileService";

export default {
  components: {
    WptMain,
    WptMessage
  },
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },
  methods: {
    dragover(event) {
      // Prevent default behavior (Prevent file from being opened)
      event.preventDefault();
    },
    drop(event) {
      // Prevent default behavior (Prevent file from being opened)
      event.preventDefault();

      let file = null;

      if (event.dataTransfer.items) {
        // Use DataTransferItemList interface to access the file(s)
        for (let i = 0; i < event.dataTransfer.items.length; i++) {
          // If dropped items aren't files, reject them
          if (event.dataTransfer.items[i].kind === 'file') {
            file = event.dataTransfer.items[i].getAsFile();
            break;
          }
        }
      } else {
        // Use DataTransfer interface to access the file(s)
        file = event.dataTransfer.files[0];
      }

      if (file) {
        const fileTypes = ['csv', 'xls', 'xlsx', 'wpt'];
        const fileType = file.name.split('.').pop().toLowerCase();
        if (fileTypes.indexOf(fileType) > -1) {
          readLocalFile(file, fileType, this.$store, this.$t.bind(this));
        }
      }
    }
  }

};
</script>

<style></style>



