<template>
  <fullscreen
    ref="WebPivotTable"
    :style="'font-size:' + styles.fontSize"
    :class="'wpt wpt-fullscreen ' + containerClass"
    background="#fff"
    @change="fullscreenChange"
  >
    <wpt-menu
      :toggle-full-screen="toggleFullScreen"
      :fullscreen="fullscreen"
    />
    <wpt-container
      :width="width"
      :height="height"
    />
    <wpt-dialog />
  </fullscreen>
</template>

<script>
import Fullscreen from "vue-fullscreen/src/component.vue";

import WptMenu from "./WptMenu.vue";
import WptContainer from "./WptContainer.vue";
import WptDialog from "./WptDialog.vue";
import apis from "../apis";
import i18n from "../i18n";
import createStore from "../store";
import { generateId } from "~/services/CommonUtils";

export default {
  components: {
    Fullscreen,
    WptMenu,
    WptContainer,
    WptDialog
  },
  props: {
    options: {
      type: String,
      required: false,
      default: ""
    }
  },

  data: () => {
    return {
      containerClass: "wpt" + generateId(),
      fullscreen: false,
      width: 0,
      height: 0
    };
  },

  computed: {
    uiFlags() {
      return this.$store.state.options.uiFlags;
    },
    styles() {
      return this.$store.state.options.styles;
    }
  },

  beforeCreate() {
    this.$store = createStore();
  },

  mounted() {
    this.getWindowSize();
    window.addEventListener("resize", this.getWindowSize);

    for (let eventName in apis) {
      this.$eventBus.$on(eventName, apis[eventName].bind(this));
    }

    if (this.options) {
      this.$eventBus.$emit("setOptions", JSON.parse(this.options));
    }

    if (this.$store.state.options.leavePageWarning) {
      window.addEventListener("beforeunload", e => {
        // User need allow popup box in their browser's setting
        const msg = this.$t("Msg_LeavingPage");
        e.returnValue = msg;
        return msg;
      });
    }

    if (!("en" in i18n.messages)) {
      this.$store.dispatch("options/setLocale", "en");
    }

    this.$store.commit("system/setContainerClass", this.containerClass);
  },

  beforeDestroy() {
    window.removeEventListener("resize", this.getWindowSize);
  },

  methods: {
    getWindowSize() {
      this.width = this.$el.clientWidth;
      this.height = this.$el.clientHeight;
      this.$store.dispatch("system/setScreenSize", {
        width: this.width,
        height: this.height
      });
    },
    fullscreenChange(fullscreen) {
      this.fullscreen = fullscreen;
    },
    toggleFullScreen() {
      this.$refs["WebPivotTable"].toggle();
    }
  }
};
</script>

<style>
</style>



