<template>
  <div
    :style="'background-color: ' + highLightColor + '; margin-bottom: 2px;'"
    class="wpt-flex-row wpt-list-item"
  >
    <span
      v-if="
        field.type === WptConstants.FIELD_VALUES ||
          field.type === WptConstants.FIELD_HIERARCHY ||
          field.type === WptConstants.FIELD_LEVEL ||
          field.type === WptConstants.FIELD_MEASURE
      "
    >
      <icon :name="getTypeIconSrc" />
    </span>

    <div :style="'flex: 1;'">
      <v-popover
        :container="popoverContainer"
        placement="right-end"
      >
        <span>
          {{
            field.type === WptConstants.FIELD_VALUES
              ? $t("Lbl_Values")
              : field.name
          }}
        </span>
        <template slot="popover">
          <div
            v-if="
              source.mode === WptConstants.MODE_MEMORY &&
                controlType === 'values'
            "
            class="wpt-menu-item"
            @click="openSettingDialog"
          >
            <icon name="setting" />
            <span>{{ $t("Lbl_ValueFieldSettings") }}</span>
          </div>

          <div
            v-if="
              ((source.mode === WptConstants.MODE_MEMORY &&
                field.type !== WptConstants.FIELD_VALUES &&
                field.type !== WptConstants.FIELD_CALCULATED) ||
                field.type === WptConstants.FIELD_LEVEL) &&
                controlType !== 'values'
            "
            class="wpt-menu-item"
            @click="openSettingDialog"
          >
            <icon name="setting" />
            <span>{{ $t("Lbl_FieldSettings") }}</span>
          </div>

          <template
            v-if="position.count > 1 && field.type !== WptConstants.FIELD_LEVEL"
          >
            <div
              v-if="position.index !== 0"
              class="wpt-menu-item"
              @click="moveUp"
            >
              <icon name="up" />
              <span>{{ $t("Lbl_MoveUp") }}</span>
            </div>

            <div
              v-if="position.index !== position.count - 1"
              class="wpt-menu-item"
              @click="moveDown"
            >
              <icon name="down" />
              <span>{{ $t("Lbl_MoveDown") }}</span>
            </div>

            <div
              v-if="position.index !== 0"
              class="wpt-menu-item"
              @click="moveToBegin"
            >
              <icon name="top" />
              <span>{{ $t("Lbl_MoveToBegin") }}</span>
            </div>

            <div
              v-if="position.index !== position.count - 1"
              class="wpt-menu-item"
              @click="moveToEnd"
            >
              <icon name="bottom" />
              <span>{{ $t("Lbl_MoveToEnd") }}</span>
            </div>
          </template>

          <template v-if="movable">
            <div
              v-if="
                controlType !== 'filters' &&
                  field.type !== WptConstants.FIELD_VALUES &&
                  field.type !== WptConstants.FIELD_MEASURE
              "
              class="wpt-menu-item"
              @click="moveToFilters"
            >
              <icon name="filters" />
              <span>{{ $t("Lbl_MoveToFilters") }}</span>
            </div>

            <div
              v-if="controlType !== 'rows'"
              class="wpt-menu-item"
              @click="moveToRows"
            >
              <icon name="rows" />
              <span>{{ $t("Lbl_MoveToRows") }}</span>
            </div>

            <div
              v-if="controlType !== 'columns'"
              class="wpt-menu-item"
              @click="moveToColumns"
            >
              <icon name="columns" />
              <span>{{ $t("Lbl_MoveToColumns") }}</span>
            </div>

            <div
              v-if="
                controlType !== 'values' &&
                  field.type !== WptConstants.FIELD_VALUES &&
                  field.type !== WptConstants.FIELD_HIERARCHY &&
                  field.type !== WptConstants.FIELD_LEVEL
              "
              class="wpt-menu-item"
              @click="moveToValues"
            >
              <icon name="values" />
              <span>{{ $t("Lbl_MoveToValues") }}</span>
            </div>
          </template>

          <div 
            class="wpt-menu-item" 
            @click="removeField">
            <icon name="remove" />
            <span>{{ $t("Lbl_RemoveField") }}</span>
          </div>
        </template>
      </v-popover>
    </div>

    <div 
      class="wpt-cursor-pointer" 
      @click.stop="removeField">
      <icon name="remove" />
    </div>

    <div
      v-if="
        source.mode === WptConstants.MODE_MEMORY &&
          field.type !== WptConstants.FIELD_VALUES
      "
      class="wpt-cursor-pointer"
      @click.stop="openSettingDialog"
    >
      <icon 
        :color="fieldChanged ? 'blue' : 'black'" 
        name="edit" />
    </div>
  </div>
</template>

<script>
import { getFieldIndexInControl } from "../../../../services/SheetService";
import { getFieldTypeIconName } from "../../../../services/CommonUtils";

export default {
  props: {
    sheet: {
      type: Object,
      required: true
    },
    source: {
      type: Object,
      required: true
    },
    field: {
      type: Object,
      required: true
    },
    controlType: {
      type: String,
      required: true
    }
  },

  computed: {
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    pivot() {
      return this.sheet.pivot;
    },
    getTypeIconSrc() {
      return getFieldTypeIconName(this.field.type);
    },
    highLightColor() {
      return this.$store.state.options.styles.highLightColor;
    },

    fieldChanged() {
      return (
        ((this.controlType === "filters" ||
          this.controlType === "rows" ||
          this.controlType === "columns") &&
          this.pivot.fields[this.field.id]) ||
        this.controlType === "values"
      );
    },

    position() {
      let count = 0;
      let index = -1;

      if (this.field.type === this.WptConstants.FIELD_VALUES) {
        count = this.pivot[this.controlType].length + 1;
        index = this.pivot.valuesIndex;
      } else if (this.field.type !== this.WptConstants.FIELD_LEVEL) {
        index = getFieldIndexInControl(
          this.pivot,
          this.source.mode,
          this.field,
          this.controlType
        );
        if (
          (this.controlType === "columns" && this.pivot.valuesInAxis === 0) ||
          (this.controlType === "rows" && this.pivot.valuesInAxis === 1)
        ) {
          count = this.pivot[this.controlType].length + 1;
          if (index >= this.pivot.valuesIndex) {
            index += 1;
          }
        } else {
          count = this.pivot[this.controlType].length;
        }
      }

      return { count, index };
    },

    movable() {
      let movable = false;

      switch (this.source.mode) {
        case this.WptConstants.MODE_MEMORY:
          if (this.controlType !== "values") {
            movable = true;
          } else {
            const sheetField = this.pivot.fields[this.field.fieldId];
            if (
              !sheetField ||
              sheetField.type !== this.WptConstants.FIELD_CALCULATED
            ) {
              movable = true;
            }
          }
          break;

        case this.WptConstants.MODE_OLAP:
          if (this.controlType !== "values") {
            movable = true;
          }
          break;

        default:
      }
      return movable;
    }
  },

  methods: {
    moveFieldWithinSameControl(controlType, direction) {
      this.$store.dispatch("wpt/moveFieldWithinSameControl", {
        sheet: this.sheet,
        pivot: this.pivot,
        source: this.source,
        field: this.field,
        controlType,
        direction
      });
    },

    moveFieldBetweenControls(from, to) {
      this.$store.dispatch("wpt/moveFieldBetweenControls", {
        sheet: this.sheet,
        pivot: this.pivot,
        source: this.source,
        field: this.field,
        from,
        to
      });
    },

    removeFieldFromControl(from) {
      this.$store.dispatch("wpt/removeFieldFromControl", {
        sheet: this.sheet,
        pivot: this.pivot,
        source: this.source,
        field: this.field,
        from
      });
    },

    moveUp() {
      this.moveFieldWithinSameControl(this.controlType, "up");
    },

    moveDown() {
      this.moveFieldWithinSameControl(this.controlType, "down");
    },

    moveToBegin() {
      this.moveFieldWithinSameControl(this.controlType, "beginning");
    },

    moveToEnd() {
      this.moveFieldWithinSameControl(this.controlType, "end");
    },

    moveToFilters() {
      this.moveFieldBetweenControls(this.controlType, "filters");
    },

    moveToColumns() {
      this.moveFieldBetweenControls(this.controlType, "columns");
    },

    moveToRows() {
      this.moveFieldBetweenControls(this.controlType, "rows");
    },

    moveToValues() {
      this.moveFieldBetweenControls(this.controlType, "values");
    },

    removeField() {
      this.removeFieldFromControl(this.controlType);
    },

    openSettingDialog() {
      this.$store.commit("dialog/open", {
        type:
          this.controlType === "values"
            ? "SHEET_VALUE_FIELD_SETTING"
            : "SHEET_FIELD_SETTING",
        title: this.field.name,
        width: 500,
        payload: {
          sheet: this.sheet,
          field: this.field
        }
      });
    }
  }
};
</script>

<style></style>



