<template>
  <div class="wpt-pagination">
    <div class="wpt-pagination-total">
      <div>{{ $t("Lbl_Total") }}</div>
      <div>{{ total }}</div>
    </div>

    <wpt-button
      :disabled="newPageIndex === 1"
      icon="collapse-columns"
      type="plain"
      @click="prevPage"
    />

    <pager
      :page-count="pageCount"
      :page-index="newPageIndex"
      :show-paging-count="showPagingCount"
      :jump-page-handler="jumpPageHandler"
    />

    <wpt-button
      :disabled="newPageIndex === pageCount"
      icon="expand-columns"
      type="plain"
      @click="nextPage"
    />

    <wpt-select
      :options="newPageSizeOption"
      type="plain"
      style="width:80px;"
      @change="handleChange"
    />

    <input 
      :value="newPageIndex" 
      style="width:30px;" 
      @keyup="jumperEnter" >
  </div>
</template>

<script>
import pager from "./pager.vue";

export default {
  components: {
    pager
  },
  props: {
    total: {
      type: Number,
      require: true,
      default: 0
    },

    pageIndex: {
      type: Number,
      default: 0
    },

    showPagingCount: {
      type: Number,
      default: 4
    },

    pageSize: {
      type: Number,
      default: 20
    },

    pageSizeOption: {
      type: Array,
      default: function() {
        return [10, 20, 50, 100, 1000];
      }
    }
  },
  data() {
    return {
      newPageIndex:
        this.pageIndex && this.pageIndex > 0 ? parseInt(this.pageIndex) : 1,
      newPageSize: this.pageSize,
      newPageSizeOption: []
    };
  },

  computed: {
    pageCount() {
      return Math.ceil(this.total / this.newPageSize);
    }
  },
  watch: {
    pageIndex: function(newVal) {
      this.newPageIndex = newVal;
    },

    pageSize: function(newVal) {
      this.newPageSize = newVal;
      this.initSelectOption();
    }
  },
  created() {
    this.initSelectOption();
  },

  methods: {
    handleChange(items) {
      if (Array.isArray(items) && items.length > 0) {
        let item = items.find(x => x.selected);
        if (item) {
          this.pageSizeChangeHandler(item.value);
        }
      }
    },
    jumperEnter(event) {
      if (event.keyCode !== 13) return;

      const val = this.getValidNum(event.target.value);

      this.newPageIndex = val;
      this.$emit("page-change", this.newPageIndex);
    },
    getValidNum(value) {
      let result = 1;

      value = parseInt(value, 10);

      if (isNaN(value) || value < 1) {
        result = 1;
      } else {
        if (value < 1) {
          result = 1;
        } else if (value > this.pageCount) {
          result = this.pageCount;
        } else {
          result = value;
        }
      }
      return result;
    },

    jumpPageHandler(newPageIndex) {
      this.newPageIndex = newPageIndex;
      this.$emit("page-change", this.newPageIndex);
    },

    prevPage() {
      if (this.newPageIndex > 1) {
        this.newPageIndex = this.newPageIndex - 1;
        this.$emit("page-change", this.newPageIndex);
      }
    },

    nextPage() {
      if (this.newPageIndex < this.pageCount) {
        this.newPageIndex = this.newPageIndex + 1;
        this.$emit("page-change", this.newPageIndex);
      }
    },

    pageSizeChangeHandler() {
      let item = this.newPageSizeOption.find(x => x.selected);

      if (item) {
        this.newPageSize = item.value;
        this.newPageIndex = 1;
        this.$emit("page-size-change", this.newPageSize);
      }
    },

    initSelectOption() {
      this.newPageSizeOption = this.pageSizeOption.map(x => {
        var temp = {};

        temp.value = x;
        temp.label = x + " /";
        if (this.newPageSize === x) {
          temp.selected = true;
        }

        return temp;
      });
    },

    goBackPageIndex() {
      this.newPageIndex =
        this.pageIndex && this.pageIndex > 0 ? parseInt(this.pageIndex) : 1;
    },

    goBackPageSize() {
      if (this.pageSize > 0) {
        this.newPageSize = this.pageSize;
        this.initSelectOption();
      }
    }
  }
};
</script>
<style scoped>
.wpt-pagination {
  display: flex;
  flex-direction: row;
  height: 30px;
}
.wpt-pagination-total {
  display: flex;
  flex-direction: column;
  font-size: small;
  padding: 0 5px;
}
</style>



