<template>
  <div class="wpt-flex-row wpt-list-item">
    <span
      v-if="field.children"
      class="wpt-cursor-pointer"
      @click="switchFieldsItemExpanded"
    >
      <icon :name="field.expanded ? 'collapse' : 'expand'" />
    </span>
    <span
      v-else-if="treeLevel > 0 && field.type !== WptConstants.FIELD_MEASURE"
      class="wpt-indent"
    />

    <span v-if="getTypeIconSrc">
      <icon :name="getTypeIconSrc" />
    </span>

    <div style="flex: 1;">
      <div v-if="field.type === WptConstants.FIELD_MEASURES">
        {{ $t("Lbl_Measures") }}
      </div>

      <div v-else>
        <span 
          :style="fieldStyle" 
          v-html="highlight(field.name)" />
        <span 
          v-if="source.mode === WptConstants.MODE_MEMORY" 
          class="wpt-badge">
          {{ field.count }}
        </span>
      </div>
    </div>

    <div
      v-if="
        source.mode === WptConstants.MODE_MEMORY ||
          field.type === WptConstants.FIELD_LEVEL
      "
      class="wpt-cursor-pointer"
      @click.stop.prevent="openSettingDialog"
    >
      <icon 
        name="edit" 
        color="black" />
    </div>
  </div>
</template>

<script>
import { getFieldTypeIconName } from "~/services/CommonUtils";

export default {
  name: "FieldsList",
  props: {
    source: {
      type: Object,
      required: true
    },
    field: {
      type: Object,
      required: true
    },
    treeLevel: {
      type: Number,
      required: true
    }
  },

  computed: {
    options() {
      return this.$store.state.options;
    },

    fieldsListFilter() {
      return this.options.wpt.source.fieldsListFilter || "";
    },

    getTypeIconSrc() {
      return getFieldTypeIconName(this.field.type);
    },
    highLightColor() {
      return this.options.styles.highLightColor;
    },

    fieldStyle() {
      const color =
        this.field.type === this.WptConstants.FIELD_MEASURES
          ? "orangered;"
          : this.fieldChecked
          ? "blue;"
          : "black;";
      // const fontWeight = this.fieldChecked ? "bold;" : "normal";
      // return "color: "+ color + "font-weight: " + fontWeight;
      return "color: " + color;
    }
  },

  methods: {
    highlight(name) {
      if (this.fieldsListFilter) {
        const search = new RegExp(
          this.fieldsListFilter.replace(/[.*+?^${}()|[\]\\]/g, "\\$&"),
          "gi"
        );
        return name.replace(
          search,
          '<span style="background-color: ' +
            this.highLightColor +
            ';">$&</span>'
        );
      }
      return name;
    },

    switchFieldsItemExpanded() {
      const newFields = {};

      for (let id in this.source.fields) {
        const field = Object.assign({}, this.source.fields[id]);
        if (field.id === this.field.id) {
          field.expanded = !field.expanded;
        }
        newFields[id] = field;
      }

      this.$store.commit("wpt/updateSourceData", {
        sourceId: this.source.id,
        sourceData: {
          fields: newFields
        }
      });
    },

    openSettingDialog() {
      this.$store.commit("dialog/open", {
        type: "SOURCE_FIELD_SETTING",
        title: this.field.name,
        width: 500,
        payload: {
          source: this.source,
          field: this.field
        }
      });
    }
  }
};
</script>

<style></style>



