<template>
  <div :style="'height: ' + height + 'px; width: ' + width + 'px;'">
    <fields-list-container :height="height" />
  </div>
</template>

<script>
import FieldsListContainer from "./source-pivot/FieldsListContainer.vue";

export default {
  components: {
    FieldsListContainer
  },
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  }
};
</script>

<style></style>



