<template>
  <div :style="'height: ' + height + 'px; width: ' + width + 'px;'">
    test
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },

  computed: {
    options() {
      return this.$store.state.options;
    }
  }
};
</script>

<style></style>



