<template>
  <transition>
    <div
      v-show="isActive"
      :aria-busy="isActive"
      tabindex="0"
      class="vld-overlay is-active"
      aria-label="Loading"
    >
      <div 
        class="vld-background" 
        @click.prevent="cancel" />
      <div class="vld-icon">
        <svg
          v-if="message && message.loading"
          width="64"
          height="64"
          stroke="#000"
          viewBox="0 0 38 38"
          xmlns="http://www.w3.org/2000/svg"
        >
          <g
            fill="none"
            fill-rule="evenodd">
            <g
              transform="translate(1 1)"
              stroke-width="2">
              <circle
                stroke-opacity=".25"
                cx="18"
                cy="18"
                r="18" />
              <path d="M36 18c0-9.94-8.06-18-18-18">
                <animateTransform
                  attributeName="transform"
                  type="rotate"
                  from="0 18 18"
                  to="360 18 18"
                  dur="0.8s"
                  repeatCount="indefinite"
                />
              </path>
            </g>
          </g>
        </svg>

        <div
          v-if="message"
          class="wpt-message"
        >
          {{ message.text }}
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  computed: {
    message() {
      return this.$store.state.system.message;
    },
    isActive() {
      return this.message && (this.message.loading || this.message.text);
    }
  },
  beforeDestroy() {
    document.removeEventListener("keyup", this.keyPress);
  },
  mounted() {
    document.addEventListener("keyup", this.keyPress);
  },
  methods: {
    cancel() {
      if (!(this.message && this.message.loading)) {
        this.$store.commit("system/setMessage", {});
      }
    },
    keyPress(event) {
      // todo keyCode is deprecated
      if (event.keyCode === 27) this.cancel();
    }
  }
};
</script>

<style>
.vld-overlay {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  align-items: center;
  display: none;
  justify-content: center;
  overflow: hidden;
  z-index: 200;
}

.vld-overlay.is-active {
  display: flex;
}

.vld-overlay .vld-background {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  background: #fff;
  opacity: 0.5;
}

.vld-overlay .vld-icon {
  position: relative;
}
</style>



