import WptConstants from "../WptConstants";
import i18n from "~/i18n.js";
import { getValueIndex } from "./CommonUtils";

export const generateTooltipHtml = (row, column) => {
  const value =
    row.data && row.data[column.id] ? row.data[column.id].fmtValue : "";
  return (
    i18n.t("Lbl_Rows") +
    ": " +
    row.label +
    "<br/>" +
    i18n.t("Lbl_Columns") +
    ": " +
    column.label +
    "<br/>" +
    i18n.t("Lbl_Values") +
    ": " +
    value +
    "<br/>" +
    "<b>" +
    i18n.t("Lbl_DoubleClickToDrillThrough") +
    "</b>"
  );
};

export const generateSheetLayoutData = (
  sourceMode,
  pivotData,
  pivot,
  limit,
  sheetOptions,
  collapsed
) => {
  // let start = new Date();
  let layoutData = null;
  const maxRows =
    limit && limit.sheetLayoutMaxRows ? limit.sheetLayoutMaxRows : 10000;
  const maxColumns =
    limit && limit.sheetLayoutMaxColumns ? limit.sheetLayoutMaxColumns : 10000;
  const maxDataCells =
    limit && limit.sheetLayoutMaxDataCells
      ? limit.sheetLayoutMaxDataCells
      : 1000000;

  switch (sourceMode) {
    case WptConstants.MODE_MEMORY:
      layoutData = generateMemoryLayoutData(
        pivotData,
        pivot,
        sheetOptions,
        collapsed,
        maxRows,
        maxColumns,
        maxDataCells
      );
      break;
    case WptConstants.MODE_OLAP:
      layoutData = generateOlapLayoutData(
        pivotData,
        pivot,
        collapsed,
        maxRows,
        maxColumns,
        maxDataCells
      );
      break;
    default:
  }

  // console.log("Generate layoutData takes: ", new Date()- start);
  return layoutData;
};

const getValueFromItem = (values, valueObj, rowItem, colItem) => {
  let vIndex = Math.max(rowItem.vIndex, colItem.vIndex);
  const newRowItemId = rowItem.id.replace(/V[0-9]*/, "");
  const newColItemId = colItem.id.replace(/V[0-9]*/, "");

  if (vIndex < 0) {
    if (values.length === 1) {
      vIndex = 0;
    } else {
      return {
        value: 0,
        fmtValue: ""
      };
    }
  }

  let value = valueObj[newRowItemId + "-" + newColItemId + "-V" + vIndex] || 0;

  let showValueAs =
    values[vIndex].showValueAs || WptConstants.SHOW_AS_NO_CALCULATION;
  let compareValue, arr;

  switch (showValueAs) {
    case WptConstants.SHOW_AS_NO_CALCULATION: //No Calculation
      compareValue = 1;
      break;
    case WptConstants.SHOW_AS_PERC_GRAND_TOTAL: //% of Grand Total
      compareValue = valueObj["R-C-V" + vIndex];
      break;
    case WptConstants.SHOW_AS_PERC_COLUMN_TOTAL: //% of Column Total
      compareValue = valueObj["R-" + newColItemId + "-V" + vIndex];
      break;
    case WptConstants.SHOW_AS_PERC_ROW_TOTAL: //% of Row Total
      compareValue = valueObj[newRowItemId + "-C-V" + vIndex];
      break;
    case WptConstants.SHOW_AS_PERC_PARENT_COLUMN: //% of Parent Column Total
      arr = newColItemId.split("-");
      if (arr.length > 1) {
        arr = arr.slice(0, arr.length - 1);
      }
      compareValue =
        valueObj[newRowItemId + "-" + arr.join("-") + "-V" + vIndex];
      break;
    case WptConstants.SHOW_AS_PERC_PARENT_ROW: //% of Parent Row Total
      arr = newRowItemId.split("-");
      if (arr.length > 1) {
        arr = arr.slice(0, arr.length - 1);
      }
      compareValue =
        valueObj[arr.join("-") + "-" + newColItemId + "-V" + vIndex];
      break;
    default:
  }

  value = value / compareValue;

  const numberFormat = function(number) {
    let postComma,
      preComma,
      stringReverse,
      _ref,
      neg = false;

    stringReverse = function(str) {
      return str
        .split("")
        .reverse()
        .join("");
    };

    if (number < 0) {
      neg = true;
      number = -number;
    }
    _ref = (number + "").split(".");
    preComma = _ref[0];
    postComma = _ref[1] || "";

    preComma = stringReverse(
      stringReverse(preComma)
        .match(/.{1,3}/g)
        .join(",")
      // .join(WptOptions.get().thousandsSep)
    );

    return (neg ? "-" : "") + preComma + (postComma ? "." + postComma : "");
    // (postComma?WptOptions.get().decimalPoint + postComma:"");
  };

  let format = values[vIndex].format;
  let fmtValue = value;

  if (format && format.category) {
    switch (format.category.toUpperCase()) {
      case WptConstants.VALUE_FORMAT_CATEGORY_NUMBER:
        if (format.decimal > 0) {
          fmtValue = fmtValue.toFixed(format.decimal);
        } else {
          fmtValue = Math.round(fmtValue);
        }
        if (format.separatorFlag) {
          fmtValue = numberFormat(fmtValue);
        } else {
          fmtValue = (fmtValue + "").replace(/\./g, ".");
          // WptOptions.get().decimalPoint);
        }

        break;

      case WptConstants.VALUE_FORMAT_CATEGORY_CURRENCY:
      case WptConstants.VALUE_FORMAT_CATEGORY_ACCOUNTING:
        if (format.decimal > 0) {
          fmtValue = fmtValue.toFixed(format.decimal);
        } else {
          fmtValue = Math.round(fmtValue);
        }
        if (format.separatorFlag) {
          fmtValue = numberFormat(fmtValue);
        } else {
          fmtValue = (fmtValue + "").replace(/\./g, ".");
          // WptOptions.get().decimalPoint);
        }
        if (format.symbol) {
          if (format.symbolSuffix) {
            fmtValue = fmtValue + format.symbol;
          } else {
            fmtValue = format.symbol + fmtValue;
          }
        }

        break;

      case WptConstants.VALUE_FORMAT_CATEGORY_PERCENTAGE:
        fmtValue = (fmtValue * 100).toFixed(format.decimal) + "%";

        break;
      default:
    }

    if (value < 0 && format.negative) {
      switch (format.negative) {
        case WptConstants.VALUE_NEGATIVE_MINUS:
          break;

        case WptConstants.VALUE_NEGATIVE_PARENTHESES:
          fmtValue = "(" + fmtValue.replace("-", "") + ")";
          break;

        case WptConstants.VALUE_NEGATIVE_PARENTHESES_MINUS:
          fmtValue = "(" + fmtValue + ")";
          break;

        case WptConstants.VALUE_NEGATIVE_RED:
          fmtValue =
            "<span style='color:red;'>" + fmtValue.replace("-", "") + "</span>";
          break;

        case WptConstants.VALUE_NEGATIVE_RED_MINUS:
          fmtValue = "<span style='color:red;'>" + fmtValue + "</span>";
          break;

        case WptConstants.VALUE_NEGATIVE_RED_PARENTHESES:
          fmtValue =
            "<span style='color:red;'>(" +
            fmtValue.replace("-", "") +
            ")</span>";
          break;

        case WptConstants.VALUE_NEGATIVE_RED_PARENTHESES_MINUS:
          fmtValue = "<span style='color:red;'>(" + fmtValue + ")</span>";
          break;

        default:
      }
    }
  }

  return {
    value,
    fmtValue
  };
};

const generateMemoryLayoutData = (
  pivotData,
  pivot,
  sheetOptions,
  collapsed,
  maxRows,
  maxColumns,
  maxDataCells
) => {
  let rowCount = 0;
  let columnCount = 0;

  const columnHeaders = [];
  const leafColumns = [];
  const rowHeaders = [];

  if (!pivotData) {
    return {
      columnHeaders,
      leafColumns,
      rowHeaders
    };
  }

  const valueObj = pivotData.valueObj;
  const rows = pivot.rows;
  const columns = pivot.columns;
  const values = pivot.values;
  const valuesInAxis = pivot.valuesInAxis;
  const valuesIndex = pivot.valuesIndex;

  const axisLens = [columns.length, rows.length];

  const checkCollapsed = treeItem =>
    collapsed[treeItem.id.replace(/V[0-9]*/, "")];

  const generateMemoryHeader = (
    axis,
    treeItem,
    axisIndex,
    grandParent,
    parent,
    valuesHandled,
    vIndex
  ) => {
    const checkExceedLimit = () => {
      return (
        (axis === 0 && columnCount >= maxColumns) ||
        (axis === 1 &&
          (rowCount >= maxRows || rowCount * columnCount >= maxDataCells))
      );
    };

    if (checkExceedLimit()) return;

    const checkFieldShowSubtotal = () => {
      const fieldId = axis === 0 ? columns[axisIndex - 1] : rows[axisIndex - 1];
      const pivotField = pivot.fields[fieldId];

      return (
        (axisIndex === 0 &&
          ((axis === 0 && sheetOptions.showColTotals) ||
            (axis === 1 && sheetOptions.showRowTotals))) ||
        ((axisIndex > 0 &&
          (((axis === 0 && sheetOptions.showColSubtotals) ||
            (axis === 1 && sheetOptions.showRowSubtotals)) &&
            (!pivotField || !pivotField.hideSubtotal))) ||
          checkCollapsed(treeItem))
      );
    };

    const getLeafLabel = item => {
      const count = item.id.split("-").length - 1;
      if (count === 0) {
        if (item.vIndex < 0) {
          if (values.length === 1 && axisLens[axis] === 0) {
            return values[0].name;
          }
          return i18n.t("Lbl_GrandTotal");
        }
        return values[item.vIndex].name;
      } else {
        if (count === axisLens[axis]) {
          return item.label;
        }
        if (item.vIndex < 0 || count > valuesIndex) {
          return (
            item.label +
            (item.expandable || item.collapsible
              ? ""
              : " - " + i18n.t("Lbl_Total"))
          );
        }
        return item.label + " - " + values[item.vIndex].name;
      }
    };

    const addDataToLeafRows = leafRow => {
      leafRow.data = {};
      if (values.length > 0) {
        for (let i = 0, len = leafColumns.length; i < len; i++) {
          const leafColumn = leafColumns[i];
          leafRow.data[leafColumn.id] = getValueFromItem(
            values,
            valueObj,
            leafRow,
            leafColumn
          );
        }
      }
    };

    if (!checkCollapsed(treeItem)) {
      if (
        valuesInAxis === axis &&
        axisIndex === valuesIndex &&
        !valuesHandled
      ) {
        if (axisIndex < axisLens[axis]) {
          for (let k = 0; k < values.length; k++) {
            const newColumn = {
              id: treeItem.id + "V" + k,
              label: values[k].name,
              vIndex: k,
              collapsible: false,
              span: 0,
              children: []
            };
            parent.children.push(newColumn);
            generateMemoryHeader(
              axis,
              treeItem,
              axisIndex,
              parent,
              newColumn,
              true,
              k
            );
            parent.span += newColumn.span;
          }
        } else {
          generateMemoryHeader(
            axis,
            treeItem,
            axisIndex,
            grandParent,
            parent,
            true,
            vIndex
          );
        }
      } else {
        if (treeItem.children) {
          for (let i = 0, len = treeItem.children.length; i < len; i++) {
            let newItem = treeItem.children[i];

            if (
              (axisIndex < axisLens[axis] - 1 &&
                !checkCollapsed(newItem) &&
                (!valuesHandled || getValueIndex(newItem.id) === vIndex)) ||
              (valuesInAxis === axis &&
                axisIndex < valuesIndex &&
                axisIndex === axisLens[axis] - 1)
            ) {
              const newColumn = {
                id: newItem.id,
                label: newItem.label,
                vIndex,
                collapsible:
                  axisIndex < axisLens[axis] - 1 && !checkCollapsed(newItem),
                span: 0,
                children: []
              };
              parent.children.push(newColumn);
              generateMemoryHeader(
                axis,
                newItem,
                axisIndex + 1,
                parent,
                newColumn,
                valuesHandled,
                vIndex
              );
              parent.span += newColumn.span;
            } else {
              generateMemoryHeader(
                axis,
                newItem,
                axisIndex + 1,
                parent,
                parent,
                valuesHandled,
                vIndex
              );
            }
          }
        }
      }
    }

    if (valuesInAxis === axis && axisIndex <= valuesIndex && !valuesHandled) {
      for (let k = 0; k < values.length; k++) {
        if (axisIndex <= axisLens[axis] - 1) {
          if (checkFieldShowSubtotal()) {
            const newColumn = {
              id: treeItem.id + "V" + k,
              label: treeItem.label,
              vIndex: k,
              expandable: k === 0 && checkCollapsed(treeItem),
              vSpan:
                axisIndex === 0
                  ? axisLens[axis] + 1
                  : axisLens[axis] - axisIndex + 2
            };
            newColumn.label = getLeafLabel(newColumn);
            grandParent.children.push(newColumn);
            grandParent.span++;
            if (axis === 0) {
              leafColumns.push(newColumn);
              columnCount++;
            } else {
              addDataToLeafRows(newColumn);
              rowCount++;
            }
          }
        } else {
          const newColumn = {
            id: treeItem.id + "V" + k,
            label: values[k].name,
            vIndex: k,
            expandable: false,
            vSpan: 1
          };
          newColumn.label = getLeafLabel(newColumn);
          parent.children.push(newColumn);
          parent.span++;
          if (axis === 0) {
            leafColumns.push(newColumn);
            columnCount++;
          } else {
            addDataToLeafRows(newColumn);
            rowCount++;
          }
        }
      }
    } else if (
      (valuesInAxis === axis &&
        axisIndex > valuesIndex &&
        getValueIndex(treeItem.id) === vIndex) ||
      valuesInAxis !== axis
    ) {
      if (axisIndex <= axisLens[axis] - 1) {
        if (checkFieldShowSubtotal()) {
          const newColumn = {
            id: treeItem.id,
            label: treeItem.label,
            vIndex,
            expandable: checkCollapsed(treeItem),
            vSpan:
              axisIndex === 0 ? axisLens[axis] : axisLens[axis] - axisIndex + 1
          };
          newColumn.label = getLeafLabel(newColumn);
          grandParent.children.push(newColumn);
          grandParent.span++;
          if (axis === 0) {
            leafColumns.push(newColumn);
            columnCount++;
          } else {
            addDataToLeafRows(newColumn);
            rowCount++;
          }
        }
      } else {
        const newColumn = {
          id: treeItem.id,
          label: treeItem.label,
          vIndex,
          expandable: false,
          vSpan: 1
        };
        newColumn.label = getLeafLabel(newColumn);
        parent.children.push(newColumn);
        parent.span++;
        if (axis === 0) {
          leafColumns.push(newColumn);
          columnCount++;
        } else {
          addDataToLeafRows(newColumn);
          rowCount++;
        }
      }
    }
  };

  if (axisLens[0] > 0 || values.length > 0) {
    const root = {
      span: 0,
      children: columnHeaders
    };
    generateMemoryHeader(0, pivotData.colTree, 0, root, root, false, -1);
  }

  if (axisLens[1] > 0 || values.length > 0) {
    const root = {
      span: 0,
      children: rowHeaders
    };
    generateMemoryHeader(1, pivotData.rowTree, 0, root, root, false, -1);
  }

  return {
    columnHeaders,
    leafColumns,
    rowHeaders
  };
};

const generateOlapLayoutData = (
  pivotData,
  pivot,
  collapsed,
  maxRows,
  maxColumns,
  maxDataCells
) => {
  let columnCount = 0;
  let rowCount = 0;

  const commonHeaders = [];
  const columnHeaders = [];
  const leafColumns = [];
  const rowHeaders = [];

  if (!pivotData) {
    return {
      commonHeaders,
      columnHeaders,
      leafColumns,
      rowHeaders
    };
  }

  if (pivotData.rowTree && pivotData.rowTree.length > 0) {
    const fields = pivotData.rowTree[0].fields;

    if (fields.length > 0) {
      for (let j = 0; j < fields.length; j++) {
        commonHeaders.push({
          field: "fields",
          label: fields[j].member,
          index: j,
          value: fields
        });
      }
    }
  }

  const addDataToLeafRows = leafRow => {
    leafRow.data = {};
    for (let i = 0, len = leafColumns.length; i < len; i++) {
      const leafColumn = leafColumns[i];
      leafRow.data[leafColumn.id] =
        pivotData.valueObj[leafRow.id + "-" + leafColumn.id];
    }
  };

  const generateOlapHeader = (axis, tree, parent, index) => {
    const checkExceedLimit = () => {
      return (
        (axis === 0 && columnCount >= maxColumns) ||
        (axis === 1 &&
          (rowCount >= maxRows || rowCount * columnCount >= maxDataCells))
      );
    };

    if (checkExceedLimit()) return;

    let newTree;
    let column;
    const lastFields = [];
    for (let i = 0, len = tree.length; i < len && !checkExceedLimit(); i++) {
      const item = tree[i];
      const field = item.fields[index];
      let lastField = lastFields[lastFields.length - 1];
      if (field) {
        if (field.labelFlag) {
          if (
            !lastField ||
            (lastField.level < field.level && lastField.collapsible) ||
            lastField.level >= field.level
          ) {
            if (column && newTree && newTree.length > 0) {
              generateOlapHeader(axis, newTree, column, index + 1);
              parent.span += column.span;
            }

            column = {
              id: field.id,
              label: field.label,
              index: index,
              level: field.level
            };

            if (collapsed[field.id + "-" + index]) {
              column.expandable = field.level < field.levelCount - 1;
            } else {
              column.collapsible = field.level < field.levelCount - 1;
            }

            if (index === item.fields.length - 1) {
              column.span = 1;
              if (axis === 0) {
                leafColumns.push(column);
              } else {
                addDataToLeafRows(column);
              }
              axis === 0 ? columnCount++ : rowCount++;
            } else {
              column.span = 0;
              column.children = [];
            }

            newTree = [item];
            parent.children.push(column);

            if (!lastField || lastField.level < field.level) {
              lastFields.push(column);
            } else if (lastField.level === field.level) {
              lastFields[lastFields.length - 1] = column;
            } else {
              while (lastField && lastField.level >= field.level) {
                lastField = lastFields.pop();
              }
              lastFields.push(column);
            }
          }
        } else {
          if (
            lastField &&
            lastField.level === field.level &&
            lastFields.every(
              lastField1 =>
                (lastField1.level < field.level && lastField1.collapsible) ||
                lastField1.level === field.level
            )
          ) {
            newTree.push(item);
          }
        }
      }
    }

    if (column && newTree && newTree.length > 0) {
      generateOlapHeader(axis, newTree, column, index + 1);
      parent.span += column.span;
    }
  };

  if (pivotData.colTree) {
    generateOlapHeader(
      0,
      pivotData.colTree,
      { span: 0, children: columnHeaders },
      0
    );
  }

  if (pivotData.rowTree) {
    generateOlapHeader(
      1,
      pivotData.rowTree,
      { span: 0, children: rowHeaders },
      0
    );
    if (rowHeaders.length === 0 && pivotData.rowTree.length === 1) {
      const item = pivotData.rowTree[0];
      const row = {
        id: item.id,
        label: item.label,
        index: 0,
        level: 0
      };
      addDataToLeafRows(row);
      rowHeaders.push(row);
    }
  }

  return {
    commonHeaders,
    columnHeaders,
    leafColumns,
    rowHeaders
  };
};



