import { mergeRecursive } from "~/services/CommonUtils";

export default {
  open(state, dialog) {
    state.visible = true;
    state.type = dialog.type;
    state.title = dialog.title;
    state.width = dialog.width || 350;
    state.height = dialog.height || 300;
    state.modal = "modal" in dialog ? dialog.modal : true;
    state.payload = dialog.payload;
    state.data = dialog.data;
  },

  close(state) {
    state.visible = false;
    state.type = null;
    state.title = "";
    state.width = null;
    state.height = null;
    state.modal = true;
    state.payload = {};
    state.data = {};
  },

  toggle(state, visible) {
    state.visible = visible;
  },

  setData(state, data) {
    state.data = data;
  },

  updateData(state, data) {
    state.data = { ...mergeRecursive(state.data, data) };
  }
};



