<template>
  <button 
    :class="buttonClass" 
    @click="onClick">
    <div class="wpt-btn-content">
      <icon 
        v-if="icon" 
        :name="icon"
      />
      <div 
        v-if="label" 
        class="wpt-btn-label"
      >
        {{ label }}
      </div>
    </div>
  </button>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: "standard"
    },
    vertical: {
      type: Boolean,
      default: false
    },
    label: {
      type: String,
      default: null
    },
    icon: {
      type: String,
      default: null
    }
  },
  computed: {
    buttonClass() {
      let cls = "wpt-btn";
      if (this.type === "plain") {
        cls += " wpt-btn-plain";
      }
      if (this.vertical) {
        cls += " wpt-btn-vertical";
      }
      return cls;
    }
  },
  methods: {
    onClick(evt) {
      this.$emit("click", evt);
    }
  }
};
</script>

<style lang="scss" scoped>
.wpt-btn {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: cyan;
  border: 1px solid #dcdfe6;
  color: #606266;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: 0;
  margin: 0;
  transition: 0.1s;
  font-weight: 500;
  padding: 4px 6px;
  border-radius: 4px;

  & .wpt-btn-content {
    display: flex;
    flex-direction: row;

    & .wpt-btn-label {
      padding: 3px;
    }
  }

  &.wpt-btn-plain {
    border-color: transparent;
    background: 0 0;

    & .wpt-btn-content .wpt-btn-label {
      color: blue;
    }
  }

  &.wpt-btn-vertical .wpt-btn-content {
    display: flex;
    flex-direction: column;

    & .wpt-btn-label {
      padding: 3px 0px;
      -webkit-writing-mode: tb;
      -ms-writing-mode: tb;
      writing-mode: tb;
      -webkit-writing-mode: vertical-lr;
      -ms-writing-mode: tb-lr;
      writing-mode: vertical-lr;
    }
  }

  &.wpt-btn-vertical.wpt-btn-plain.wpt-selected {
    background-color: aliceblue;
    margin-left: -1px;
    /*padding-left: 1px;*/
    width: calc(100% + 1px);
    border-color: #888;
    border-radius: 0;
    border-left-width: 0;
  }
}
</style>



