<template>
  <div>
    <div style="text-align: center;">
      <a 
        :href="options.server.aboutUrl" 
        target="_blank">
        <img src="../../ui-widgets/icon/iconfont/logo.png" >
      </a>
    </div>

    <div style="text-align: center;">
      {{ edition }}
    </div>

    <div style="text-align: center;">
      {{ "2011-2018 Brightsea Inc. All Rights Reserved." }}
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    edition() {
      return (
        (this.wptEdition === this.WptConstants.EDITION_FREE ? "Free" : "Pro") +
        " edition"
      );
    },
    options() {
      return this.$store.state.options;
    }
  }
};
</script>

<style></style>



