import axios from "axios";

import WptConstants from "~/WptConstants";
import { getCubeFields } from "~/services/OlapService";
import { readFileUrl } from "~/services/FileService";

// below are internal function used by APIs
const newWptFromDataArray = function(
  attrArray,
  dataArray,
  dataUrl,
  wptObject,
  successCB,
  errorCB,
  _this
) {
  _this.$store.dispatch("wpt/loadDataArray", {
    attrArray,
    dataArray,
    successHandler: response => {
      if (!wptObject) {
        _this.$store.dispatch("wpt/generateNewWptFromSource", {
          newSource: {
            mode: _this.WptConstants.MODE_MEMORY,
            url: dataUrl || "",
            data: dataArray,
            fields: response
          }
        });
        if (successCB) {
          successCB.apply(null, [_this]);
        }
      } else {
        // _this.setWptData(wptObject, response, successCB, errorCB);
      }
    },
    errorHandler: errorCB
  });
};

const setWptData = function(wpt, dataObject, successCB, errorCB, _this) {
  let wptObject = {};

  if (typeof wpt === "string") {
    wptObject = JSON.parse(wpt);
  } else if (wpt !== null && typeof wpt === "object") {
    wptObject = wpt;
  }

  if (wptObject.format === "WPT" && wptObject.version === "3.0") {
    _this.$store.dispatch("wpt/setWpt", wptObject);
    if (successCB) {
      successCB.apply(_this, [wptObject]);
    }
  } else {
    _this.$store.dispatch("system/errorMessage", _this.$t("Msg_NotValidWptFormat"));
    if (errorCB) {
      errorCB.apply(_this, [new Error(_this.$t("Msg_NotValidWptFormat"))]);
    }
  }
};

// start from here, each function represent an API

const setLocale = function(locale) {
  this.$store.dispatch("options/setLocale", locale);
};

const setOptions = function(options) {
  this.$store.dispatch("options/setOptions", options);
};


/**
 * Load source data from data array
 * @param {Array} attrArray - Array of attributes
 *       example: ["department", "area", "cost", ...]
 * @param {Array} dataArray - Array of data rows, each row is also an array of values
 *       example: [["dep1", "area1", 1000, ...], ..., ["dep6", "area2", 2000,...]]
 * @param {String} dataUrl - optional, Url for track the source data
 *       This is just for tracking, pass "" if not know or no need.
 * @param {Object} wptObject - optional, pre-saved wpt JSON object
 * @param {Func} successCB(wpt) - optional, success callback
 *        in this callback, current wpt object will be published as first parameter
 *        and "this" will be WebPivotTable object
 *
 * @param {Func} errorCB(error) - optional, error callback
 *        in this callback, error object will be published as first parameter
 *        and "this" will be WebPivotTable object
 *
 * Note:
 * 1. If wptObject is not null, use it as pre-saved pivot selections
 * 2. This is major API to load source data into WPT component, it was
 *   used by setCsvUrl and setCsvRawData internally.  Actually, developers
 *   can load any kinds of data from any other resources, like SQL databases,
 *   and format them as attrArray and dataArray, then load them into WPT component.
 */
const setDataArray = function(
  attrArray,
  dataArray,
  dataUrl,
  wptObject,
  successCB,
  errorCB
) {
  if (this.wptEdition === WptConstants.EDITION_FREE) {
    alert("Free version doesn't have this feature.");
    return;
  }

  newWptFromDataArray(
    attrArray,
    dataArray,
    dataUrl,
    wptObject,
    successCB,
    errorCB,
    this
  );
};

/**
 * Load source data from CSV file URL
 * @param {string} csvUrl - URL String of CSV file
 * @param {string} separator - optional, separator char for CSV file, default is ","
 * @param {Object} wptObject - optional, pre-saved wpt JSON object
 * @param {Func} successCB(wpt) - optional, success callback
 *        in this callback, current wpt object will be published as first parameter
 *        and "this" will be WebPivotTable object
 *
 * @param {Func} errorCB(error) - optional, error callback
 *        in this callback, error object will be published as first parameter
 *        and "this" will be WebPivotTable object
 *
 * if wptObject is not null, use it as pivot selections
 */
const setCsvUrl = function(csvUrl, separator, wptObject, successCB, errorCB) {
  if (this.wptEdition === WptConstants.EDITION_FREE) {
    alert("Free version doesn't have this feature.");
    return;
  }

  readFileUrl(csvUrl, 'csv', this.$store, this.$t.bind(this), separator);
};

/**
 * Load WPT format String
 * @param {String} wptString - pre-saved WPT format string
 * @param {Object} dataObject - optional, if exist, use this as data and fields of first source
 *        format {data:[], fields:[]}
 * @param {Func} successCB(wpt) - optional, success callback
 *        in this callback, current wpt object will be published as first parameter
 *        and "this" will be WebPivotTable object
 *
 * @param {Func} errorCB(error) - optional, error callback
 *        in this callback, error object will be published as first parameter
 *        and "this" will be WebPivotTable object
 */
const setWptString = function(wptString, dataObject, successCB, errorCB) {
  if (this.wptEdition === WptConstants.EDITION_FREE) {
    alert("Free version doesn't have this feature.");
    return;
  }
  setWptData(wptString, dataObject, successCB, errorCB, this);
};

/**
 * Load WPT format JSON Oject
 * @param {Object} wptObject - pre-saved WPT format JSON object
 * @param {Object} dataObject - optional, if exist, use this as data and fields of first source
 *        format {data:[], fields:[]}
 * @param {Func} successCB(wpt) - optional, success callback
 *        in this callback, current wpt object will be published as first parameter
 *        and "this" will be WebPivotTable object
 *
 * @param {Func} errorCB(error) - optional, error callback
 *        in this callback, error object will be published as first parameter
 *        and "this" will be WebPivotTable object
 */
const setWptObject = function(wptObject, dataObject, successCB, errorCB) {
  if (this.wptEdition === WptConstants.EDITION_FREE) {
    alert("Free version doesn't have this feature.");
    return;
  }
  setWptData(wptObject, dataObject, successCB, errorCB, this);
};

/**
 * Load WPT format String or JSON Oject with updated first source data
 * @param {String/Object} wpt - pre-saved WPT format String or JSON object
 * @param {Object} dataObject - optional, if exist, use this as data and fields of first source
 *        format {data:[], fields:[]}
 * @param {Func} successCB(wpt) - optional, success callback
 *        in this callback, current wpt object will be published as first parameter
 *        and "this" will be WebPivotTable object
 *
 * @param {Func} errorCB(error) - optional, error callback
 *        in this callback, error object will be published as first parameter
 *        and "this" will be WebPivotTable object
 */
const setWpt = function(wpt, dataObject, successCB, errorCB) {
  if (this.wptEdition === WptConstants.EDITION_FREE) {
    alert("Free version doesn't have this feature.");
    return;
  }
  setWptData(wpt, dataObject, successCB, errorCB, this);
};

/**
 * Generate WPT format string
 * @param {boolean} ignoreData
 *        if equal to true, ignore data and fields, only save options and controls
 * @return {string}
 */
const generateWptString = function(ignoreData, successCB) {
  if (this.wptEdition === WptConstants.EDITION_FREE) {
    alert("Free version doesn't have this feature.");
    return;
  }
  this.$store.dispatch("wpt/generateWptForSave", { ignoreData, successCB });
};

/**
 * Generate WPT format JSON object
 * @param {boolean} ignoreData
 *        if equal to true, ignore data and fields, only save options and controls
 * @return {Object}
 */
const generateWptJSON = function(ignoreData, successCB) {
  if (this.wptEdition === WptConstants.EDITION_FREE) {
    alert("Free version doesn't have this feature.");
    return;
  }
  this.$store.dispatch("wpt/generateWptForSave", {
    ignoreData,
    successCB,
    retJSON: true
  });
};

/**
 * Load OLAP Cube
 * @param {Object} olapData - Object of OLAP Cube Information
 *     {
 *         xmlaUrl: "Xmla server url",
 *         dataSourceInfo: "Data source info",
 *         catalog: "Catalog name",
 *         cubeName: "Cube name"
 *     }
 * @param {Func} successCB(wpt) - optional, success callback
 *        in this callback, current wpt object will be published as first parameter
 *        and "this" will be WebPivotTable object
 *
 * @param {Func} errorCB(error) - optional, error callback
 *        in this callback, error object will be published as first parameter
 *        and "this" will be WebPivotTable object
 */
const setOlapCube = function(olapData, successCB, errorCB) {
  if (this.wptEdition === WptConstants.EDITION_FREE) {
    alert("Free version doesn't have this feature.");
    return;
  }

  const serverOptions = this.$store.state.options.server;

  const options = {
    xmlaUrl: olapData.xmlaUrl,
    dataSourceInfo: olapData.dataSourceInfo,
    catalog: olapData.catalog,
    cubeName: olapData.cubeName,
    serverOptions
  };

  this.$store.dispatch("system/loadingMessage", this.$t("Msg_LoadingCubeData"));
  getCubeFields(options, (error, result) => {
    if (error) {
      this.$store.dispatch("system/errorMessage", error);
      if (errorCB) {
        errorCB.apply(null, [this]);
      }
      return;
    }
    this.$store.dispatch("system/clearMessage");
    this.$store.dispatch("wpt/generateNewWptFromSource", {
      newSource: {
        data: olapData,
        mode: WptConstants.MODE_OLAP,
        url: olapData.xmlaUrl
      },
      newDataSource: {
        fields: result
      }
    });
    if (successCB) {
      successCB.apply(null, [this]);
    }
  });
};

/**
 * Load source data from Web Service Data URL
 * @param {string} wsDataUrl - URL String of Web Service Data URL
 * @param {Object} wptObject - optional, pre-saved wpt JSON object
 * @param {Func} successCB(wpt) - optional, success callback
 *        in this callback, current wpt object will be published as first parameter
 *        and "this" will be WebPivotTable object
 *
 * @param {Func} errorCB(error) - optional, error callback
 *        in this callback, error object will be published as first parameter
 *        and "this" will be WebPivotTable object
 *
 *
 * Note:
 * 1. The Web Service should return a JSON object
 *       {
 *           attrArray: [],
 *           dataArray: []
 *       }
 * 2. This is a very useful API to access data in SQL databases or any other data storages
 *    since Web Service access is cross domain and can be provided by any backend technologies.
 * 3. If wptObject is not null, use it as pivot selections
 */
const setWebServiceDataUrl = function(
  wsDataUrl,
  wptObject,
  successCB,
  errorCB
) {
  if (this.wptEdition === WptConstants.EDITION_FREE) {
    alert("Free version doesn't have this feature.");
    return;
  }

  this.$store.dispatch("system/loadingMessage", this.$t("Msg_LoadingWSData"));

  const successHandler = response => {
    const data = response.data;
    if (data.attrArray && data.dataArray) {
      newWptFromDataArray(
        data.attrArray,
        data.dataArray,
        wsDataUrl,
        wptObject,
        successCB,
        errorCB,
        this
      );
    } else {
      this.$store.dispatch("system/errorMessage", this.$t("Msg_WrongWSData"));
      if (errorCB) {
        errorCB.apply(this, [new Error(this.$t("Msg_WrongWSData"))]);
      }
    }
  };

  const errorHandler = error => {
    this.$store.dispatch("system/errorMessage", error);
    if (errorCB) {
      errorCB.apply(this, [error]);
    }
  };

  if (this.$store.state.options.server.fileProxyEnabled) {
    axios
      .post(
        this.$store.state.options.server.fileProxy,
        {},
        {
          timeout: this.$store.state.options.server.fileTimeout,
          responseType: "json",
          headers: { "x-file-url": wsDataUrl }
        }
      )
      .then(successHandler)
      .catch(errorHandler);
  } else {
    axios
      .get(wsDataUrl, {
        timeout: this.$store.state.options.server.fileTimeout,
        responseType: "json"
      })
      .then(successHandler)
      .catch(errorHandler);
  }
};

export default {
  setLocale,
  setOptions,
  setDataArray,
  setCsvUrl,
  setWptString,
  setWptObject,
  setWpt,
  generateWptString,
  generateWptJSON,
  setOlapCube,
  setWebServiceDataUrl
};



