<template>
  <div>
    <div class="wpt-section">
      <div>{{ $t("Lbl_PublicSpreadsheetUrl") }}</div>
      <div>
        <input
          v-model="fileUrl"
          :placeholder="$t('Msg_InputPublicSpreadsheetUrl')"
        >
      </div>
    </div>
    <div class="wpt-section">
      <wpt-button
        :disabled="!fileUrl"
        :label="$t('Btn_Load')"
        @click="loadCsvFileUrl"
      />
    </div>

    <div class="wpt-section">
      <div>{{ $t("Lbl_Samples") }}</div>

      <div 
        v-for="item in fileLinks" 
        :key="item.url">
        <wpt-button
          :label="item.label"
          type="plain"
          @click="readCsvFileUrl(item.url)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Tabletop from "~/services/WptTableTop";

export default {
  props: {
    createSource: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      fileUrl: ""
    };
  },

  computed: {
    options() {
      return this.$store.state.options;
    },

    fileLinks() {
      return this.options.fileLinks.filter(
        item => item.type === this.WptConstants.FORMAT_GSS
      );
    }
  },

  methods: {
    loadCsvFileUrl() {
      this.readCsvFileUrl(this.fileUrl);
    },

    readCsvFileUrl(fileUrl) {
      this.$store.dispatch(
        "system/loadingMessage",
        this.$t("Msg_LoadingGSSData")
      );

      try {
        Tabletop.init({
          key: fileUrl,
          callback: data => {
            const attrArray = [],
              dataArray = [];

            for (let key in data[0]) {
              if (data[0].hasOwnProperty(key)) {
                attrArray.push(key);
              }
            }

            data.forEach(function(item) {
              const dataItem = [];
              attrArray.forEach(function(attr) {
                dataItem.push(item[attr]);
              });
              dataArray.push(dataItem);
            });

            this.$store.dispatch("wpt/loadDataArray", {
              attrArray,
              dataArray,
              successHandler: response => {
                this.$store.commit("dialog/setData", {
                  type: this.WptConstants.FORMAT_GSS,
                  url: fileUrl,
                  content: null,
                  newSource: {
                    name: this.$t("Lbl_Source") + 1,
                    mode: this.WptConstants.MODE_MEMORY,
                    url: fileUrl,
                    data: dataArray,
                    fields: response
                  }
                });
                this.createSource();
              },
              errorHandler: null
            });
          },
          simpleSheet: true
        });
      } catch (error) {
        this.$store.dispatch("system/errorMessage", error);
      }
    }
  }
};
</script>

<style></style>



