import WptConstants from "../WptConstants";

//https://stackoverflow.com/questions/8511281/check-if-a-value-is-an-object-in-javascript
export const isObject = obj => {
  return (
    obj === Object(obj) &&
    Object.prototype.toString.call(obj) !== "[object Array]" &&
    typeof obj !== "function"
  );
};

/*
 * Recursively merge properties of two objects
 * http://stackoverflow.com/questions/171251
 */
export const mergeRecursive = (obj1, obj2) => {
  for (let p in obj2) {
    if (obj2.hasOwnProperty(p)) {
      try {
        // Property in destination object set;
        // update its value.
        if (isObject(obj2[p])) {
          obj1[p] = mergeRecursive(obj1[p], obj2[p]);
        } else {
          obj1[p] = obj2[p];
        }
      } catch (e) {
        // Property in destination object not set;
        // create it and set its value.
        obj1[p] = obj2[p];
      }
    }
  }
  return obj1;
};

// generate a String type id
export function generateId() {
  return new Date().getTime() + Math.round(Math.random() * 1000000000000) + "";
}

export function getFieldTypeIconName(fieldType) {
  switch (fieldType) {
    case WptConstants.FIELD_STRING:
      return "type-string";
    case WptConstants.FIELD_NUMBER:
      return "type-number";
    case WptConstants.FIELD_CALCULATED:
      return "type-calculated";
    case WptConstants.FIELD_VALUES:
      return "type-values";
    case WptConstants.FIELD_DIMENSION:
      return "type-dimension";
    case WptConstants.FIELD_HIERARCHY:
      return "type-hierarchy";
    case WptConstants.FIELD_LEVEL:
      return "type-level";
    case WptConstants.FIELD_MEASURE:
      return "type-measure";
    case WptConstants.FIELD_MEASURES:
      return "type-measures";
  }
  return "";
}

export const getValueIndex = itemId => {
  const arr = itemId.match(/V[0-9]*/);
  if (arr) {
    return parseInt(arr[0].substring(1), 10);
  }
  return -1;
};



