import WptConstants from "~/WptConstants";
import { generateTooltipHtml } from "./SheetLayoutService";
import { getValueIndex } from "./CommonUtils";

export const renderTable = (
  h,
  i18n,
  sheetId,
  sourceMode,
  layoutData,
  gridOptions,
  pivot,
  sourceFields,
  switchTreeItemExpanded,
  drillThrough,
  openValueFieldSettingDialog,
  width,
  height,
  popoverContainer
) => {
  let commonHeaders = [];
  let newColumnHeaders = [];
  let newRowHeaders = [];
  let renderCommonHeader = null;
  let renderColumnHeader = null;
  let renderRowHeader = null;
  let renderRowData = null;

  const renderDataCell = (row, column) => {
    return row.data && row.data[column.id]
      ? h("td", {}, [
          h(
            "div",
            {
              style: Object.assign(
                {
                  cursor: "pointer",
                  width: gridOptions.cellWidth + "px",
                  height: gridOptions.cellHeight + "px"
                },
                gridOptions.cellStyle.dataCell
              ),
              class: "wpt-table-body-cell",
              directives: [
                {
                  name: "tooltip",
                  value: {
                    content: generateTooltipHtml(row, column),
                    container: popoverContainer
                  }
                }
              ],
              on: {
                dblclick: event => {
                  drillThrough(row, column, event);
                }
              },
              domProps: {
                innerHTML: row.data[column.id].fmtValue
              }
            },
            []
          )
        ])
      : h("td", {}, [
          h(
            "div",
            {
              style: Object.assign(
                {
                  cursor: "pointer",
                  width: gridOptions.cellWidth + "px",
                  height: gridOptions.cellHeight + "px"
                },
                gridOptions.cellStyle.noDataCell
              ),
              class: "wpt-table-body-cell"
            },
            ""
          )
        ]);
  };

  switch (sourceMode) {
    case WptConstants.MODE_MEMORY: {
      const generateMemoryColumnHeader = (parent, index) => {
        const headers = [];
        parent.forEach(header => {
          if (header.children) {
            header.children.forEach(child => {
              headers.push(child);
            });
          }
        });
        newColumnHeaders.push(headers);

        if (index < layoutData.columnHeaders.length - 1) {
          generateMemoryColumnHeader(headers, index + 1);
        }
      };

      newColumnHeaders.push(layoutData.columnHeaders);
      generateMemoryColumnHeader(layoutData.columnHeaders, 0);

      const generateCompactRowHeader = (parent, index) => {
        parent.forEach(header => {
          newRowHeaders.push({ ...header, ...{ level: index } });
          if (header.children) {
            generateCompactRowHeader(header.children, index + 1);
          }
        });
      };

      const generateTabularRowHeader = (parent, row) => {
        if (parent.children) {
          parent.children.forEach((child, index) => {
            if (index === 0) {
              row.push(child);
              generateTabularRowHeader(child, row);
            } else {
              const newRow = [child];
              newRowHeaders.push(newRow);
              generateTabularRowHeader(child, newRow);
            }
          });
        }
      };

      if (gridOptions.compactForm) {
        generateCompactRowHeader(layoutData.rowHeaders, 0);
      } else {
        layoutData.rowHeaders.forEach(header => {
          const row = [header];
          newRowHeaders.push(row);
          generateTabularRowHeader(header, row);
        });
      }

      if (
        pivot.rows.length === 0 ||
        (pivot.rows.length === 1 && pivot.values.length < 2) ||
        (pivot.rows.length === 1 && pivot.valuesInAxis === 0)
      ) {
        if (pivot.rows.length === 1) {
          commonHeaders.push({
            label: sourceFields.filter(field => field.id === pivot.rows[0])[0]
              .name,
            vSpan: pivot.columns.length + (pivot.valuesInAxis === 0 ? 1 : 0)
          });
        } else {
          if (pivot.columns.length === 0) {
            commonHeaders.push({
              label: "",
              vSpan: 1
            });
          } else if (pivot.columns.length === 1) {
            if (pivot.values.length < 2) {
              commonHeaders.push({
                label: sourceFields.filter(
                  field => field.id === pivot.columns[0]
                )[0].name,
                vSpan: 1
              });
            } else {
              commonHeaders.push({
                label: "",
                vSpan: 2
              });
            }
          } else {
            commonHeaders.push({
              label: "",
              vSpan: pivot.columns.length + (pivot.valuesInAxis === 0 ? 1 : 0)
            });
          }
        }
      } else {
        if (gridOptions.compactForm) {
          if (pivot.values.length === 0) {
            commonHeaders.push({
              label: "",
              vSpan: pivot.columns.length || 1
            });
          } else if (pivot.values.length === 1) {
            if (pivot.columns.length === 0) {
              commonHeaders.push({
                label: "",
                vSpan: 1
              });
            } else {
              commonHeaders.push({
                label: pivot.values[0].label,
                vSpan: pivot.columns.length || 1
              });
            }
          } else {
            commonHeaders.push({
              label: "",
              vSpan: pivot.columns.length + (pivot.valuesInAxis === 0 ? 1 : 0)
            });
          }
        } else {
          pivot.rows.forEach(row => {
            commonHeaders.push({
              label: sourceFields.filter(field => field.id === row)[0].name,
              vSpan: pivot.columns.length + (pivot.valuesInAxis === 0 ? 1 : 0)
            });
          });

          if (pivot.valuesInAxis === 1) {
            commonHeaders.splice(pivot.valuesIndex, 0, {
              label: i18n("Lbl_Values"),
              vSpan: pivot.columns.length
            });
          }
        }
      }

      renderCommonHeader = () =>
        commonHeaders.map(header =>
          h(
            "th",
            {
              attrs: {
                rowspan: header.vSpan
              }
            },
            [
              h(
                "div",
                {
                  style: Object.assign(
                    {
                      width: gridOptions.rowHeaderWidth + "px",
                      height: gridOptions.cellHeight + "px"
                    },
                    gridOptions.cellStyle.columnHeader
                  ),
                  class: "wpt-table-header-cell"
                },
                header.label
              )
            ]
          )
        );

      const renderMemoryColumnHeader = column => {
        const idArray = column.id.split("-");
        const lastId = idArray[idArray.length - 1];
        const vIndex = lastId ? getValueIndex(lastId) : -1;

        return vIndex > -1
          ? h(
              "span",
              {
                style: {
                  cursor: "pointer"
                },
                on: {
                  click: () => {
                    openValueFieldSettingDialog(vIndex);
                  }
                }
              },
              column.label
            )
          : h("span", {}, column.label);
      };

      renderColumnHeader = headers =>
        headers.map(column =>
          column.children
            ? h(
                "th",
                {
                  attrs: {
                    colspan: column.span
                  }
                },
                [
                  column.collapsible
                    ? h(
                        "div",
                        {
                          style: Object.assign(
                            {
                              cursor: "pointer",
                              width: gridOptions.cellWidth * column.span + "px",
                              height: gridOptions.cellHeight + "px"
                            },
                            gridOptions.cellStyle.compoundColumn
                          ),
                          class: "wpt-table-header-cell",
                          on: {
                            click: () => switchTreeItemExpanded(column.id)
                          }
                        },
                        [
                          h("icon", {
                            attrs: {
                              name: "collapse"
                            }
                          }),
                          renderMemoryColumnHeader(column)
                        ]
                      )
                    : h(
                        "div",
                        {
                          style: Object.assign(
                            {
                              width: gridOptions.cellWidth * column.span + "px",
                              height: gridOptions.cellHeight + "px"
                            },
                            gridOptions.cellStyle.compoundColumn
                          ),
                          class: "wpt-table-header-cell"
                        },
                        [renderMemoryColumnHeader(column)]
                      )
                ]
              )
            : h(
                "th",
                {
                  attrs: {
                    rowspan: column.vSpan
                  }
                },
                [
                  column.expandable
                    ? h(
                        "div",
                        {
                          on: {
                            click: () => switchTreeItemExpanded(column.id)
                          },
                          style: Object.assign(
                            {
                              cursor: "pointer",
                              width: gridOptions.cellWidth + "px",
                              height: gridOptions.cellHeight + "px"
                            },
                            gridOptions.cellStyle.columnHeader
                          ),
                          class: "wpt-table-header-cell"
                        },
                        [
                          h("icon", {
                            attrs: {
                              name: "expand"
                            }
                          }),
                          renderMemoryColumnHeader(column)
                        ]
                      )
                    : h(
                        "div",
                        {
                          style: Object.assign(
                            {
                              cursor: "pointer",
                              width: gridOptions.cellWidth + "px",
                              height: gridOptions.cellHeight + "px"
                            },
                            gridOptions.cellStyle.columnHeader
                          ),
                          class: "wpt-table-header-cell"
                        },
                        [renderMemoryColumnHeader(column)]
                      )
                ]
              )
        );

      renderRowHeader = row =>
        gridOptions.compactForm
          ? h("td", {}, [
              row.collapsible || row.expandable
                ? h(
                    "div",
                    {
                      style: Object.assign(
                        {
                          paddingLeft: row.level * 16 + "px",
                          cursor: "pointer",
                          width: gridOptions.rowHeaderWidth + "px",
                          height: gridOptions.cellHeight + "px"
                        },
                        gridOptions.cellStyle.rowHeader
                      ),
                      class: "wpt-table-body-cell",
                      on: {
                        click: () => switchTreeItemExpanded(row.id)
                      }
                    },
                    [
                      h("icon", {
                        attrs: {
                          name: row.collapsible ? "collapse" : "expand"
                        }
                      }),
                      h("span", {}, row.label)
                    ]
                  )
                : h(
                    "div",
                    {
                      style: Object.assign(
                        {
                          paddingLeft: row.level * 16 + "px",
                          width: gridOptions.rowHeaderWidth + "px",
                          height: gridOptions.cellHeight + "px"
                        },
                        gridOptions.cellStyle.rowHeader
                      ),
                      class: "wpt-table-body-cell"
                    },
                    row.label
                  )
            ])
          : row.map(row1 =>
              h(
                "td",
                {
                  attrs: {
                    colspan: row1.vSpan || 1,
                    rowspan: row1.span || 1
                  }
                },
                [
                  row1.collapsible || row1.expandable
                    ? h(
                        "div",
                        {
                          style: Object.assign(
                            {
                              cursor: "pointer",
                              width:
                                gridOptions.rowHeaderWidth * (row1.vSpan || 1) +
                                "px",
                              height:
                                gridOptions.cellHeight * (row1.span || 1) + "px"
                            },
                            row1.span > 1
                              ? gridOptions.cellStyle.compoundRow
                              : gridOptions.cellStyle.rowHeader
                          ),
                          class: "wpt-table-body-cell",
                          on: {
                            click: () => switchTreeItemExpanded(row1.id)
                          }
                        },
                        [
                          h("icon", {
                            attrs: {
                              name: row1.collapsible ? "collapse" : "expand"
                            }
                          }),
                          h("span", {}, row1.label)
                        ]
                      )
                    : h(
                        "div",
                        {
                          style: Object.assign(
                            {
                              width:
                                gridOptions.rowHeaderWidth * (row1.vSpan || 1) +
                                "px",
                              height:
                                gridOptions.cellHeight * (row1.span || 1) + "px"
                            },
                            row1.span > 1
                              ? gridOptions.cellStyle.compoundRow
                              : gridOptions.cellStyle.rowHeader
                          ),
                          class: "wpt-table-body-cell"
                        },
                        row1.label
                      )
                ]
              )
            );

      renderRowData = row =>
        layoutData.leafColumns.map(column =>
          renderDataCell(
            gridOptions.compactForm ? row : row[row.length - 1],
            column
          )
        );
      break;
    }

    case WptConstants.MODE_OLAP: {
      const generateOlapColumnHeader = (parent, index) => {
        parent.forEach(header => {
          if (!header.children || header.children.length > 0) {
            if (header.children) {
              generateOlapColumnHeader(header.children, index + 1);
            }
            newColumnHeaders[index] = newColumnHeaders[index] || [];
            newColumnHeaders[index].push(header);
          }
        });
      };
      generateOlapColumnHeader(layoutData.columnHeaders, 0);

      const generateOlapRowHeader = (row, parent, index) => {
        parent.forEach((header, i) => {
          if (!header.children || header.children.length > 0) {
            const newRow = i === 0 ? row : [];
            newRow.push(header);
            if (i > 0) {
              newRowHeaders.push(newRow);
            }
            if (header.children) {
              generateOlapRowHeader(newRow, header.children, index + 1);
            }
          }
        });
      };

      newRowHeaders.push([]);
      generateOlapRowHeader(newRowHeaders[0], layoutData.rowHeaders, 0);

      commonHeaders = layoutData.commonHeaders;
      if (commonHeaders.length === 0) {
        commonHeaders.push({
          label: "",
          vSpan: newColumnHeaders.length
        });
      }

      renderCommonHeader = () =>
        commonHeaders.map(header1 =>
          h(
            "th",
            {
              attrs: {
                rowspan: header1.vSpan || 1,
                width: gridOptions.rowHeaderWidth,
                height: gridOptions.cellHeight * header1.vSpan
              },
              style: gridOptions.cellStyle.columnHeader
            },
            [h("div", {}, header1.label)]
          )
        );

      renderColumnHeader = headers =>
        headers.map(column =>
          h(
            "th",
            {
              attrs: {
                colspan: column.span,
                width: gridOptions.cellWidth * column.span,
                height: gridOptions.cellHeight
              },
              style: gridOptions.cellStyle.compoundColumn
            },
            [
              column.collapsible || column.expandable
                ? h(
                    "div",
                    {
                      style: {
                        paddingTop: column.level * 16 + "px",
                        cursor: "pointer"
                      },
                      on: {
                        click: () =>
                          switchTreeItemExpanded(column.id + "-" + column.index)
                      }
                    },
                    [
                      h("icon", {
                        attrs: {
                          name: column.collapsible ? "collapse" : "expand"
                        }
                      }),
                      h("span", {}, column.label)
                    ]
                  )
                : h(
                    "div",
                    {
                      style: {
                        paddingTop: column.level * 16 + "px"
                      }
                    },
                    column.label
                  )
            ]
          )
        );

      renderRowHeader = row =>
        row.map(row1 =>
          h(
            "td",
            {
              attrs: {
                rowspan: row1.span,
                width: gridOptions.rowHeaderWidth,
                height: gridOptions.cellHeight * row1.span
              },
              style:
                row1.span > 1
                  ? gridOptions.cellStyle.compoundRow
                  : gridOptions.cellStyle.rowHeader
            },
            [
              row1.collapsible || row1.expandable
                ? h(
                    "div",
                    {
                      style: {
                        paddingLeft: row1.level * 16 + "px",
                        cursor: "pointer"
                      },
                      on: {
                        click: () =>
                          switchTreeItemExpanded(row1.id + "-" + row1.index)
                      }
                    },
                    [
                      h("icon", {
                        attrs: {
                          name: row1.collapsible ? "collapse" : "expand"
                        }
                      }),
                      h("span", {}, row1.label)
                    ]
                  )
                : h(
                    "div",
                    {
                      style: {
                        paddingLeft: row1.level * 16 + "px"
                      }
                    },
                    row1.label
                  )
            ]
          )
        );

      renderRowData = row =>
        layoutData.leafColumns.map(column =>
          renderDataCell(row[row.length - 1], column)
        );

      break;
    }

    default:
  }

  const leftViewWidth =
    commonHeaders.length === 0
      ? 0
      : commonHeaders.length * gridOptions.rowHeaderWidth;
  const rightViewWidth = width - leftViewWidth;
  const headerHeight = gridOptions.cellHeight;
  const bodyHeight = height - headerHeight;
  const rightBodyTableWidth =
    layoutData.leafColumns.length * gridOptions.cellWidth;

  return gridOptions.noFixedColumns
    ? h(
        "div",
        {
          attrs: {
            id: "pivot-grid-" + sheetId
          },
          style: {
            width:
              gridOptions.rowHeaderWidth * (pivot.rows.length || 1) +
              gridOptions.cellWidth * layoutData.leafColumns.length +
              "px"
          },
          class: "wpt-table-views"
        },
        [
          h("table", {}, [
            h(
              "thead",
              {},
              newColumnHeaders.map((headers, index) =>
                h("tr", {}, [
                  index === 0 ? renderCommonHeader() : null,
                  renderColumnHeader(headers)
                ])
              )
            ),

            h(
              "tbody",
              {},
              newRowHeaders.map(row =>
                h("tr", {}, [renderRowHeader(row), renderRowData(row)])
              )
            )
          ])
        ]
      )
    : h(
        "div",
        {
          attrs: {
            id: "pivot-grid-" + sheetId
          },
          style: {
            width: width + "px",
            height: height + "px"
          },
          class: "wpt-table-views"
        },
        [
          commonHeaders.length === 0
            ? null
            : h(
                "div",
                {
                  style: {
                    width: leftViewWidth + "px",
                    height: height + "px"
                  },
                  class: "wpt-table-leftview"
                },
                [
                  h(
                    "div",
                    {
                      style: {
                        width: leftViewWidth + "px",
                        height: headerHeight + "px"
                      },
                      class: "wpt-table-header"
                    },
                    [
                      h(
                        "table",
                        {
                          attrs: {
                            border: 0,
                            cellspacing: 0,
                            cellpadding: 0
                          }
                        },
                        [h("thead", {}, [h("tr", {}, [renderCommonHeader()])])]
                      )
                    ]
                  ),

                  h(
                    "div",
                    {
                      style: {
                        width: leftViewWidth + "px",
                        height: bodyHeight + "px",
                        paddingBottom: "20px"
                      },
                      class: "wpt-table-body"
                    },
                    [
                      h(
                        "table",
                        {
                          attrs: {
                            border: 0,
                            cellspacing: 0,
                            cellpadding: 0
                          }
                        },
                        [
                          h(
                            "tbody",
                            {},
                            newRowHeaders.map(row =>
                              h("tr", {}, [renderRowHeader(row)])
                            )
                          )
                        ]
                      )
                    ]
                  )
                ]
              ),

          h(
            "div",
            {
              style: {
                width: rightViewWidth + "px",
                height: height + "px"
              },
              class: "wpt-table-rightview"
            },
            [
              h(
                "div",
                {
                  style: {
                    width: rightViewWidth + "px",
                    height: headerHeight + "px"
                  },
                  class: "wpt-table-header"
                },
                [
                  h(
                    "table",
                    {
                      attrs: {
                        border: 0,
                        cellspacing: 0,
                        cellpadding: 0
                      }
                    },
                    [
                      h(
                        "thead",
                        {},
                        newColumnHeaders.map(headers =>
                          h("tr", {}, [renderColumnHeader(headers)])
                        )
                      )
                    ]
                  )
                ]
              ),

              h(
                "div",
                {
                  style: {
                    width: rightViewWidth + "px",
                    height: bodyHeight + "px"
                  },
                  class: "wpt-table-body"
                },
                [
                  newRowHeaders.length === 0
                    ? h(
                        "div",
                        {
                          style: {
                            width: rightBodyTableWidth + "px",
                            height: "1px"
                          }
                        },
                        ""
                      )
                    : h(
                        "table",
                        {
                          attrs: {
                            border: 0,
                            cellspacing: 0,
                            cellpadding: 0
                          }
                        },
                        [
                          h(
                            "tbody",
                            {},
                            newRowHeaders.map(row =>
                              h("tr", {}, [renderRowData(row)])
                            )
                          )
                        ]
                      )
                ]
              )
            ]
          )
        ]
      );
};



