<template>
  <div>
    <div class="wpt-section wpt-flex-row">
      <div style="width: 150px;">
        {{ $t("Lbl_OriginalName") }}
      </div>
      <div style="flex: 1">
        {{ originalField.name }}
      </div>
    </div>
    <div class="wpt-section wpt-flex-row">
      <div style="width: 150px;">
        {{ $t("Lbl_CustomName") }}
      </div>
      <div style="flex: 1">
        <input v-model="valueCustomName" >
      </div>
    </div>
    <div class="wpt-section wpt-flex-row">
      <div style="width: 150px;">
        {{ $t("Lbl_Summarize") }}
      </div>
      <div style="flex: 1">
        <wpt-select
          :value="stats"
          :options="statsOptions"
          style="width:100%;"
          @change="statsChanged"
        />
      </div>
      <wpt-number-input
        v-if="stats.indexOf('PERCENTILE') === 0"
        :min="0"
        :max="100"
        :step="5"
        :value="statsParameter"
        inline
        controls
        size="small"
        @change="statsParameterChanged"
      />
    </div>

    <div class="wpt-section wpt-flex-row">
      <div style="width: 150px;">
        {{ $t("Lbl_ShowValuesAs") }}
      </div>
      <div style="flex: 1">
        <wpt-select
          v-model="showValueAs"
          :options="showAsOptions"
          style="width:100%;"
        />
      </div>
    </div>

    <el-tabs type="border-card">
      <el-tab-pane>
        <span slot="label"> {{ $t("Lbl_NumberFormat") }}</span>

        <div class="wpt-flex-row">
          <wpt-radio
            v-for="item in categoryOptions"
            v-model="category"
            :key="item.value"
            :value="item.value"
            :checked="theme === item.value"
            name="category"
          >
            {{ item.label }}
          </wpt-radio>

          <div
            style="flex: 1; border-left: 1px solid #888; padding-left: 10px;"
          >
            <div v-if="category === WptConstants.VALUE_FORMAT_CATEGORY_GENERAL">
              {{ $t("Lbl_NoSpecificFormat") }}
            </div>
            <div
              v-if="
                category === WptConstants.VALUE_FORMAT_CATEGORY_NUMBER ||
                  category === WptConstants.VALUE_FORMAT_CATEGORY_CURRENCY ||
                  category === WptConstants.VALUE_FORMAT_CATEGORY_PERCENTAGE
              "
            >
              <div class="wpt-section wpt-flex-row">
                <div style="margin-right: 5px;">
                  {{ $t("Lbl_DecimalPlaces") }}
                </div>
                <wpt-number-input
                  v-model="decimal"
                  :min="0"
                  :max="30"
                  :step="1"
                  inline
                  controls
                  size="small"
                />
              </div>
            </div>
            <div
              v-if="
                category === WptConstants.VALUE_FORMAT_CATEGORY_NUMBER ||
                  category === WptConstants.VALUE_FORMAT_CATEGORY_CURRENCY
              "
            >
              <div class="wpt-section wpt-flex-row">
                <wpt-checkbox v-model="separatorFlag">
                  {{ $t("Lbl_UseKSeparator") }}
                </wpt-checkbox>
              </div>
            </div>

            <div
              v-if="category === WptConstants.VALUE_FORMAT_CATEGORY_CURRENCY"
              class="wpt-section wpt-flex-row"
            >
              <div style="margin-right: 5px;">
                {{ $t("Lbl_Symbol") }}
              </div>
              <input 
                v-model="symbol" 
                style="width:50px;" >
              <wpt-select
                v-model="symbolSuffix"
                :options="[
                  {
                    value: 0,
                    label: $t('Lbl_Prefix')
                  },
                  {
                    value: 1,
                    label: $t('Lbl_Suffix')
                  }
                ]"
                style="width:100px;"
              />
            </div>

            <div
              v-if="
                category === WptConstants.VALUE_FORMAT_CATEGORY_NUMBER ||
                  category === WptConstants.VALUE_FORMAT_CATEGORY_CURRENCY ||
                  category === WptConstants.VALUE_FORMAT_CATEGORY_PERCENTAGE
              "
            >
              <div class="wpt-section wpt-flex-row">
                <span style="margin-right: 5px;">
                  {{ $t("Lbl_NegativeNumbers") }}
                </span>
                <wpt-radio
                  v-model="negative"
                  :value="WptConstants.VALUE_NEGATIVE_MINUS"
                  name="negative"
                >
                  {{ "-1234.5678" }}
                </wpt-radio>

                <el-radio-group
                  v-model="negative"
                  class="wpt-flex-column"
                  size="small"
                >
                  <el-radio :label="WptConstants.VALUE_NEGATIVE_MINUS">
                    {{ "-1234.5678" }}
                  </el-radio>
                  <el-radio :label="WptConstants.VALUE_NEGATIVE_PARENTHESES">
                    {{ "(1234.5678)" }}
                  </el-radio>
                  <el-radio
                    :label="WptConstants.VALUE_NEGATIVE_PARENTHESES_MINUS"
                  >
                    {{ "(-1234.5678)" }}
                  </el-radio>
                  <el-radio :label="WptConstants.VALUE_NEGATIVE_RED">
                    <span style="color:red">{{ "1234.5678" }}</span>
                  </el-radio>
                  <el-radio :label="WptConstants.VALUE_NEGATIVE_RED_MINUS">
                    <span style="color:red">{{ "-1234.5678" }}</span>
                  </el-radio>
                  <el-radio
                    :label="WptConstants.VALUE_NEGATIVE_RED_PARENTHESES"
                  >
                    <span style="color:red">{{ "(1234.5678)" }}</span>
                  </el-radio>
                  <el-radio
                    :label="WptConstants.VALUE_NEGATIVE_RED_PARENTHESES_MINUS"
                  >
                    <span style="color:red">{{ "(-1234.5678)" }}</span>
                  </el-radio>
                </el-radio-group>
              </div>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>

    <div class="wpt-flex-row">
      <div
        style="flex: 1"
        class="wpt-message">
        <div v-if="dialog.message">
          {{ dialog.message.text }}
        </div>
      </div>
      <wpt-button
        :label="$t('Btn_OK')"
        @click="handleConfirm" />
    </div>

  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },
  data() {
    const field = this.$store.state.dialog.payload.field;
    const format = field.format;

    return {
      statsOptions: [
        {
          value: this.WptConstants.VALUE_STATS_SUM,
          label: this.$t("Lbl_Stat_Sum")
        },
        {
          value: this.WptConstants.VALUE_STATS_COUNT,
          label: this.$t("Lbl_Stat_Count")
        },
        {
          value: this.WptConstants.VALUE_STATS_DISTINCTCOUNT,
          label: this.$t("Lbl_Stat_DistinctCount")
        },
        {
          value: this.WptConstants.VALUE_STATS_AVERAGE,
          label: this.$t("Lbl_Stat_Average")
        },
        {
          value: this.WptConstants.VALUE_STATS_MAX,
          label: this.$t("Lbl_Stat_Max")
        },
        {
          value: this.WptConstants.VALUE_STATS_MIN,
          label: this.$t("Lbl_Stat_Min")
        },
        {
          value: this.WptConstants.VALUE_STATS_MEDIAN,
          label: this.$t("Lbl_Stat_Median")
        },
        {
          value: this.WptConstants.VALUE_STATS_MODE,
          label: this.$t("Lbl_Stat_Mode")
        },
        {
          value: this.WptConstants.VALUE_STATS_STDDEV,
          label: this.$t("Lbl_Stat_StdDev")
        },
        {
          value: this.WptConstants.VALUE_STATS_VARIANCE,
          label: this.$t("Lbl_Stat_Variance")
        },
        {
          value: this.WptConstants.VALUE_STATS_PERCENTILEINC,
          label: this.$t("Lbl_Stat_PercentileInc")
        },
        {
          value: this.WptConstants.VALUE_STATS_PERCENTILEEXC,
          label: this.$t("Lbl_Stat_PercentileExc")
        }
      ],

      showAsOptions: [
        {
          value: this.WptConstants.SHOW_AS_NO_CALCULATION,
          label: this.$t("Lbl_NoCalculation")
        },
        {
          value: this.WptConstants.SHOW_AS_PERC_GRAND_TOTAL,
          label: this.$t("Lbl_Percent_GrandTotal")
        },
        {
          value: this.WptConstants.SHOW_AS_PERC_COLUMN_TOTAL,
          label: this.$t("Lbl_Percent_ColumnTotal")
        },
        {
          value: this.WptConstants.SHOW_AS_PERC_ROW_TOTAL,
          label: this.$t("Lbl_Percent_RowTotal")
        },
        {
          value: this.WptConstants.SHOW_AS_PERC_PARENT_COLUMN,
          label: this.$t("Lbl_Percent_ParentColumnTotal")
        },
        {
          value: this.WptConstants.SHOW_AS_PERC_PARENT_ROW,
          label: this.$t("Lbl_Percent_ParentRowTotal")
        }
      ],

      categoryOptions: [
        {
          value: this.WptConstants.VALUE_FORMAT_CATEGORY_GENERAL,
          label: this.$t("Lbl_Category_General")
        },
        {
          value: this.WptConstants.VALUE_FORMAT_CATEGORY_NUMBER,
          label: this.$t("Lbl_Category_Number")
        },
        {
          value: this.WptConstants.VALUE_FORMAT_CATEGORY_CURRENCY,
          label: this.$t("Lbl_Category_Currency")
        },
        // {value: this.WptConstants.VALUE_FORMAT_CATEGORY_ACCOUNTING, label: this.$t('Lbl_Category_Accounting')},
        // {value: this.WptConstants.VALUE_FORMAT_CATEGORY_DATE, label: this.$t('Lbl_Category_Date')},
        // {value: this.WptConstants.VALUE_FORMAT_CATEGORY_TIME, label: this.$t('Lbl_Category_Time')},
        {
          value: this.WptConstants.VALUE_FORMAT_CATEGORY_PERCENTAGE,
          label: this.$t("Lbl_Category_Percentage")
        }
        // {value: this.WptConstants.VALUE_FORMAT_CATEGORY_FRACTION, label: this.$t('Lbl_Category_Fraction')},
        // {value: this.WptConstants.VALUE_FORMAT_CATEGORY_SCIENTIFIC, label: this.$t('Lbl_Category_Scientific')},
        // {value: this.WptConstants.VALUE_FORMAT_CATEGORY_TEXT, label: this.$t('Lbl_Category_Text')},
        // {value: this.WptConstants.VALUE_FORMAT_CATEGORY_SPECIAL, label: this.$t('Lbl_Category_Special')},
        // {value: this.WptConstants.VALUE_FORMAT_CATEGORY_CUSTOM, label: this.$t('Lbl_Category_Custom')}
      ],

      valueCustomName: field.name,
      stats: field.stats,
      statsParameter: field.statsParameter || 0,
      showValueAs:
        field.showValueAs || this.WptConstants.SHOW_AS_NO_CALCULATION,
      category:
        format && format.category
          ? format.category
          : this.WptConstants.VALUE_FORMAT_CATEGORY_GENERAL,
      decimal: format && format.decimal ? format.decimal : 0,
      separatorFlag: format && format.separatorFlag,
      symbol: format && format.symbol ? format.symbol : "$",
      symbolSuffix: format && format.symbolSuffix ? 1 : 0,
      negative:
        format && format.negative
          ? format.negative
          : this.WptConstants.VALUE_NEGATIVE_MINUS
    };
  },

  computed: {
    payload() {
      return this.$store.state.dialog.payload;
    },

    sheet() {
      return this.payload.sheet;
    },

    field() {
      return this.payload.field;
    },

    source() {
      return this.$store.state.wpt.source;
    },

    originalField() {
      let arr = this.source.fields.filter(
        field => field.id === this.field.fieldId
      );
      return arr.length > 0
        ? arr[0]
        : this.sheet.pivot.fields[this.field.fieldId];
    }
  },

  methods: {
    statsChanged(value) {
      let name;
      const label = this.statsOptions.filter(
        option => option.value === value
      )[0].label;
      if (value.indexOf("PERCENTILE") === 0) {
        name =
          this.originalField.name +
          "(" +
          this.statsParameter +
          "th " +
          label +
          ")";
      } else {
        name = this.originalField.name + "(" + label + ")";
      }
      this.stats = value;
      this.valueCustomName = name;
    },

    statsParameterChanged(value) {
      const label = this.statsOptions.filter(
        option => option.value === this.stats
      )[0].label;
      const name =
        value + "th " + label + this.$t("Lbl_OF") + this.originalField.name;
      this.statsParameter = value;
      this.valueCustomName = name;
    },

    close() {
      this.$store.commit("dialog/close");
    },

    handleConfirm() {
      let newField = Object.assign({}, this.field);

      newField.name = this.valueCustomName;
      newField.stats = this.stats;
      newField.statsParameter = this.statsParameter;

      if (this.showValueAs) {
        newField.showValueAs = this.showValueAs;
      } else {
        delete newField.showValueAs;
      }

      switch (this.category.toUpperCase()) {
        case this.WptConstants.VALUE_FORMAT_CATEGORY_GENERAL:
          delete newField.format;
          break;

        case this.WptConstants.VALUE_FORMAT_CATEGORY_NUMBER:
        case this.WptConstants.VALUE_FORMAT_CATEGORY_CURRENCY:
        case this.WptConstants.VALUE_FORMAT_CATEGORY_ACCOUNTING:
        case this.WptConstants.VALUE_FORMAT_CATEGORY_PERCENTAGE:
          newField.format = {
            category: this.category,
            decimal: this.decimal,
            separatorFlag: this.separatorFlag,
            symbol: this.symbol,
            symbolSuffix: this.symbolSuffix,
            negative: this.negative
          };
          break;
        default:
      }

      this.$store.dispatch("wpt/changeValueFieldSetting", {
        sheet: this.sheet,
        newValueField: newField,
        source: this.sourcer
      });

      this.close();
    }
  }
};
</script>

<style></style>



