<template>
  <div>
    <div
      v-for="field in fields"
      :key="field.id"
      :draggable="draggableFlag(field)"
      :class="draggableFlag(field) ? 'wpt-draggable' : ''"
      @dragstart="evt => dragstart(evt, field)"
      @dragend="dragend"
    >
      <fields-item
        :sheet="sheet"
        :source="source"
        :field="field"
        :tree-level="treeLevel"
        :draggable-flag="draggableFlag(field)"
      />

      <div 
        v-if="field.children && field.expanded" 
        class="wpt-indent">
        <fields-list
          :sheet="sheet"
          :source="source"
          :fields="field.children"
          :tree-level="treeLevel + 1"
        />
      </div>
    </div>
  </div>
</template>

<script>
import FieldsItem from "./FieldsItem.vue";

export default {
  name: "FieldsList",

  components: {
    FieldsItem
  },

  props: {
    sheet: {
      type: Object,
      required: true
    },
    source: {
      type: Object,
      required: true
    },
    fields: {
      type: Array,
      required: true
    },
    treeLevel: {
      type: Number,
      required: true
    }
  },

  methods: {
    draggableFlag(field) {
      return (
        this.source.mode === this.WptConstants.MODE_MEMORY ||
        (this.source.mode === this.WptConstants.MODE_OLAP &&
          (field.type === this.WptConstants.FIELD_HIERARCHY ||
            field.type === this.WptConstants.FIELD_LEVEL ||
            field.type === this.WptConstants.FIELD_MEASURE))
      );
    },

    dragstart(evt, field) {
      //https://stackoverflow.com/questions/19055264/why-doesnt-html5-drag-and-drop-work-in-firefox
      evt.dataTransfer.setData("text", "anything");
      this.$store.commit("system/setDragData", {
        type: "fields",
        item: field
      });
    },
    dragend() {
      this.$store.commit("system/setDragData", {});
    }
  }
};
</script>

<style></style>



