<template>
  <div
    v-show="dialog.visible"
    :aria-label="dialog.title"
    tabindex="0"
    class="wpt-dialog-overlay is-active"
  >
    <div 
      class="wpt-dialog-background" 
      @click.prevent.stop="handleCancel" />

    <div
      :style="{ width: dialog.width + 'px', height: dialog.height +'px' }"
      class="wpt-dialog-content"
    >
      <div class="wpt-dialog-header">
        <div class="wpt-dialog-title">
          {{ dialog.title }}
        </div>
        <wpt-button 
          icon="close" 
          type="plain" 
          @click="handleCancel" />
      </div>
      <div class="wpt-dialog-body">
        <new-wpt 
          v-if="dialog.type === 'NEW_WPT'"
          :width="dialog.width"
          :height="dialog.height - 30"
        />
        <open-wpt 
          v-if="dialog.type === 'OPEN_WPT'" 
        />
        <save-wpt 
          v-if="dialog.type === 'SAVE_WPT'" 
        />
        <about-me 
          v-if="dialog.type === 'ABOUT_ME'" 
        />
        <setting
          v-if="dialog.type === 'SETTING'"
        />
        <source-field-setting
          v-else-if="dialog.type === 'SOURCE_FIELD_SETTING'"
        />
        <sheet-field-setting
          v-else-if="dialog.type === 'SHEET_FIELD_SETTING'"
          :width="dialog.width"
          :height="dialog.height - 30"
        />
        <sheet-value-field-setting
          v-else-if="dialog.type === 'SHEET_VALUE_FIELD_SETTING'"
          :width="dialog.width"
          :height="dialog.height - 30"
        />
        <sheet-calculated-field
          v-else-if="dialog.type === 'SHEET_CALCULATED_FIELD'"
          :width="dialog.width"
          :height="dialog.height - 30"
        />
        <sheet-mdx-statement
          v-else-if="dialog.type === 'SHEET_MDX_STATEMENT'"
        />
      </div>
    </div>
  </div>
</template>

<script>
import NewWpt from "./dialogs/NewWpt.vue";
import OpenWpt from "./dialogs/OpenWpt.vue";
import SaveWpt from "./dialogs/SaveWpt.vue";
import AboutMe from "./dialogs/AboutMe.vue";
import Setting from "./dialogs/Setting.vue";
import SourceFieldSetting from "./dialogs/SourceFieldSetting.vue";
import SheetFieldSetting from "./dialogs/SheetFieldSetting.vue";
import SheetValueFieldSetting from "./dialogs/SheetValueFieldSetting.vue";
import SheetCalculatedField from "./dialogs/SheetCalculatedField.vue";
import SheetMdxStatement from "./dialogs/SheetMdxStatement.vue";

export default {
  components: {
    NewWpt,
    OpenWpt,
    SaveWpt,
    AboutMe,
    Setting,
    SourceFieldSetting,
    SheetFieldSetting,
    SheetValueFieldSetting,
    SheetCalculatedField,
    SheetMdxStatement
  },

  computed: {
    dialog() {
      return this.$store.state.dialog;
    },
    message() {
      return this.dialog.message;
    },
    visible: {
      get() {
        return this.dialog.visible;
      },
      set(value) {
        this.$store.commit("dialog/toggle", value);
      }
    },
    options() {
      return this.$store.state.options;
    },
    messageStyle() {
      return "color: " + this.options.styles.messageColor;
    }
  },

  methods: {
    handleCancel() {
      this.$store.commit("dialog/close");
    }
  }
};
</script>

<style scoped>
.wpt-dialog-overlay {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  align-items: center;
  display: none;
  justify-content: center;
  overflow: hidden;
  z-index: 100;
}

.wpt-dialog-overlay.is-active {
  display: flex;
}

.wpt-dialog-overlay .wpt-dialog-background {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  background: #fff;
  opacity: 0.5;
}

.wpt-dialog-content {
  margin: auto;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  border: 1px solid #888;
  border-radius: 4px;
  z-index: 101;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.wpt-dialog-header {
  display: flex;
  flex-direction: row;
  background-color: lightblue;
  height: 30px;
  width: 100%;
}

.wpt-dialog-title {
  flex: 1;
  text-align: center;
  font-weight: bold;
  font-size: large;
  padding: 5px;
}

</style>



