<template>
  <memory-source
    v-if="source.mode === WptConstants.MODE_MEMORY"
    :width="width"
    :height="height"
  />
  <olap-source
    v-else-if="source.mode === WptConstants.MODE_OLAP"
    :width="width"
    :height="height"
  />
</template>

<script>
import MemorySource from "./source-main/MemorySource.vue";
import OlapSource from "./source-main/OlapSource.vue";

export default {
  components: {
    MemorySource,
    OlapSource
  },

  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },

  computed: {
    source() {
      return this.$store.state.wpt.source;
    }
  }
};
</script>

<style></style>



