<template>
  <div class="wpt-side-bar">
    <div
      :style="
        'height: ' +
          height +
          'px; width: ' +
          drawer.width +
          'px;border-right: 1px solid #888;background-color: aliceblue;'
      "
    >
      <sheet-pivot
        v-show="navPanel === 'FIELDS'"
        :width="drawer.width"
        :height="height"
      />
      <sheet-options
        v-show="navPanel === 'OPTIONS'"
        :width="drawer.width"
        :height="height"
      />
    </div>
    <sheet-side-buttons
      :change-nav-panel="changeNavPanel"
      :nav-panel="navPanel"
    />
  </div>
</template>

<script>
import SheetPivot from "./sheet-drawer/SheetPivot.vue";
import SheetOptions from "./sheet-drawer/SheetOptions.vue";
import SheetSideButtons from "./sheet-drawer/SheetSideButtons.vue";

export default {
  components: {
    SheetPivot,
    SheetOptions,
    SheetSideButtons
  },

  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },

  data() {
    return {
      navPanel: "FIELDS"
    };
  },

  computed: {
    options() {
      return this.$store.state.options;
    },
    drawer() {
      return this.options.wpt.drawer;
    }
  },

  methods: {
    changeNavPanel(panel) {
      this.navPanel = panel;
    }
  }
};
</script>

<style></style>



