import Vue from "vue";
import VueI18n from "vue-i18n";

// import en from '../build/public/lang/en.json';

// https://stackoverflow.com/questions/28218395/how-do-i-redefine-a-property-from-a-prototype
// force Vue.prototype.$i18n configurable
const _defineProperty = Object.defineProperty;
Object.defineProperty = function(obj, prop, descriptor) {
  if (obj == Vue.prototype && prop == "$i18n") {
    descriptor.configurable = true;
  }
  return _defineProperty(obj, prop, descriptor);
};

Vue.use(VueI18n);

// restore
Object.defineProperty = _defineProperty;

const i18n = new VueI18n({
  locale: "",
  fallbackLocale: "en",
  messages: {
    // en
  }
});

Object.defineProperty(Vue.prototype, "$i18n", {
  writable: true
});
Vue.prototype.$i18n = i18n;

export default i18n;



