<template>
  <div 
    v-if="pivotExisting" 
    class="wpt-flex-row" 
    style="margin: 3px;">
    <wpt-button
      v-tooltip="{content: $t('Lbl_SwapAxis'), container: popoverContainer}"
      icon="swap-axis"
      type="plain"
      @click="swapSheetAxis"
    />

    <wpt-button
      v-tooltip="{content: $t('Lbl_ClearFilter'), container: popoverContainer}"
      icon="clear-filter"
      type="plain"
      @click="clearSheetAllFilters"
    />

    <wpt-button
      v-tooltip="{content: $t('Lbl_ClearAll'), container: popoverContainer}"
      icon="clear-all"
      type="plain"
      @click="clearSheetAllPivot"
    />
  </div>
</template>

<script>
export default {
  props: {
    sheet: {
      type: Object,
      required: true
    },
    source: {
      type: Object,
      required: true
    }
  },

  computed: {
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    pivot() {
      return this.sheet.pivot;
    },

    pivotExisting() {
      return (
        this.pivot.rows.length > 0 ||
        this.pivot.columns.length > 0 ||
        this.pivot.values.length > 0 ||
        this.pivot.filters.length > 0
      );
    }
  },

  methods: {
    swapSheetAxis() {
      this.$store.dispatch("wpt/swapSheetAxis", {
        sheet: this.sheet,
        pivot: this.pivot,
        source: this.source
      });
    },
    clearSheetAllPivot() {
      this.$store.dispatch("wpt/clearSheetAllPivot", {
        sheet: this.sheet,
        pivot: this.pivot,
        source: this.source
      });
    },
    clearSheetAllFilters() {
      this.$store.dispatch("wpt/clearSheetAllFilters", {
        sheet: this.sheet,
        pivot: this.pivot,
        source: this.source
      });
    }
  }
};
</script>

<style></style>



