// 
import utils from "./utils.js";
export default {
  data() {
    return {
      draggingColumn: null, // 
      isDragging: false, // 
      draggingStartX: 0, // 
      draggingEndX: 0, // 
      minColumnWidth: 15 // 
    };
  },

  methods: {
    handleTitleMouseMove(event, column) {
      if (!this.columnWidthDrag) {
        return false;
      }

      let target, rect;

      if (this.isDragging) {
        this.setDragLinePosition(event);
      }

      // 
      if (Array.isArray(column)) {
        if (column.length > 1) {
          return false;
        } else {
          column = column[0];
        }
      }

      target = event.target;

      while (
        target &&
        ((target.className && !target.classList.contains("wpt-table-header-cell")) ||
          !target.className)
      ) {
        target = target.parentNode;
      }

      rect = target.getBoundingClientRect();

      const bodyStyle = document.body.style;

      if (rect.width >= this.minColumnWidth && rect.right - event.pageX < 10) {
        if (!this.isDragging) {
          // 
          this.draggingColumn = this.columns.find(
            x => x.field === column
          );
        }

        bodyStyle.cursor = "col-resize";
      } else {
        if (!this.isDragging) {
          // 

          this.draggingColumn = null;
          bodyStyle.cursor = "";
        }
      }
    },

    handleTitleMouseOut() {
      if (!this.isDragging) {
        document.body.style.cursor = "";
      }
    },

    handleTitleMouseDown(event, column) {
      if (!this.draggingColumn) {
        return false;
      }

      this.isDragging = true;

      this.draggingStartX = event.clientX;

      this.setDragLinePosition(event);

      document.onselectstart = function() {
        return false;
      };
      document.ondragstart = function() {
        return false;
      };

      utils.bind(document, "mousemove", this.handleDragMouseMove);
      utils.bind(document, "mouseup", this.handleDragMouseUp);
    },

    handleDragMouseMove(e) {
      if (!this.isDragging) {
        return false;
      }

      this.setDragLinePosition(e);
    },

    setDragLinePosition(e) {
      const tableLeft = this.$el.getBoundingClientRect().left,
        dragLine = this.$el.querySelector(".wpt-table-drag-line"),
        clientX = e.clientX;
      if (
        this.draggingColumn.width + (clientX - this.draggingStartX) <=
        this.minColumnWidth
      ) {
        return;
      }

      dragLine.style.left = clientX - tableLeft + "px";
    },

    // mouseup
    handleDragMouseUp(e) {
      if (!this.isDragging) {
        return false;
      }

      this.draggingEndX = e.clientX;

      var differ = this.draggingEndX - this.draggingStartX;

      // 1
      if (Math.abs(differ) > 1) {
        let draggingColumn = this.draggingColumn;

        if (draggingColumn.width + differ < this.minColumnWidth) {
          draggingColumn.width = this.minColumnWidth;
        } else {
          draggingColumn.width += differ;
        }
      }

      this.draggingColumn = null;
      document.body.style.cursor = "";
      this.isDragging = false;

      document.onselectstart = function() {
        return true;
      };
      document.ondragstart = function() {
        return true;
      };

      utils.unbind(document, "mousemove", this.handleDragMouseMove);
      utils.unbind(document, "mouseup", this.handleDragMouseUp);
    }
  }
};



