import axios from "axios";

import i18n from "~/i18n.js";

export default {
  async setLocale({ commit, state, dispatch }, locale) {
    if (locale in i18n.messages) {
      i18n.locale = locale;
      commit("setLocale", locale);
    } else {
      let message;
      if ("en" in i18n.messages) {
        message = i18n.getLocaleMessage("en");
      } else {
        try {
          const res = await axios.get(state.localeFilePath + `en.json`);
          message = res.data;
          i18n.locale = "en";
          i18n.setLocaleMessage("en", message);
          commit("setLocale", "en");
        } catch (error) {
          dispatch("system/errorMessage", error, {root: true});
        }
      }

      if (i18n.locale !== locale) {
        try {
          const res = await axios.get(state.localeFilePath + `${locale}.json`);
          message = Object.assign(message, res.data);
          i18n.locale = locale;
          i18n.setLocaleMessage(locale, message);
          commit("setLocale", locale);
        } catch (error) {
          dispatch("system/errorMessage", error, {root: true});
        }
      }
    }
  },

  setOptions({ commit, dispatch }, options) {
    if ("locale" in options) {
      const locale = options.locale;
      delete options.locale;
      commit("setOptions", options);
      dispatch("setLocale", locale);
    } else {
      commit("setOptions", options);
      if ("localeFilePath" in options) {
        dispatch("setLocale", "en");
      }
    }
  }
};



