<template>
  <div class="wpt-menu">
    <v-popover
      :container="popoverContainer"
    >
      <wpt-button
        icon="menu"
        type="plain"/>
      <template slot="popover">
        <div
          v-close-popover="true"
          class="wpt-menu-item"
          @click="toggleDrawer"
        >
          <icon :name="drawer.show ? 'push-right' : 'push-left'"/>
          <span style="margin-left:3px">{{ drawer.show ? $t("Msg_HideNavigationPanel") : $t("Msg_ShowNavigationPanel") }}</span>
        </div>
        <div
          v-close-popover="true"
          v-if="uiFlags.fullScreen"
          class="wpt-menu-item"
          @click="toggleFullScreen"
        >
          <icon :name="fullscreen ? 'fullscreen-exit' : 'fullscreen'"/>
          <span style="margin-left:3px">{{ $t("Msg_FullScreen") }}</span>
        </div>
        <br>
        <div
          v-close-popover="true"
          v-if="uiFlags.newWpt"
          class="wpt-menu-item"
          @click="newWpt"
        >
          <icon name="new"/>
          <span style="margin-left:3px">{{ $t("Msg_NewWpt") }}</span>
        </div>
        <div
          v-close-popover="true"
          v-if="uiFlags.openWpt"
          class="wpt-menu-item"
          @click="openWpt"
        >
          <icon name="open"/>
          <span style="margin-left:3px">{{ $t("Msg_OpenWpt") }}</span>
        </div>
        <div
          v-close-popover="true"
          v-if="uiFlags.saveWpt"
          class="wpt-menu-item"
          @click="saveWpt"
        >
          <icon name="save"/>
          <span style="margin-left:3px">{{ $t("Msg_SaveWpt") }}</span>
        </div>
        <div
          v-close-popover="true"
          v-if="uiFlags.setting"
          class="wpt-menu-item"
          @click="openSetting"
        >
          <icon name="setting"/>
          <span style="margin-left:3px">{{ $t("Lbl_Setting") }}</span>
        </div>
        <div
          v-close-popover="true"
          v-if="uiFlags.help"
          class="wpt-menu-item"
          @click="openHelp"
        >
          <icon name="help"/>
          <span style="margin-left:3px">{{ $t("Lbl_Help") }}</span>
        </div>
        <div
          v-close-popover="true"
          v-if="uiFlags.about"
          class="wpt-menu-item"
          @click="openAbout"
        >
          <icon name="about"/>
          <span style="margin-left:3px">{{ $t("Lbl_About") }}</span>
        </div>
      </template>
    </v-popover>
  </div>
</template>

<script>

export default {
  props: {
    toggleFullScreen: {
      type: Function,
      required: true
    },
    fullscreen: {
      type: Boolean,
      required: true
    }
  },

  computed: {
    proEdition() {
      return this.wptEdition !== this.WptConstants.EDITION_FREE;
    },
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    modal() {
      return this.$store.state.modal;
    },
    system() {
      return this.$store.state.system;
    },
    options() {
      return this.$store.state.options;
    },
    drawer() {
      return this.options.wpt.drawer;
    },
    uiFlags() {
      return this.options.uiFlags;
    }
  },
  methods: {
    newWpt() {
      if (this.wptEdition !== this.WptConstants.EDITION_FREE) {
        this.$eventBus.$emit("newWpt", "new event");
        if (this.options.customHandlers && this.options.customHandlers.newWpt) {
          return;
        }
      }
      this.$store.commit("dialog/open", {
        type: "NEW_WPT",
        title: this.$t("Msg_NewWptWithSource"),
        width: Math.min(this.system.width - 20, 600),
        height: this.system.height - 20,
        payload: {
          newWptFlag: true
        }
      });
    },

    openWpt() {
      if (this.wptEdition !== this.WptConstants.EDITION_FREE) {
        this.$eventBus.$emit("openWpt", "open event");
        if (
          this.options.customHandlers &&
          this.options.customHandlers.openWpt
        ) {
          return;
        }
      }
      this.$store.commit("dialog/open", {
        type: "OPEN_WPT",
        title: this.$t("Msg_OpenWpt")
      });
    },
    saveWpt() {
      if (this.wptEdition !== this.WptConstants.EDITION_FREE) {
        this.$eventBus.$emit("saveWpt", "save event");
        if (
          this.options.customHandlers &&
          this.options.customHandlers.saveWpt
        ) {
          return;
        }
      }
      this.$store.commit("dialog/open", {
        type: "SAVE_WPT",
        title: this.$t("Msg_SaveWpt")
      });
    },
    openHelp() {
      window.open(this.options.server.helpUrl, "_blank");
    },
    openAbout() {
      this.$store.commit("dialog/open", {
        type: "ABOUT_ME",
        title: this.$t("Lbl_About"),
        modal: false
      });
    },
    openSetting() {
      this.$store.commit("dialog/open", {
        type: "SETTING",
        title: this.$t("Lbl_Setting"),
        modal: false
      });
    },
    toggleDrawer() {
      this.$store.commit("options/setOptions", {
        wpt: {
          drawer: {
            show: this.drawer.show ? 0 : 1
          }
        }
      })
    }
  }
};
</script>

<style>
</style>



