<template>
  <label>
    <div class="wpt-btn">
      <div class="wpt-btn-label">{{ label }}</div>
    </div>
    <input 
      type="file" 
      style="display:none" 
      @change="onFileChange" >
  </label>
</template>

<script>
export default {
  props: {
    onFileChange: {
      type: Function,
      required: true
    },
    label: {
      type: String,
      required: true
    }
  }
};
</script>

<style scoped>
.wpt-btn {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: cyan;
  border: 1px solid #dcdfe6;
  color: #606266;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: 0;
  margin: 0;
  transition: 0.1s;
  font-weight: 500;
  padding: 4px 6px;
  border-radius: 4px;
}

.wpt-btn-label {
  padding: 3px;
}
</style>



