export default {
  setWidth(state, width) {
    state.width = width;
  },
  setHeight(state, height) {
    state.height = height;
  },
  setScreenSize(state, size) {
    state.screenSize = size;
  },

  setMessage(state, message) {
    state.message = message;
  },

  setDragData(state, dragData) {
    state.dragData = dragData;
  },

  setContainerClass(state, containerClass) {
    state.containerClass = containerClass;
  }
};



