import X from "./Xmla";

import WptConstants from "../WptConstants";

const xmlaCall = function(options, callback) {
  const x = new X();

  const xmlaRequest = Object.assign(options, {
    success: function(xmla, xmlaRequest, xmlaResponse) {
      //It worked, call the content handler to write the data to the response

      try {
        switch (options.method) {
          case X.METHOD_EXECUTE:
            if (options.isDrillThrough) {
              callback.call(null, null, {
                data: xmlaResponse.fetchAllAsObject(),
                fields: []
              });
            } else {
              callback.call(null, null, xmlaResponse.fetchAsObject());
            }
            break;
          case X.METHOD_DISCOVER:
            callback.call(null, null, xmlaResponse.fetchAllAsObject());
            break;
          default:
            callback.call(
              null,
              new Error("Invalid method " + xmlaRequest.method)
            );
        }
      } catch (error) {
        // console.log("fetchAsObject error:", error);
        callback.call(null, error);
      }
    },
    error: function(xmla, xmlaRequest, exception) {
      //It failed, lets write the error to the response.
      callback.call(null, exception);
    }
  });

  x.request(xmlaRequest);
};

export const discoverDatasource = function(
  { xmlaUrl, serverOptions },
  callback
) {
  xmlaCall(
    {
      xmlaUrl,
      serverOptions,
      method: X.METHOD_DISCOVER,
      requestType: X.DISCOVER_DATASOURCES
    },
    callback
  );
};

export const discoverCatalog = function(
  { xmlaUrl, serverOptions, dataSourceInfo },
  callback
) {
  xmlaCall(
    {
      xmlaUrl,
      serverOptions,
      method: X.METHOD_DISCOVER,
      requestType: X.DBSCHEMA_CATALOGS,
      properties: {
        DataSourceInfo: dataSourceInfo
      }
    },
    callback
  );
};

export const discoverCube = function(
  { xmlaUrl, serverOptions, dataSourceInfo, catalog },
  callback
) {
  xmlaCall(
    {
      xmlaUrl,
      serverOptions,
      method: X.METHOD_DISCOVER,
      requestType: X.MDSCHEMA_CUBES,
      properties: {
        DataSourceInfo: dataSourceInfo,
        Catalog: catalog
      }
    },
    callback
  );
};

export const getCubeFields = function(
  { xmlaUrl, serverOptions, dataSourceInfo, catalog, cubeName },
  callback
) {
  const fields = {};

  xmlaCall(
    {
      xmlaUrl,
      serverOptions,
      method: X.METHOD_DISCOVER,
      requestType: X.MDSCHEMA_DIMENSIONS,
      properties: {
        DataSourceInfo: dataSourceInfo,
        Catalog: catalog
      },
      restrictions: {
        CUBE_NAME: cubeName
      }
    },
    function(error, response) {
      if (error) {
        callback.call(null, error);
      } else if (!response || !response.length) {
        callback.call(null, new Error("No dimensions in cube: " + cubeName));
      } else {
        response.forEach(function(dimension) {
          fields["D-" + dimension.DIMENSION_UNIQUE_NAME] = {
            id: "D-" + dimension.DIMENSION_UNIQUE_NAME,
            name: dimension.DIMENSION_CAPTION || dimension.DIMENSION_NAME,
            type:
              dimension.DIMENSION_TYPE !== 2
                ? WptConstants.FIELD_DIMENSION
                : WptConstants.FIELD_MEASURES
            // xmlaData: dimension
          };

          if (dimension.DIMENSION_TYPE === 2) {
            // fixme  should set defaultMember, but defaultMember is inside Hirarchy [Measures]
          }
        });

        xmlaCall(
          {
            xmlaUrl,
            serverOptions,
            method: X.METHOD_DISCOVER,
            requestType: X.MDSCHEMA_HIERARCHIES,
            properties: {
              DataSourceInfo: dataSourceInfo,
              Catalog: catalog
            },
            restrictions: {
              CUBE_NAME: cubeName
            }
          },
          function(error, response) {
            if (error) {
              callback.call(null, error);
              return;
            } else {
              if (response) {
                response.forEach(function(hierarchy) {
                  if (hierarchy.DIMENSION_TYPE !== 2) {
                    // filter out MEASURES
                    fields["H-" + hierarchy.HIERARCHY_UNIQUE_NAME] = {
                      id: "H-" + hierarchy.HIERARCHY_UNIQUE_NAME,
                      name:
                        hierarchy.HIERARCHY_CAPTION || hierarchy.HIERARCHY_NAME,
                      type: WptConstants.FIELD_HIERARCHY,
                      parentId: "D-" + hierarchy.DIMENSION_UNIQUE_NAME,
                      allMember: hierarchy.ALL_MEMBER
                      // xmlaData: hierarchy
                    };
                  }
                });
              }
            }

            xmlaCall(
              {
                xmlaUrl,
                serverOptions,
                method: X.METHOD_DISCOVER,
                requestType: X.MDSCHEMA_MEASURES,
                properties: {
                  DataSourceInfo: dataSourceInfo,
                  Catalog: catalog
                },
                restrictions: {
                  CUBE_NAME: cubeName
                }
              },
              function(error, response) {
                if (error) {
                  callback.call(null, error);
                  return;
                } else {
                  if (response) {
                    response.forEach(function(measure) {
                      fields["M-" + measure.MEASURE_UNIQUE_NAME] = {
                        id: "M-" + measure.MEASURE_UNIQUE_NAME,
                        name: measure.MEASURE_CAPTION || measure.MEASURE_NAME,
                        type: WptConstants.FIELD_MEASURE
                        // xmlaData: measure
                      };
                    });
                  }
                }

                xmlaCall(
                  {
                    xmlaUrl,
                    serverOptions,
                    method: X.METHOD_DISCOVER,
                    requestType: X.MDSCHEMA_LEVELS,
                    properties: {
                      DataSourceInfo: dataSourceInfo,
                      Catalog: catalog
                    },
                    restrictions: {
                      CUBE_NAME: cubeName
                    }
                  },
                  function(error, response) {
                    if (error) {
                      callback.call(null, error);
                      return;
                    } else {
                      if (response) {
                        response.forEach(function(level) {
                          fields["L-" + level.LEVEL_UNIQUE_NAME] = {
                            id: "L-" + level.LEVEL_UNIQUE_NAME,
                            name: level.LEVEL_CAPTION || level.LEVEL_NAME,
                            type: WptConstants.FIELD_LEVEL,
                            parentId: "H-" + level.HIERARCHY_UNIQUE_NAME,
                            levelType: level.LEVEL_TYPE,
                            levelNumber: level.LEVEL_NUMBER,
                            xmlaData: level
                          };
                        });
                      }
                    }

                    callback.call(null, null, fields);
                  }
                );
              }
            );
          }
        );
      }
    }
  );
};

export const executeMdx = (serverOptions, mdx, source, callback) => {
  const isDrillThrough = mdx.toLowerCase().indexOf("drillthrough") >= 0;
  const properties = {
    DataSourceInfo: source.data.dataSourceInfo,
    Catalog: source.data.catalog
  };
  properties.Format = isDrillThrough
    ? X.PROP_FORMAT_TABULAR
    : X.PROP_FORMAT_MULTIDIMENSIONAL;

  if (!isDrillThrough) {
    properties.AxisFormat = X.PROP_AXISFORMAT_TUPLE;
  }

  xmlaCall(
    {
      xmlaUrl: source.data.xmlaUrl,
      serverOptions,
      method: X.METHOD_EXECUTE,
      statement: mdx,
      properties,
      isDrillThrough
    },
    (error, response) => {
      if (error) {
        callback.call(null, error);
        return;
      }
      callback.call(null, null, response);
    }
  );
};

export const generateOlapPivotData = response => {
  const generateAxisData = axis => {
    const lastLabel = {};
    const axisData = [];
    const axisMetaData = [];

    for (let i = 0, len = axis.positions.length; i < len; i++) {
      const tuple = axis.positions[i];

      for (let member in tuple) {
        const lnum = tuple[member].LNum;
        if (i === 0) {
          axisMetaData.push({
            name: member,
            levels: [lnum]
          });
        } else {
          const metaItem = axisMetaData.filter(
            field => field.name === member
          )[0];
          if (metaItem.levels.indexOf(lnum) < 0) {
            metaItem.levels.push(lnum);
          }
        }
      }
    }

    for (let i = 0, len = axis.positions.length; i < len; i++) {
      const tuple = axis.positions[i];

      const item = {
        id: i,
        fields: []
      };

      let itemLabel = "";
      let parentChanged = false;

      for (let j = 0, len1 = axisMetaData.length; j < len1; j++) {
        const metaItem = axisMetaData[j];
        const member = metaItem.name;
        const lnum = tuple[member].LNum;
        const label = tuple[member].Caption;
        itemLabel += (itemLabel === "" ? "" : " - ") + label;
        const level = metaItem.levels.indexOf(lnum);
        item.fields.push({
          id: i,
          member: member,
          name: tuple[member].UName,
          label: label,
          labelFlag: i === 0 || label !== lastLabel[member],
          level: level,
          levelNum: lnum,
          levelCount: metaItem.levels.length
        });
        if (!parentChanged) {
          lastLabel[member] = label;
        } else {
          delete lastLabel[member];
        }
        if (j === 0 || parentChanged || label !== lastLabel[member]) {
          lastLabel[member] = label;
          if (j > 0 && !parentChanged) {
            parentChanged = true;
          }
        } else {
          delete lastLabel[member];
        }
      }
      item.label = itemLabel;
      axisData.push(item);
    }
    return axisData;
  };

  let rowTree = [];
  const colTree = generateAxisData(response.axes[0]);

  if (response.axes[1]) {
    rowTree = generateAxisData(response.axes[1]);
  } else {
    rowTree.push({
      id: 0,
      fields: [],
      label: ""
    });
  }

  const valueObj = {};

  const len = colTree.length;
  const len1 = rowTree.length;

  for (let i = 0; i < len1; i++) {
    for (let j = 0; j < len; j++) {
      valueObj[i + "-" + j] = {
        value: null,
        fmtValue: "",
        type: WptConstants.CELL_DATA
      };
    }
  }

  response.cells.forEach(cell => {
    const cloc = cell.ordinal % len;
    const rloc = Math.floor(cell.ordinal / len);
    valueObj[rloc + "-" + cloc] = {
      value: cell.Value,
      fmtValue: cell.FmtValue,
      type: WptConstants.CELL_DATA
    };
  });

  return {
    rowTree,
    colTree,
    valueObj
  };
};

export const getAttributeMembers = function(
  {
    xmlaUrl,
    serverOptions,
    dataSourceInfo,
    catalog,
    cubeName,
    dimensionName,
    hierarchyName,
    levelName
  },
  callback
) {
  xmlaCall(
    {
      xmlaUrl,
      serverOptions,
      method: X.METHOD_DISCOVER,
      requestType: X.MDSCHEMA_MEMBERS,
      properties: {
        DataSourceInfo: dataSourceInfo,
        Catalog: catalog
      },
      restrictions: {
        CATALOG_NAME: catalog,
        CUBE_NAME: cubeName,
        DIMENSION_UNIQUE_NAME: dimensionName,
        HIERARCHY_UNIQUE_NAME: hierarchyName,
        LEVEL_UNIQUE_NAME: levelName
      }
    },
    function(error, response) {
      if (error) {
        callback.call(null, error);
      } else {
        const lookups = {};
        response.forEach(member => {
          lookups[member.MEMBER_UNIQUE_NAME] =
            member.MEMBER_CAPTION || member.MEMBER_NAME;
        });
        callback.call(null, null, lookups);
      }
    }
  );
};



