<template>
  <div>
    <fields-list-container
      :sheet="sheet"
      :source="source"
      :height="halfHeight"
    />
    <div class="wpt-toolbar">
      <span style="flex:1; margin: 5px;">
        {{ $t("Lbl_DragFields") }}
      </span>
      <swap-and-clear 
        :sheet="sheet" 
        :source="source" />
    </div>
    <div :style="'height: ' + halfHeight + 'px; margin: 0 5px;'">
      <div style="height: 50%; width: 140px; float:left;">
        <div>
          <icon 
            name="filters" 
            color="black" />
          <span>
            {{ $t("Lbl_Filters") }}
          </span>
        </div>
        <control-list-container
          :sheet="sheet"
          :source="source"
          :height="listHeight"
          control-type="filters"
        />
      </div>
      <div style="height: 50%; width: 140px; float:right;">
        <div>
          <icon 
            name="columns" 
            color="black" />
          <span>
            {{ $t("Lbl_Columns") }}
          </span>
        </div>
        <control-list-container
          :sheet="sheet"
          :source="source"
          :height="listHeight"
          control-type="columns"
        />
      </div>
      <div style="height: 50%; width: 140px; float:left;">
        <div>
          <icon 
            name="rows" 
            color="black" />
          <span>
            {{ $t("Lbl_Rows") }}
          </span>
        </div>
        <control-list-container
          :sheet="sheet"
          :source="source"
          :height="listHeight"
          control-type="rows"
        />
      </div>
      <div style="height: 50%; width: 140px; float:right;">
        <div>
          <icon 
            name="values" 
            color="black" />
          <span>
            {{ $t("Lbl_Values") }}
          </span>
        </div>
        <control-list-container
          :sheet="sheet"
          :source="source"
          :height="listHeight"
          control-type="values"
        />
      </div>
    </div>
    <div class="wpt-toolbar">
      <wpt-checkbox 
        v-model="deferLayoutUpdate" 
        style="margin: 5px; flex: 1;">
        {{ $t("Lbl_DeferUpdate") }}
      </wpt-checkbox>
      <mdx-and-refresh 
        :sheet="sheet" 
        :source="source" />
    </div>
  </div>
</template>

<script>
import FieldsListContainer from "./sheet-pivot/FieldsListContainer.vue";
import ControlListContainer from "./sheet-pivot/ControlListContainer.vue";
import SwapAndClear from "./sheet-pivot/SwapAndClear.vue";
import MdxAndRefresh from "./sheet-pivot/MdxAndRefresh.vue";

export default {
  components: {
    FieldsListContainer,
    ControlListContainer,
    SwapAndClear,
    MdxAndRefresh
  },

  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },

  computed: {
    source() {
      return this.$store.state.wpt.source;
    },
    sheet() {
      return this.$store.state.wpt.sheet;
    },
    options() {
      return this.$store.state.options;
    },
    halfHeight() {
      return Math.floor((this.height - 2 * 30) / 2);
    },

    listHeight() {
      return Math.floor(this.halfHeight / 2) - 20;
    },

    deferLayoutUpdate: {
      get() {
        return this.options.wpt.sheet.deferLayoutUpdate;
      },
      set(value) {
        this.$store.commit("options/changeSheetOptions", { deferLayoutUpdate: value });
      }
    }
  }
};
</script>

<style></style>



