<template>
  <div class="wpt-side-bar">
    <div
      :style="
        'height: ' +
          height +
          'px; width: ' +
          drawer.width +
          'px;border-right: 1px solid #888;background-color: aliceblue;'
      "
    >
      <drill-pivot
        v-show="navPanel === 'FIELDS'"
        :width="drawer.width"
        :height="height - 30"
      />
    </div>
    <drill-side-buttons
      :change-nav-panel="changeNavPanel"
      :nav-panel="navPanel"
    />
  </div>
</template>

<script>
import DrillPivot from "./drill-drawer/DrillPivot.vue";
import DrillSideButtons from "./drill-drawer/DrillSideButtons.vue";

export default {
  components: {
    DrillPivot,
    DrillSideButtons
  },

  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },

  data() {
    return {
      navPanel: "FIELDS",
    };
  },

  computed: {
    options() {
      return this.$store.state.options;
    },
    drawer() {
      return this.options.wpt.drawer;
    }
  },

  methods: {
    changeNavPanel(panel) {
      this.navPanel = panel;
    }
  }
};
</script>

<style></style>



