<template>
  <div>
    <div 
      v-for="field in fields" 
      :key="field.id">
      <fields-item 
        :source="source" 
        :field="field" 
        :tree-level="treeLevel" />

      <div 
        v-if="field.children && field.expanded" 
        class="wpt-indent">
        <fields-list
          :source="source"
          :fields="field.children"
          :tree-level="treeLevel + 1"
        />
      </div>
    </div>
  </div>
</template>

<script>
import FieldsItem from "./FieldsItem.vue";

export default {
  name: "FieldsList",

  components: {
    FieldsItem
  },

  props: {
    source: {
      type: Object,
      required: true
    },
    fields: {
      type: Array,
      required: true
    },
    treeLevel: {
      type: Number,
      required: true
    }
  },

  methods: {}
};
</script>

<style></style>



