import WptConstants from "../WptConstants";

export function getFieldCheckboxChecked(pivot, sourceMode, field) {
  const rows = pivot.rows;
  const columns = pivot.columns;
  const filters = pivot.filters;
  const values = pivot.values;

  switch (sourceMode) {
    case WptConstants.MODE_MEMORY:
      return (
        []
          .concat(
            rows,
            columns,
            filters,
            values.map(valueItem => valueItem.fieldId)
          )
          .indexOf(field.id) >= 0
      );

    case WptConstants.MODE_OLAP: {
      let arr = [];
      switch (field.type) {
        case WptConstants.FIELD_DIMENSION:
          arr = [].concat(rows, columns, filters).map(item => item.id);
          return field.children.some(item => arr.indexOf(item.id) >= 0);
        case WptConstants.FIELD_HIERARCHY:
          arr = [].concat(rows, columns, filters).map(item => item.id);
          return arr.indexOf(field.id) >= 0;
        case WptConstants.FIELD_LEVEL:
          [].concat(rows, columns, filters).forEach(item => {
            arr = arr.concat(item.children.map(level => level.id));
          });
          return arr.indexOf(field.id) >= 0;
        case WptConstants.FIELD_MEASURE:
          arr = values.map(item => item.id);
          return arr.indexOf(field.id) >= 0;
        case WptConstants.FIELD_MEASURES:
          return values.length > 0;
        default:
      }
      return false;
    }

    default:
  }
  return false;
}

export function isFieldFiltered(pivot, sourceMode, field) {
  const pivotField = pivot.fields[field.id];

  switch (sourceMode) {
    case WptConstants.MODE_MEMORY:
      return (
        pivotField &&
        pivotField.filter &&
        (pivotField.filter.filterType ||
          !pivotField.filter.excludes ||
          pivotField.filter.excludes.length > 0)
      );

    case WptConstants.MODE_OLAP:
      if (field.type && field.type === WptConstants.FIELD_LEVEL) {
        if (pivotField && pivotField.filter) {
          const filter = pivotField.filter;
          if (filter.filterType === WptConstants.FILTER_BY_LABEL) {
            return (
              (filter.filterLabelType === WptConstants.FILTER_LABEL_INCLUDES &&
                filter.filterLabelIncludes &&
                filter.filterLabelIncludes.length > 0) ||
              (filter.filterLabelType === WptConstants.FILTER_LABEL_EXCLUDES &&
                filter.filterLabelExcludes &&
                filter.filterLabelExcludes.length > 0)
            );
          }
        }
      }
      break;

    default:
  }
  return false;
}

// 1. controlType only include rows/columns/filters, not include values
// 2. for Level field, we will return controlType of its hierarchy field
export function getControlType(pivot, sourceMode, field) {
  switch (sourceMode) {
    case WptConstants.MODE_MEMORY:
      if (pivot.rows.some(item => item === field.id)) {
        return "rows";
      } else if (pivot.columns.some(item => item === field.id)) {
        return "columns";
      } else if (pivot.filters.some(item => item === field.id)) {
        return "filters";
      } else {
        return null;
      }

    case WptConstants.MODE_OLAP:
      if (field.type === WptConstants.FIELD_HIERARCHY) {
        if (pivot.rows.some(item => item.id === field.id)) {
          return "rows";
        } else if (pivot.columns.some(item => item.id === field.id)) {
          return "columns";
        } else if (pivot.filters.some(item => item.id === field.id)) {
          return "filters";
        } else {
          return null;
        }
      } else if (field.type === WptConstants.FIELD_LEVEL) {
        if (pivot.rows.some(item => item.id === field.parentId)) {
          return "rows";
        } else if (pivot.columns.some(item => item.id === field.parentId)) {
          return "columns";
        } else if (pivot.filters.some(item => item.id === field.parentId)) {
          return "filters";
        } else {
          return null;
        }
      }
      break;
    default:
  }
  return null;
}

export function getFieldIndexInControl(pivot, sourceMode, field, controlType) {
  let index = -1;

  switch (sourceMode) {
    case WptConstants.MODE_MEMORY:
      if (controlType !== "values") {
        index = pivot[controlType].indexOf(field.id);
      } else {
        index = pivot[controlType].indexOf(field);
      }
      break;

    case WptConstants.MODE_OLAP:
      for (let i = 0; i < pivot[controlType].length; i++) {
        if (pivot[controlType][i].id === field.id) {
          index = i;
          break;
        }
      }
      break;
    default:
  }
  return index;
}

export function getControlFields(pivot, sourceMode, sourceFields, controlType) {
  let controlFields = pivot[controlType];

  switch (sourceMode) {
    case WptConstants.MODE_MEMORY:
      if (controlType !== "values") {
        controlFields = controlFields.map(
          controlField =>
            sourceFields.filter(field => field.id === controlField)[0]
        );
      }
      break;

    case WptConstants.MODE_OLAP:
      if (controlType !== "values") {
        controlFields = controlFields.map(item =>
          Object.assign({}, sourceFields[item.id], {
            children: item.children
              .map(item1 => sourceFields[item1.id])
              .sort((a, b) => a.levelNumber - b.levelNumber)
          })
        );
      } else {
        controlFields = controlFields.map(item => sourceFields[item.id]);
      }
      break;

    default:
  }

  if (
    (controlType === "columns" && pivot.valuesInAxis === 0) ||
    (controlType === "rows" && pivot.valuesInAxis === 1)
  ) {
    controlFields.splice(pivot.valuesIndex, 0, {
      id: "VALUES-ID",
      name: "Values",
      type: WptConstants.FIELD_VALUES
    });
  }

  return controlFields;
}

export function generateOlapSheetFields(sourceFields, filter, sort) {
  let dimensions = [];
  let hierarchies = [];
  let levels = [];
  let measures = [];
  let measuresItem;

  for (let fieldId in sourceFields) {
    const field = Object.assign({}, sourceFields[fieldId]);
    switch (field.type) {
      case WptConstants.FIELD_DIMENSION:
        dimensions.push(field);
        break;
      case WptConstants.FIELD_HIERARCHY:
        hierarchies.push(field);
        break;
      case WptConstants.FIELD_LEVEL:
        levels.push(field);
        break;
      case WptConstants.FIELD_MEASURE:
        measures.push(field);
        break;
      case WptConstants.FIELD_MEASURES:
        measuresItem = field;
        break;

      default:
    }
  }

  const sortByName = (a, b) => {
    if (a.name > b.name) {
      return sort && sort < 0 ? -1 : 1;
    } else {
      return sort && sort < 0 ? 1 : -1;
    }
  };

  levels.forEach(level => {
    for (let hierarchy of hierarchies) {
      if (hierarchy.id === level.parentId) {
        hierarchy.children = hierarchy.children || [];
        hierarchy.children.push(level);
        break;
      }
      if (hierarchy.children) {
        hierarchy.children.sort((a, b) => a.levelNumber - b.levelNumber);
      }
    }
  });

  hierarchies.forEach(hierarchy => {
    for (let dimension of dimensions) {
      if (dimension.id === hierarchy.parentId) {
        dimension.children = dimension.children || [];
        dimension.children.push(hierarchy);
        break;
      }
      if (dimension.children) {
        dimension.children.sort(sortByName);
      }
    }
  });

  if (filter) {
    const matchFilter = name => name.match(new RegExp(filter.trim(), "i"));

    const newDimensions = [];
    dimensions.forEach(dimension => {
      dimension.children = dimension.children.filter(
        hierarchy =>
          matchFilter(hierarchy.name) ||
          hierarchy.children.some(level => matchFilter(level.name))
      );

      dimension.children.forEach(hierarchy => {
        if (hierarchy.children.some(level => matchFilter(level.name))) {
          hierarchy.expanded = true;
        }
      });

      if (dimension.children.length > 0) {
        dimension.expanded = true;
        newDimensions.push(dimension);
      }
    });
    dimensions = newDimensions;

    measures = measures.filter(measure => matchFilter(measure.name));
    measuresItem.expanded = true;
  }

  dimensions.sort(sortByName);
  measures.sort(sortByName);

  measuresItem.children = measures;

  return [measuresItem, ...dimensions];
}

export function generateMdxString(pivot, source) {
  const rows = pivot.rows;
  const columns = pivot.columns;
  const filters = pivot.filters;
  const values = pivot.values;
  const valuesInAxis = pivot.valuesInAxis;
  const nonEmpty = pivot.nonEmpty ? "NON EMPTY " : " ";
  const sourceFields = source.fields;

  let setCount = 0;
  let sets = [];

  const getMDXAxisString = axisName => {
    let rowAttrs = [],
      rowAttr,
      attrs,
      joins,
      rowString;

    let axis = getControlFields(
      pivot,
      WptConstants.MODE_OLAP,
      sourceFields,
      axisName
    );

    axis.forEach(function(hierarchy) {
      rowAttr = [];

      if (hierarchy.type === WptConstants.FIELD_HIERARCHY) {
        attrs = hierarchy.children;

        attrs.forEach(function(attr, index) {
          let attrMember = "";
          if (attr.levelType === 1) {
            attrMember = "{" + hierarchy.allMember + "}";
          } else {
            if (!isFieldFiltered(pivot, WptConstants.MODE_OLAP, attr)) {
              attrMember = attr.id.slice(2) + ".Members";
            } else {
              const pivotField = pivot.fields[attr.id];
              if (
                pivotField &&
                pivotField.filter &&
                pivotField.filter.filterType === WptConstants.FILTER_BY_LABEL &&
                pivotField.filter.filterLabelType ===
                  WptConstants.FILTER_LABEL_INCLUDES &&
                pivotField.filter.filterLabelIncludes &&
                pivotField.filter.filterLabelIncludes.length > 0
              ) {
                attrMember = pivotField.filter.filterLabelIncludes.join(", ");
              } else if (
                pivotField &&
                pivotField.filter &&
                pivotField.filter.filterType === WptConstants.FILTER_BY_LABEL &&
                pivotField.filter.filterLabelType ===
                  WptConstants.FILTER_LABEL_EXCLUDES &&
                pivotField.filter.filterLabelExcludes &&
                pivotField.filter.filterLabelExcludes.length > 0
              ) {
                attrMember =
                  "Except(" +
                  attr.id.slice(2) +
                  ".Members" +
                  ", {" +
                  pivotField.filter.filterLabelExcludes.join(", ") +
                  "})";
              } else {
                attrMember = attr.id.slice(2) + ".Members";
              }
            }
          }

          let attrString = "";

          if (index === 0) {
            attrString = "{" + attrMember + "}";
          } else {
            attrString =
              "Exists({" +
              attrMember +
              "}, " +
              rowAttr[rowAttr.length - 1] +
              ")";
          }

          if (axisName === "filters") {
            rowAttr.push(attrString);
          } else {
            sets[setCount] = attrString;
            rowAttr.push("[~" + setCount + "]");
            setCount++;
          }
        });
        rowAttrs.push(rowAttr);
      } else if (hierarchy.type === WptConstants.FIELD_VALUES) {
        sets[setCount] =
          "{" + pivot.values.map(item => item.id.slice(2)).join(", ") + "}";
        rowAttrs.push(["[~" + setCount + "]"]);
        setCount++;
      }
    });

    if (
      axisName === "columns" &&
      pivot.values.length === 1 &&
      pivot.rows.length > 0
    ) {
      sets[setCount] =
        "{" + pivot.values.map(item => item.id.slice(2)).join(", ") + "}";
      rowAttrs.push(["[~" + setCount + "]"]);
      setCount++;
    }

    if (axisName === "filters") {
      rowAttrs = rowAttrs.map(function(hierarchy) {
        return [hierarchy[hierarchy.length - 1]];
      });
    }
    joins = JSON.parse(JSON.stringify(rowAttrs[rowAttrs.length - 1]));

    const addRowCrossJoin = index => {
      const temp = [];
      rowAttrs[index].forEach(function(level) {
        joins.forEach(function(join) {
          temp.push("CrossJoin(" + level + "," + join + ")");
        });
      });
      joins = temp;
      if (index > 0) {
        addRowCrossJoin(index - 1);
      }
    };

    if (rowAttrs.length - 2 >= 0) {
      addRowCrossJoin(rowAttrs.length - 2);

      rowString = joins[joins.length - 1];

      const addRowUnion = index => {
        rowString = "Union(" + joins[index] + ", " + rowString + ")";
        if (index > 0) {
          addRowUnion(index - 1);
        }
      };

      if (joins.length >= 2) {
        if (rowAttrs.length === 1) {
          rowString = "{" + joins[joins.length - 2] + ",{" + rowString + "}}";
        } else {
          addRowUnion(joins.length - 2);
        }
      }
    } else {
      rowString = joins.join(",");
    }

    if (rowAttrs.length === 1) {
      return " {Hierarchize({" + rowString + "})}";
    } else {
      return " Hierarchize(" + rowString + ")";
    }
  };

  let defaultMeasure,
    mdxString = "SELECT \n";

  const getDefaultMeasure = () => {
    for (let id in sourceFields) {
      const field = sourceFields[id];
      if (field.type === WptConstants.FIELD_MEASURES) {
        return field.defaultMember; //fixme  Mearsure dimension hasn't , it is in hirarchy
      }
    }
    return null;
  };

  defaultMeasure = getDefaultMeasure();
  let rowString = "";
  let columnString = "";

  if (columns.length > 0 || valuesInAxis === 0) {
    columnString += nonEmpty + getMDXAxisString("columns") + " ON COLUMNS ";
  } else if (values.length === 1) {
    columnString += nonEmpty + " {" + values[0].id.slice(2) + "} ON COLUMNS ";
  } else if (valuesInAxis === -1 && defaultMeasure) {
    // use default measure
    columnString += nonEmpty + " {" + defaultMeasure + "} ON COLUMNS ";
  } else {
    // throw new Error("Please select at least one column dimension");
    //                mdxString +=" {} ON COLUMNS "; // mondrain doesn't support this
  }

  if (rows.length > 0 || valuesInAxis === 1) {
    rowString += nonEmpty + getMDXAxisString("rows") + " ON ROWS \n";
  } else if (values.length === 1 && columns.length > 0) {
    rowString += nonEmpty + " {" + values[0].id.slice(2) + "} ON ROWS \n";
  } else if (values.length === 0 && valuesInAxis === -1 && defaultMeasure) {
    rowString += nonEmpty + " {" + defaultMeasure + "} ON ROWS \n";
  }

  if (columnString) {
    mdxString += columnString;
    if (rowString) {
      mdxString += ", \n";
    }
  }
  mdxString += rowString;

  mdxString += "FROM [" + source.data.cubeName + "] \n";

  if (filters.length > 0) {
    mdxString += " WHERE " + getMDXAxisString("filters");
  }

  let setsString = "";
  if (sets.length > 0) {
    setsString += "WITH \n";
    sets.forEach(function(set, index) {
      setsString += "SET [~" + index + "] AS " + set + " \n";
    });
  }

  return setsString + "\n" + mdxString;
}

export const getTreeItemById = (tree, id) => {
  let ret = tree,
    arr = id.split("-");

  for (let i = 1; i < arr.length; i++) {
    let ids = arr.slice(0);
    ids.splice(i + 1, arr.length - i);
    for (let j = ret.children.length - 1; j >= 0; j--) {
      if (ret.children[j].id === ids.join("-")) {
        ret = ret.children[j];
        break;
      }
    }
  }
  return ret;
};



