<template>
  <div class="wpt-side-bar">
    <div
      :style="
        'height: ' +
          height +
          'px; width: ' +
          drawer.width +
          'px;border-right: 1px solid #888;background-color: aliceblue;'
      "
    >
      <refine-pivot
        v-show="navPanel === 'FIELDS'"
        :width="drawer.width"
        :height="height - 30"
      />
    </div>
    <refine-side-buttons
      :change-nav-panel="changeNavPanel"
      :nav-panel="navPanel"
    />
  </div>
</template>

<script>
import RefineSideButtons from "./refine-drawer/RefineSideButtons.vue";
import RefineMain from "../refine-data/RefineMain.vue";
import RefinePivot from "./refine-drawer/RefinePivot.vue";

export default {
  components: {
    RefineSideButtons,
    RefinePivot
  },

  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },

  data() {
    return {
      navPanel: "FIELDS"
    };
  },

  computed: {
    options() {
      return this.$store.state.options;
    },
    drawer() {
      return this.options.wpt.drawer;
    }
  },

  methods: {
    changeNavPanel(panel) {
      this.navPanel = panel;
    }
  }
};
</script>

<style></style>



