<template>
  <div
    v-if="width < 500"
    :style="'width: ' + width + 'px; height: ' + height + 'px; overflow: auto; padding: 0 10px;'"
  >
    <wpt-accordion
      v-if="width < 500"
      v-model="activeName"
    >
      <wpt-accordion-item
        v-if="options.uiFlags.loadFromCsv"
        :title="$t('Lbl_CsvFile')"
        name="1"
      >
        <div slot="title">{{ $t("Lbl_CsvFile") }}</div>
        <nav-csv-file 
          slot="content" />
      </wpt-accordion-item>

      <wpt-accordion-item
        v-if="options.uiFlags.loadFromExcel"
        :title="$t('Lbl_ExcelFile')"
        name="2"
      >
        <div slot="title">{{ $t("Lbl_ExcelFile") }}</div>
        <nav-excel-file 
          slot="content" />
      </wpt-accordion-item>

      <wpt-accordion-item
        v-if="options.uiFlags.loadFromGss"
        :title="$t('Lbl_GssFile')"
        name="3"
      >
        <div slot="title">{{ $t("Lbl_GssFile") }}</div>
        <nav-gss 
          slot="content" 
          :create-source="createSource" />
      </wpt-accordion-item>

      <wpt-accordion-item
        v-if="options.uiFlags.loadFromWs"
        :title="$t('Lbl_WebService')"
        name="4"
      >
        <div slot="title">{{ $t("Lbl_WebService") }}</div>
        <nav-web-service 
          slot="content" 
          :create-source="createSource" />
      </wpt-accordion-item>

      <wpt-accordion-item
        v-if="options.uiFlags.olapMode"
        :title="$t('Lbl_OlapMode')"
        name="4"
      >
        <div slot="title">{{ $t("Lbl_OlapMode") }}</div>
        <nav-olap 
          slot="content" 
          :create-source="createSource" />
      </wpt-accordion-item>
    </wpt-accordion>
  </div>
  <div
    v-else
    class="wpt-flex-row">
    <div
      :style="'width: 200px; height: '+height+'px; background-color: aliceblue;'"
      class="wpt-flex-column"
    >
      <wpt-button
        v-if="options.uiFlags.loadFromCsv"
        :label="$t('Lbl_CsvFile')"
        :type="activeName === '1' ? 'standard' : 'plain'"
        @click="activeName = '1'"
      />
      <wpt-button
        v-if="options.uiFlags.loadFromExcel"
        :label="$t('Lbl_ExcelFile')"
        :type="activeName === '2' ? 'standard' : 'plain'"
        @click="activeName = '2'"
      />
      <wpt-button
        v-if="options.uiFlags.loadFromGss"
        :label="$t('Lbl_GssFile')"
        :type="activeName === '3' ? 'standard' : 'plain'"
        @click="activeName = '3'"
      />
      <wpt-button
        v-if="options.uiFlags.loadFromWs"
        :label="$t('Lbl_WebService')"
        :type="activeName === '4' ? 'standard' : 'plain'"
        @click="activeName = '4'"
      />
      <wpt-button
        v-if="options.uiFlags.olapMode"
        :label="$t('Lbl_OlapMode')"
        :type="activeName === '5' ? 'standard' : 'plain'"
        @click="activeName = '5'"
      />
    </div>
    <div
      :style="'width: '+ (width -200)+ 'px; height: '+height+'px; overflow: auto; padding: 10px;'"
      class="wpt-flex-column"
    >
      <nav-csv-file
        v-if="activeName === '1' " />

      <nav-excel-file
        v-else-if="activeName === '2'" />

      <nav-gss
        v-else-if="activeName === '3'"
        :create-source="createSource" />

      <nav-web-service
        v-else-if="activeName === '4'"
        :create-source="createSource" />

      <nav-olap
        v-else-if="activeName === '5'"
        :create-source="createSource" />
    </div>
  </div>
</template>

<script>
import NavCsvFile from "./new-wpt/NavCsvFile.vue";
import NavExcelFile from "./new-wpt/NavExcelFile.vue";
import NavGss from "./new-wpt/NavGss.vue";
import NavWebService from "./new-wpt/NavWebService.vue";
import NavOlap from "./new-wpt/NavOlap.vue";

export default {
  components: {
    NavCsvFile,
    NavExcelFile,
    NavGss,
    NavWebService,
    NavOlap
  },
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      contentHeight: this.height - 60,
      activeName: "1"
    };
  },
  computed: {
    options() {
      return this.$store.state.options;
    }
  },
  methods: {
    createSource() {
      let newSource = this.$store.state.dialog.data.newSource;
      this.$store.dispatch("wpt/generateNewWptFromSource", { newSource });
      this.$store.commit("dialog/close");
    }
  }
};
</script>

<style></style>



