<template>
  <div
    :style="'height: ' + height + 'px;'"
    class="wpt-list-container"
    @dragover="dragover"
    @drop="drop"
  >
    <control-list
      :sheet="sheet"
      :source="source"
      :control-fields="controlFields"
      :control-type="controlType"
    />
  </div>
</template>

<script>
import ControlList from "./ControlList.vue";
import { getControlFields } from "../../../../services/SheetService";

export default {
  components: {
    ControlList
  },

  props: {
    sheet: {
      type: Object,
      required: true
    },
    source: {
      type: Object,
      required: true
    },
    height: {
      type: Number,
      required: false,
      default: 100
    },
    controlType: {
      type: String,
      required: true
    }
  },

  computed: {
    pivot() {
      return this.sheet.pivot;
    },
    controlFields() {
      const fields = this.source.fields;
      return getControlFields(
        this.pivot,
        this.source.mode,
        fields,
        this.controlType
      );
    },
    dragData() {
      return this.$store.state.system.dragData;
    }
  },

  methods: {
    dragover(event) {
      if (this.dragData.type) {
        event.preventDefault();
      }
    },
    drop(event) {
      event.preventDefault();
      if (this.dragData.type){
        if (this.dragData.type !== this.controlType) {
          this.$store.dispatch("wpt/moveFieldBetweenControls", {
            sheet: this.sheet,
            pivot: this.pivot,
            source: this.source,
            field: this.dragData.item,
            from: this.dragData.type,
            to: this.controlType
          });
        } else {
          this.$store.dispatch("wpt/moveFieldWithinSameControl", {
            sheet: this.sheet,
            pivot: this.pivot,
            source: this.source,
            field: this.dragData.item,
            controlType: this.controlType,
            direction: "end"
          });
        }
      }
    }
  }
};
</script>

<style></style>



