<template>
  <div :style="'width: ' + width + 'px;height: ' + height + 'px;'">
    <wpt-pagination
      :total="drillThrough.data.length"
      :page-size="pageSize"
      @page-change="pageChange"
      @page-size-change="pageSizeChange"
    />
    <wpt-table
      :height="height - 30"
      :width="width"
      :columns="columns"
      :table-data="tableData"
      :paging-index="(pageIndex - 1) * pageSize"
      column-width-drag
      even-bg-color="#f2f2f2"
    />
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },

  data() {
    return {
      pageIndex: 1,
      pageSize: 20,
      tableData: [],
      columns: [
        {
          field: "custome",
          width: 50,
          titleAlign: "center",
          columnAlign: "center",
          formatter: function(rowData, index, pagingIndex) {
            const currentIndex = index + pagingIndex;
            return currentIndex < 3
              ? '<span style="color:red;font-weight: bold;">' +
                  (currentIndex + 1) +
                  "</span>"
              : currentIndex + 1;
          },
          isFrozen: true
        },
        ...this.$store.state.modal.data.fields.map(field => ({
          field: field.name,
          title: field.name,
          width: 100,
          columnAlign: "center",
          titleAlign: "center",
          isFrozen: false
        }))
      ]
    };
  },

  computed: {
    drillThrough() {
      return this.$store.state.modal.data;
    }
  },

  created() {
    this.getTableData();
  },
  methods: {
    getTableData() {
      this.tableData = this.drillThrough.data.slice(
        (this.pageIndex - 1) * this.pageSize,
        this.pageIndex * this.pageSize
      );
    },
    pageChange(pageIndex) {
      this.pageIndex = pageIndex;
      this.getTableData();
    },
    pageSizeChange(pageSize) {
      this.pageIndex = 1;
      this.pageSize = pageSize;
      this.getTableData();
    }
  }
};
</script>

<style></style>



