import Worker from "simple-web-worker";

import WptConstants from "~/WptConstants";
import i18n from "~/i18n";
import { createDataModel } from "~/services/MemoryService";
import { generateId } from "~/services/CommonUtils";

export default {
  setWpt(store, wpt) {
    store.commit("options/setWptOptions", wpt.options, {root:true});
    store.commit('setSource', wpt.source);
    store.commit('setSheet', wpt.sheet);
  },

  loadDataArray(store, { attrArray, dataArray, successHandler, errorHandler }) {
    if (dataArray.length < 1) {
      store.dispatch("system/errorMessage", i18n.t("Msg_InvalidData"));
    } else if (
      dataArray.length > store.rootState.options.limit.sourceDataMaxRows
    ) {
      store.dispatch(
        "system/errorMessage",
        i18n.t("Msg_TooManyRows") +
          " : " +
          dataArray.length +
          " > " +
          store.rootState.options.limit.sourceDataMaxRows
      );
    } else {
      store.dispatch(
        "system/loadingMessage",
        i18n.t("Msg_LoadingData") + dataArray.length, {root: true}
      );

      Worker.run(createDataModel, [attrArray, dataArray, WptConstants])
        .then(response => {
          store.dispatch("system/clearMessage", null, {root: true});

          if (successHandler) {
            successHandler.apply(null, [response]);
          }
        })
        .catch(error => {
          store.dispatch("system/errorMessage", error);
          if (errorHandler) {
            errorHandler.apply(null, [error]);
          }
        });
    }
  },

  generateNewWptFromSource(store, { newSource }) {
    newSource.id = generateId();
    store.commit("setSource", newSource);
    store.commit("setSheet", {
      id: generateId(),
      pivot: {
        fields: {},
        rows: [],
        columns: [],
        filters: [],
        values: [],
        valuesInAxis: -1,
        valuesIndex: -1,
        nonEmpty: 1
      },

      collapsed: {},
      sheetData: null
    });
    store.commit("options/resetWptOptions", null, { root: true });
  },

  generateWptForSave(store, { ignoreData, successCB, errorCB, retJSON }) {
    try {
      const savedWpt = JSON.parse(JSON.stringify(store.state));
      savedWpt.options = JSON.parse(
        JSON.stringify(store.rootState.options.wpt)
      );

      const source = savedWpt.source;
      switch (source.mode) {
        case WptConstants.MODE_MEMORY:
          if (ignoreData) {
            delete source.data;
          }

          for (let field of source.fields) {
            delete field.count;
            delete field.lookup;
          }
          break;

        case WptConstants.MODE_OLAP:
          delete source.fields;
          break;

        default:
      }
      delete source.loading;
      delete source.error;

      const sheet = savedWpt.sheet;
      delete sheet.sheetData;

      successCB.apply(null, [retJSON ? savedWpt : JSON.stringify(savedWpt)]);
    } catch (error) {
      if (errorCB) {
        errorCB.apply(null, [error]);
      }
    }
  }
};



