<template>
  <div
    :style="'width: ' + width + 'px;height: ' + height + 'px; overflow: auto;'"
  >
    <div style="padding:5px;">
      <div class="wpt-section wpt-flex-row">
        <div style="width: 150px;">{{ $t("Lbl_XmlaUrl") }}</div>
        <div>{{ source.data.xmlaUrl }}</div>
      </div>

      <div class="wpt-section wpt-flex-row">
        <div style="width: 150px;">{{ $t("Lbl_DataSource") }}</div>
        <div>{{ source.data.dataSourceInfo }}</div>
      </div>

      <div class="wpt-section wpt-flex-row">
        <div style="width: 150px;">{{ $t("Lbl_Catalog") }}</div>
        <div>{{ source.data.catalog }}</div>
      </div>

      <div class="wpt-section wpt-flex-row">
        <div style="width: 150px;">{{ $t("Lbl_CubeName") }}</div>
        <div>{{ source.data.cubeName }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },
  computed: {
    source() {
      return this.$store.state.wpt.source;
    }
  }
};
</script>

<style></style>



