import axios from "axios";
import Papa from "papaparse";
import XLSX from "xlsx";
import WptConstants from "../WptConstants";

const parseCsvData = (data, delimiter, url, store, i18n) => {
  store.dispatch("system/loadingMessage", i18n("Msg_ParsingData"));

  Papa.parse(data, {
    delimiter: delimiter || ",",
    worker: false,
    skipEmptyLines: true,
    header: false,
    dynamicTyping: false,
    complete: results => {
      const attrArray = results.data[0];
      const dataArray = results.data.splice(1);

      store.dispatch("wpt/loadDataArray", {
        attrArray,
        dataArray,
        successHandler: response => {
          const newSource = {
            name: i18n("Lbl_Source") + 1,
            mode: WptConstants.MODE_MEMORY,
            url,
            data: dataArray,
            fields: response
          };
          store.dispatch("wpt/generateNewWptFromSource", { newSource });
          store.commit("dialog/close");
        },
        errorHandler: null
      });
    }
  });
};


export function readLocalFile (file, fileType, store, i18n, delimiter) {

  const errorHandler = error => {
    store.dispatch("system/errorMessage", error);
  };


  const reader = new FileReader();

  reader.onloadstart = () => {
    store.dispatch("system/loadingMessage", i18n("Msg_LoadingFile"));
  };

  reader.onerror = () => {
    store.dispatch("system/errorMessage", i18n("Msg_NoDataLoaded"));
  };

  reader.onabort = () => {};

  // If we use onloadend, we need to check the readyState.
  reader.onloadend = e => {
    if (e.target.readyState === FileReader.DONE) {
      store.dispatch("system/clearMessage");
      const response = e.target.result;

      switch (fileType) {
        case "csv":
          store.commit("dialog/setData", {
            type: WptConstants.FORMAT_CSV,
            url: file.name,
            content: response
          });
          parseCsvData(response, delimiter, file.name, store, i18n);
          break;

        case "wpt":
          const wpt = JSON.parse(response);
          try {
            if (wpt.format === "WPT" && wpt.version === "3.0") {
              store.dispatch("wpt/setWpt", wpt);
              store.commit("dialog/close");
            } else {
              store.dispatch("system/errorMessage", i18n("Msg_NotValidWptFormat"));
            }
          } catch (error) {
            errorHandler(error);
          }
          break;

        case "xls":
        case "xlsx":
          try {
            const workbook = XLSX.read(response, { type: "binary" });
            const excelSheets = [];

            workbook.SheetNames.forEach(function(sheet, index) {
              excelSheets.push({
                id: index,
                label: sheet,
                data: XLSX.utils.sheet_to_csv(workbook.Sheets[sheet])
              });
            });

            store.commit("dialog/setData", {
              type: WptConstants.FORMAT_EXCEL,
              url: file.name,
              content: excelSheets
            });
            if (excelSheets.length > 0) {
              parseCsvData(excelSheets[0].data, ",", file.name, store, i18n);
            } else {
              errorHandler("Empty excel file");
            }
          } catch (error) {
            errorHandler(error);
          }
          break;

      }
    }
  };
  if (fileType === 'xls' || fileType === 'xlsx') {
    reader.readAsBinaryString(file);
  } else {
    reader.readAsText(file);
  }

}


export function readFileUrl (fileUrl, fileType, store, i18n, delimiter) {

  const errorHandler = error => {
    store.dispatch("system/errorMessage", error);
  };

  const successHandler = response => {
    store.dispatch("system/clearMessage");

    switch (fileType) {
      case "csv":
        store.commit("dialog/setData", {
          type: WptConstants.FORMAT_CSV,
          url: fileUrl,
          content: response.data
        });
        parseCsvData(response.data, delimiter, fileUrl, store, i18n);
        break;

      case "wpt":
        const wpt = response.data;
        try {
          if (wpt.format === "WPT" && wpt.version === "3.0") {
            store.dispatch("wpt/setWpt", wpt);
            store.commit("dialog/close");
          } else {
            store.dispatch("system/errorMessage", i18n("Msg_NotValidWptFormat"));
          }
        } catch (error) {
          errorHandler(error);
        }
        break;

      case "xls":
      case "xlsx":
        const reader = new FileReader();

        reader.addEventListener("loadend", () => {
          try {
            // reader.result contains the contents of blob as a typed array
            const workbook = XLSX.read(reader.result, { type: "binary" });
            const excelSheets = [];

            workbook.SheetNames.forEach(function(sheet, index) {
              excelSheets.push({
                id: index,
                label: sheet,
                data: XLSX.utils.sheet_to_csv(workbook.Sheets[sheet])
              });
            });

            store.commit("dialog/setData", {
              type: WptConstants.FORMAT_EXCEL,
              url: fileUrl,
              content: excelSheets
            });
            if (excelSheets.length > 0) {
              parseCsvData(excelSheets[0].data, ",", fileUrl, store, i18n);
            } else {
              errorHandler("Empty excel file");
            }
          } catch (error) {
            errorHandler(error);
          }
        });
        reader.readAsBinaryString(response.data);

        break;

    }

  };

  store.dispatch("system/loadingMessage", i18n("Msg_LoadingFile"));

  const serverOption = store.state.options.server;
  const responseType = (fileType === 'xls' || fileType === 'xlsx') ? 'blob': 'text';
  if (serverOption.fileProxyEnabled) {
    axios
      .post(
        serverOption.fileProxy,
        {},
        {
          timeout: serverOption.fileTimeout,
          responseType,
          headers: { "x-file-url": fileUrl }
        }
      )
      .then(successHandler)
      .catch(errorHandler);
  } else {
    axios
      .get(fileUrl, {
        timeout: serverOption.fileTimeout,
        responseType
      })
      .then(successHandler)
      .catch(errorHandler);
  }

}




