import Tooltip from "tooltip.js";

export let state = {
  enabled: true
};

const positions = [
  "top",
  "top-start",
  "top-end",
  "right",
  "right-start",
  "right-end",
  "bottom",
  "bottom-start",
  "bottom-end",
  "left",
  "left-start",
  "left-end"
];

export const defaultOptions = {
  // Default tooltip placement relative to target element
  placement: "top",
  // Default CSS classes applied to the tooltip element
  class: "vue-tooltip-theme",
  // Default CSS classes applied to the target element of the tooltip
  targetClass: "has-tooltip",
  // Is the content HTML by default?
  html: true,
  // Default HTML template of the tooltip element
  // It must include `tooltip-arrow` & `tooltip-inner` CSS classes (can be configured, see below)
  // Change if the classes conflict with other libraries (for example bootstrap)
  template:
    '<div class="tooltip" role="tooltip"><div class="tooltip-arrow"></div><div class="tooltip-inner"></div></div>',
  // Selector used to get the arrow element in the tooltip template
  arrowSelector: ".tooltip-arrow, .tooltip__arrow",
  // Selector used to get the inner content element in the tooltip template
  innerSelector: ".tooltip-inner, .tooltip__inner",
  // Delay (ms)
  delay: 500,
  // Default events that trigger the tooltip
  trigger: "hover focus",
  // Default position offset (px)
  offset: 0,
  // Default container where the tooltip will be appended
  container: "body",
  boundariesElement: undefined,
  popperOptions: {},
  // Class added when content is loading
  loadingClass: "tooltip-loading",
  // Displayed when tooltip content is loading
  loadingContent: "...",
  // Hide on mouseover tooltip
  autoHide: true,
  // Close tooltip on click on tooltip target?
  hideOnTargetClick: true,
  // Auto destroy tooltip DOM nodes (ms)
  disposeTimeout: 5000
};

const getOptions = options => {
  const result = {
    placement:
      typeof options.placement !== "undefined"
        ? options.placement
        : defaultOptions.placement,
    delay:
      typeof options.delay !== "undefined"
        ? options.delay
        : defaultOptions.delay,
    html:
      typeof options.html !== "undefined" ? options.html : defaultOptions.html,
    template:
      typeof options.template !== "undefined"
        ? options.template
        : defaultOptions.template,
    arrowSelector:
      typeof options.arrowSelector !== "undefined"
        ? options.arrowSelector
        : defaultOptions.arrowSelector,
    innerSelector:
      typeof options.innerSelector !== "undefined"
        ? options.innerSelector
        : defaultOptions.innerSelector,
    trigger:
      typeof options.trigger !== "undefined"
        ? options.trigger
        : defaultOptions.trigger,
    offset:
      typeof options.offset !== "undefined"
        ? options.offset
        : defaultOptions.offset,
    container:
      typeof options.container !== "undefined"
        ? options.container
        : defaultOptions.container,
    boundariesElement:
      typeof options.boundariesElement !== "undefined"
        ? options.boundariesElement
        : defaultOptions.boundariesElement,
    autoHide:
      typeof options.autoHide !== "undefined"
        ? options.autoHide
        : defaultOptions.autoHide,
    hideOnTargetClick:
      typeof options.hideOnTargetClick !== "undefined"
        ? options.hideOnTargetClick
        : defaultOptions.hideOnTargetClick,
    loadingClass:
      typeof options.loadingClass !== "undefined"
        ? options.loadingClass
        : defaultOptions.loadingClass,
    loadingContent:
      typeof options.loadingContent !== "undefined"
        ? options.loadingContent
        : defaultOptions.loadingContent,
    popperOptions: {
      ...(typeof options.popperOptions !== "undefined"
        ? options.popperOptions
        : defaultOptions.popperOptions)
    }
  };

  if (result.offset) {
    const typeofOffset = typeof result.offset;
    let offset = result.offset;

    // One value -> switch
    if (
      typeofOffset === "number" ||
      (typeofOffset === "string" && offset.indexOf(",") === -1)
    ) {
      offset = `0, ${offset}`;
    }

    if (!result.popperOptions.modifiers) {
      result.popperOptions.modifiers = {};
    }
    result.popperOptions.modifiers.offset = {
      offset
    };
  }

  if (result.trigger && result.trigger.indexOf("click") !== -1) {
    result.hideOnTargetClick = false;
  }

  if (!result.boundariesElement) {
    result.boundariesElement = window.document.querySelector(result.container);
  }
  return result;
};

const getPlacement = (value, modifiers) => {
  let placement = value.placement;
  for (let i = 0; i < positions.length; i++) {
    const pos = positions[i];
    if (modifiers[pos]) {
      placement = pos;
    }
  }
  return placement;
};

const getContent = value => {
  const type = typeof value;
  if (type === "string") {
    return value;
  } else if (value && type === "object") {
    return value.content;
  } else {
    return false;
  }
};

const createTooltip = (el, value, modifiers = {}) => {
  const content = getContent(value);
  const opts = {
    title: content,
    ...getOptions({
      ...value,
      placement: getPlacement(value, modifiers)
    })
  };
  el._tooltip = new Tooltip(el, opts);
};

const destroyTooltip = el => {
  if (el._tooltip) {
    el._tooltip.dispose();
    delete el._tooltip;
  }
};

const bind = (el, { value, modifiers }) => {
  destroyTooltip(el);
  const content = getContent(value);
  if (content && state.enabled) {
    createTooltip(el, value, modifiers);
  }
};

export default {
  options: defaultOptions,
  bind,
  update: bind,
  unbind(el) {
    destroyTooltip(el);
  }
};



