<template>
  <div class="wpt-side-bar">
    <div
      :style="
        'height: ' +
          height +
          'px; width: ' +
          drawer.width +
          'px;border-right: 1px solid #888;background-color: aliceblue;'
      "
    >
      <source-pivot
        v-show="navPanel === 'FIELDS'"
        :width="drawer.width"
        :height="height - 30"
      />
    </div>
    <source-side-buttons
      :change-nav-panel="changeNavPanel"
      :nav-panel="navPanel"
    />
  </div>
</template>

<script>
import SourcePivot from "./source-drawer/SourcePivot.vue";
import SourceSideButtons from "./source-drawer/SourceSideButtons.vue";

export default {
  components: {
    SourcePivot,
    SourceSideButtons
  },

  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },

  data() {
    return {
      navPanel: "FIELDS"
    };
  },

  computed: {
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    source() {
      return this.$store.state.wpt.source;
    },
    options() {
      return this.$store.state.options;
    },
    drawer() {
      return this.options.wpt.drawer;
    },
    uiFlags() {
      return this.options.uiFlags;
    }
  },

  methods: {
    changeNavPanel(panel) {
      this.navPanel = panel;
    },
    openSheetView() {
      this.$store.commit("options/setView", this.WptConstants.VIEW_SHEET);
    },

    refineData() {
      this.$store.commit("modal/open", {
        type: this.WptConstants.MODAL_REFINE_DATA,
        payload: {
          source: this.source
        }
      });
    }
  }
};
</script>

<style></style>



