import { mergeRecursive } from "~/services/CommonUtils";

export default {
  open(state, modal) {
    state.type = modal.type;
    state.title = modal.title;
    state.payload = modal.payload || {};
    state.data = modal.data || {};
  },

  close(state) {
    state.type = null;
    state.title = "";
    state.payload = {};
    state.data = {};
  },

  setData(state, data) {
    state.data = data;
  },

  updateData(state, data) {
    state.data = { ...mergeRecursive(state.data, data) };
  }
};



