<template>
  <div v-if="!noLookupsYet">
    <wpt-tabs>
      <wpt-tab
        v-if="source.mode === WptConstants.MODE_MEMORY"
        :name="$t('Lbl_Customize')"
      >
        <div class="wpt-section wpt-flex-row">
          <div style="width: 100px;">
            {{ $t("Lbl_OriginalName") }}
          </div>
          <div style="flex: 1">
            {{ field.name }}
          </div>
        </div>
        <div class="wpt-section wpt-flex-row">
          <div style="width: 100px;">
            {{ $t("Lbl_CustomName") }}
          </div>
          <div style="flex: 1">
            <input v-model="customName" >
          </div>
        </div>

        <div class="wpt-section wpt-flex-row">
          <wpt-checkbox
            v-if="controlType === 'rows' || controlType === 'columns'"
            v-model="hideSubtotal"
          >
            {{ $t("Lbl_HideSubtotal") }}
          </wpt-checkbox>
        </div>
      </wpt-tab>

      <wpt-tab
        v-if="
          source.mode === WptConstants.MODE_MEMORY &&
            (controlType === 'rows' || controlType === 'columns')
        "
        :name="$t('Lbl_SortBy')"
        style="min-height:200px;"
      >
        <div class="wpt-section wpt-flex-row">
          <wpt-radio
            v-model="sortType"
            :value="-1"
            :checked="sortType === -1"
            name="sortType"
          >
            {{ $t("Lbl_NotSort") }}
          </wpt-radio>
          <wpt-radio
            v-model="sortType"
            :value="WptConstants.SORT_BY_LABEL"
            :checked="sortType === WptConstants.SORT_BY_LABEL"
            name="sortType"
          >
            {{ $t("Lbl_Label") }}
          </wpt-radio>
          <wpt-radio
            v-if="source.mode === WptConstants.MODE_MEMORY && values.length > 0"
            v-model="sortType"
            :value="WptConstants.SORT_BY_VALUE"
            :checked="sortType === WptConstants.SORT_BY_VALUE"
            name="sortType"
          >
            {{ $t("Lbl_Value") }}
          </wpt-radio>
        </div>

        <template v-if="sortType !== -1">
          <div v-if="sortType === WptConstants.SORT_BY_LABEL">
            <div class="wpt-section">
              {{ field.name }}
            </div>
          </div>

          <div v-if="sortType === WptConstants.SORT_BY_VALUE">
            <div class="wpt-section">
              <wpt-select
                v-model="sortValueId"
                :options="filterValueIdOptions"
              />
            </div>
          </div>

          <wpt-radio
            v-model="sortDescend"
            :value="0"
            :checked="sortDescend === 0"
            name="sortDescend"
          >
            {{ $t("Lbl_SortAscend") }}
          </wpt-radio>
          <wpt-radio
            v-model="sortDescend"
            :value="1"
            :checked="sortDescend === 1"
            name="sortDescend"
          >
            {{ $t("Lbl_SortDescend") }}
          </wpt-radio>
        </template>
      </wpt-tab>

      <wpt-tab
        v-if="
          controlType === 'rows' ||
            controlType === 'columns' ||
            controlType === 'filters'
        "
        :name="$t('Lbl_FilterBy')"
        style="min-height:200px;"
      >
        <div class="wpt-section wpt-flex-row">
          <wpt-radio
            v-model="filterType"
            :value="-1"
            :checked="filterType === -1"
            name="filterType"
          >
            {{ $t("Lbl_NotFilter") }}
          </wpt-radio>
          <wpt-radio
            v-model="filterType"
            :value="WptConstants.FILTER_BY_LABEL"
            :checked="filterType === WptConstants.FILTER_BY_LABEL"
            name="filterType"
          >
            {{ $t("Lbl_Label") }}
          </wpt-radio>
          <template
            v-if="
              source.mode === WptConstants.MODE_MEMORY &&
                values.length > 0 &&
                (controlType === 'rows' || controlType === 'columns')
            "
          >
            <wpt-radio
              v-model="filterType"
              :value="WptConstants.FILTER_BY_VALUE"
              :checked="filterType === WptConstants.FILTER_BY_VALUE"
              name="filterType"
            >
              {{ $t("Lbl_Value") }}
            </wpt-radio>
            <wpt-radio
              v-model="filterType"
              :value="WptConstants.FILTER_BY_TOP"
              :checked="filterType === WptConstants.FILTER_BY_TOP"
              name="filterType"
            >
              {{ $t("Lbl_TopBottom") }}
            </wpt-radio>
          </template>
        </div>

        <template v-if="filterType !== -1">
          <div v-if="filterType === WptConstants.FILTER_BY_LABEL">
            <div class="wpt-flex-row">
              <div style="width: 150px; padding-right: 10px;">
                <div style="padding: 10px 0;">
                  <wpt-radio
                    v-model="filterLabelType"
                    :value="WptConstants.FILTER_LABEL_INCLUDES"
                    :checked="filterLabelType === WptConstants.FILTER_LABEL_INCLUDES"
                    name="filterLabelType"
                  >
                    {{ $t("Lbl_ManuallyInclude") }}
                  </wpt-radio>
                  <wpt-radio
                    v-model="filterLabelType"
                    :value="WptConstants.FILTER_LABEL_EXCLUDES"
                    :checked="filterLabelType === WptConstants.FILTER_LABEL_EXCLUDES"
                    name="filterLabelType"
                  >
                    {{ $t("Lbl_ManuallyExclude") }}
                  </wpt-radio>
                  <wpt-radio
                    v-if="source.mode === WptConstants.MODE_MEMORY"
                    v-model="filterLabelType"
                    :value="WptConstants.FILTER_LABEL_FORMULA"
                    :checked="filterLabelType === WptConstants.FILTER_LABEL_FORMULA"
                    name="filterLabelType"
                  >
                    {{ $t("Lbl_Formula") }}
                  </wpt-radio>
                </div>

                <div
                  v-if="
                    filterLabelType === WptConstants.FILTER_LABEL_FORMULA &&
                      source.mode === WptConstants.MODE_MEMORY
                  "
                  class="wpt-flex-column"
                >
                  <div>
                    {{ $t("Lbl_Label") }}
                  </div>
                  <wpt-select
                    v-model="filterLabelOperator"
                    :options="filterLabelCompareOptions"
                  />

                  <input v-model="filterLabelValue" >
                  <template
                    v-if="
                      filterLabelOperator === WptConstants.COMPARE_BETWEEN ||
                        filterLabelOperator === WptConstants.COMPARE_NOT_BETWEEN
                    "
                  >
                    <input v-model="filterLabelValue1" >
                  </template>
                </div>
                <div v-else>
                  {{
                    filterLabelType === WptConstants.FILTER_LABEL_INCLUDES
                      ? $t("Msg_ManuallyInclude")
                      : $t("Msg_ManuallyExclude")
                  }}
                </div>
              </div>

              <div
                style="flex: 1; border-left: 1px solid #888; padding-left: 10px;"
              >
                <div class="wpt-flex-row">
                  <input
                    :placeholder="$t('Btn_Search')"
                    v-model="lookupsSearch"
                    style="width:100px;"
                  >
                  <template
                    v-if="filterLabelType !== WptConstants.FILTER_LABEL_FORMULA"
                  >
                    <wpt-button
                      :label="$t('Lbl_SelectAll')"
                      type="plain"
                      @click="selectAll"
                    />
                    <wpt-button
                      :label="$t('Lbl_SelectNone')"
                      type="plain"
                      @click="selectNone"
                    />
                  </template>
                </div>

                <div
                  class="wpt-list-container"
                  style="height:300px; padding: 5px;"
                >
                  <div 
                    v-for="item in data" 
                    :key="item.id">
                    <wpt-checkbox
                      :disabled="
                        filterLabelType === WptConstants.FILTER_LABEL_FORMULA
                      "
                      :value="isItemSelected(item)"
                      @change="
                        value => {
                          handleItemSelected(value, item);
                        }
                      "
                    >
                      {{
                        item.label +
                          (source.mode === WptConstants.MODE_OLAP
                            ? " [" + item.id + "]"
                            : "")
                      }}
                    </wpt-checkbox>
                  </div>
                </div>
                <el-pagination
                  :current-page.sync="currentPage"
                  :page-size="pageSize"
                  :total="lookup.length"
                  small
                  layout="prev, pager, next, total"
                />
              </div>
            </div>
          </div>

          <div
            v-if="filterType === WptConstants.FILTER_BY_VALUE"
            class="wpt-flex-column"
          >
            <wpt-select
              v-model="filterValueId"
              :options="filterValueIdOptions"
            />

            <wpt-select
              v-model="filterValueOperator"
              :options="filterValueCompareOptions"
            />

            <div class="wpt-flex-row">
              <input
                v-model="filterValue"
                style="width:100px;"
              >

              <template
                v-if="
                  filterValueOperator === WptConstants.COMPARE_BETWEEN ||
                    filterValueOperator === WptConstants.COMPARE_NOT_BETWEEN
                "
              >
                <input
                  v-model="filterValue1"
                  style="width:100px;"
                >
              </template>
            </div>
          </div>

          <div
            v-if="filterType === WptConstants.FILTER_BY_TOP"
            class="wpt-flex-column"
          >
            <wpt-select
              v-model="filterTopOperator"
              :options="topBottomOptions"
            />

            <input
              v-model="filterTopValue"
              style="width:100px;"
            >

            <wpt-select
              v-model="filterTopValueId"
              :options="filterValueIdOptions"
            />
          </div>
        </template>
      </wpt-tab>
    </wpt-tabs>

    <div class="wpt-flex-row">
      <div
        style="flex: 1"
        class="wpt-message"
      />
      <wpt-button
        :label="$t('Btn_OK')"
        @click="handleConfirm" />
    </div>

  </div>
</template>

<script>
import { getControlType } from "~/services/SheetService";

export default {
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },
  data() {
    const { field, sheet } = this.$store.state.dialog.payload;
    const sField = Object.assign({}, field, sheet.pivot.fields[field.id]);
    const { name, hideSubtotal, sort, filter } = sField;
    const WptConstants = this.WptConstants;
    const values = sheet.pivot.values;

    return {
      selectedLookups: [],
      currentPage: 1,
      pageSize: 100,

      sortType: sort && sort.sortType ? sort.sortType : -1,
      sortDescend: sort && sort.sortDescend ? 1 : 0,
      sortValueId:
        sort && sort.sortType === WptConstants.SORT_BY_VALUE
          ? sort.sortValueId
          : values.length > 0
          ? values[0].id
          : "",

      filterType: filter && filter.filterType ? filter.filterType : -1,

      filterLabelType:
        filter && filter.filterType === WptConstants.FILTER_BY_LABEL
          ? filter.filterLabelType
          : WptConstants.FILTER_LABEL_INCLUDES,
      filterLabelOperator:
        filter &&
        filter.filterType === WptConstants.FILTER_BY_LABEL &&
        filter.filterLabelType === WptConstants.FILTER_LABEL_FORMULA
          ? filter.filterLabelOperator
          : "",
      filterLabelValue:
        filter &&
        filter.filterType === WptConstants.FILTER_BY_LABEL &&
        filter.filterLabelType === WptConstants.FILTER_LABEL_FORMULA
          ? filter.filterLabelValue
          : "",
      filterLabelValue1:
        filter &&
        filter.filterType === WptConstants.FILTER_BY_LABEL &&
        filter.filterLabelType === WptConstants.FILTER_LABEL_FORMULA
          ? filter.filterLabelValue1
          : "",
      filterLabelIncludes:
        filter &&
        filter.filterType === WptConstants.FILTER_BY_LABEL &&
        filter.filterLabelType === WptConstants.FILTER_LABEL_INCLUDES
          ? filter.filterLabelIncludes
          : [],
      filterLabelExcludes:
        filter &&
        filter.filterType === WptConstants.FILTER_BY_LABEL &&
        filter.filterLabelType === WptConstants.FILTER_LABEL_EXCLUDES
          ? filter.filterLabelExcludes
          : [],

      lookupsSearch: "",

      filterValueId:
        filter && filter.filterType === WptConstants.FILTER_BY_VALUE
          ? filter.filterValueId
          : values.length > 0
          ? values[0].id
          : "",
      filterValueOperator:
        filter && filter.filterType === WptConstants.FILTER_BY_VALUE
          ? filter.filterValueOperator
          : "",
      filterValue:
        filter && filter.filterType === WptConstants.FILTER_BY_VALUE
          ? filter.filterValue
          : "",
      filterValue1:
        filter && filter.filterType === WptConstants.FILTER_BY_VALUE
          ? filter.filterValue1
          : "",

      filterTopValueId:
        filter && filter.filterType === WptConstants.FILTER_BY_TOP
          ? filter.filterTopValueId
          : values.length > 0
          ? values[0].id
          : "",
      filterTopOperator:
        filter && filter.filterType === WptConstants.FILTER_BY_TOP
          ? filter.filterTopOperator
          : WptConstants.COMPARE_TOP,
      filterTopValue:
        filter && filter.filterType === WptConstants.FILTER_BY_TOP
          ? filter.filterTopValue
          : 10,

      hideSubtotal,
      customName: name
    };
  },

  computed: {
    highLightColor() {
      return this.$store.state.options.styles.highLightColor;
    },

    payload() {
      return this.$store.state.dialog.payload;
    },

    sheet() {
      return this.payload.sheet;
    },

    field() {
      return this.payload.field;
    },

    source() {
      return this.$store.state.wpt.source;
    },

    controlType() {
      return getControlType(this.sheet.pivot, this.source.mode, this.field);
    },

    values() {
      return this.sheet.pivot.values;
    },

    noLookupsYet() {
      switch (this.source.mode) {
        case this.WptConstants.MODE_MEMORY:
          if (this.field.type !== this.WptConstants.FIELD_CALCULATED) {
            if (!this.field.lookup) {
              this.$store.dispatch("wpt/getMemoryLookups", {
                source: this.source
              });
              return true;
            }
          }
          return false;

        case this.WptConstants.MODE_OLAP:
          if (this.field.type === this.WptConstants.FIELD_LEVEL) {
            const sourceField = this.source.fields[this.field.id];
            if (!sourceField || !sourceField.lookup) {
              if (!sourceField.loading && !sourceField.error) {
                this.$store.dispatch("wpt/getOlapAttributeMembers", {
                  source: this.source,
                  field: this.field
                });
                return true;
              }
            }
          }
          return false;
        default:
      }
      return false;
    },

    lookup() {
      let lookup = [];
      switch (this.source.mode) {
        case this.WptConstants.MODE_MEMORY:
          for (let key in this.field.lookup) {
            lookup.push({ id: key, label: key });
          }
          break;

        case this.WptConstants.MODE_OLAP:
          if (this.field.type === this.WptConstants.FIELD_LEVEL) {
            const fieldLookup = this.source.fields[this.field.id].lookup;
            for (let key in fieldLookup) {
              lookup.push({ id: key, label: fieldLookup[key] });
            }
          }
          break;

        default:
      }

      if (this.lookupsSearch) {
        lookup = lookup.filter(item =>
          item.label.match(new RegExp(this.lookupsSearch.trim(), "i"))
        );
      }
      return lookup;
    },

    data() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.lookup.slice(start, end);
    },

    filterValueIdOptions() {
      const filterValueIdOptions = [];
      this.values.forEach(function(item) {
        filterValueIdOptions.push({ value: item.id, text: item.name });
      });
      return filterValueIdOptions;
    },

    filterLabelCompareOptions() {
      return [
        { value: "", label: "" },
        {
          value: this.WptConstants.COMPARE_EQUALS,
          label: this.$t("Lbl_Equals")
        },
        {
          value: this.WptConstants.COMPARE_NOT_EQUALS,
          label: this.$t("Lbl_NotEquals")
        },
        {
          value: this.WptConstants.COMPARE_GREAT_THAN,
          label: this.$t("Lbl_GreatThan")
        },
        {
          value: this.WptConstants.COMPARE_GREAT_EQUAL,
          label: this.$t("Lbl_GreatEqual")
        },
        {
          value: this.WptConstants.COMPARE_LESS_THAN,
          label: this.$t("Lbl_LessThan")
        },
        {
          value: this.WptConstants.COMPARE_LESS_EQUAL,
          label: this.$t("Lbl_LessEqual")
        },
        {
          value: this.WptConstants.COMPARE_BEGIN_WITH,
          label: this.$t("Lbl_BeginWith")
        },
        {
          value: this.WptConstants.COMPARE_NOT_BEGIN_WITH,
          label: this.$t("Lbl_NotBeginWith")
        },
        {
          value: this.WptConstants.COMPARE_END_WITH,
          label: this.$t("Lbl_EndWith")
        },
        {
          value: this.WptConstants.COMPARE_NOT_END_WITH,
          label: this.$t("Lbl_NotEndWith")
        },
        {
          value: this.WptConstants.COMPARE_CONTAINS,
          label: this.$t("Lbl_Contains")
        },
        {
          value: this.WptConstants.COMPARE_NOT_CONTAINS,
          label: this.$t("Lbl_NotContains")
        },
        {
          value: this.WptConstants.COMPARE_BETWEEN,
          label: this.$t("Lbl_Between")
        },
        {
          value: this.WptConstants.COMPARE_NOT_BETWEEN,
          label: this.$t("Lbl_NotBetween")
        }
      ];
    },

    filterValueCompareOptions() {
      return [
        { value: "", label: "" },
        {
          value: this.WptConstants.COMPARE_EQUALS,
          label: this.$t("Lbl_Equals")
        },
        {
          value: this.WptConstants.COMPARE_NOT_EQUALS,
          label: this.$t("Lbl_NotEquals")
        },
        {
          value: this.WptConstants.COMPARE_GREAT_THAN,
          label: this.$t("Lbl_GreatThan")
        },
        {
          value: this.WptConstants.COMPARE_GREAT_EQUAL,
          label: this.$t("Lbl_GreatEqual")
        },
        {
          value: this.WptConstants.COMPARE_LESS_THAN,
          label: this.$t("Lbl_LessThan")
        },
        {
          value: this.WptConstants.COMPARE_LESS_EQUAL,
          label: this.$t("Lbl_LessEqual")
        },
        {
          value: this.WptConstants.COMPARE_BETWEEN,
          label: this.$t("Lbl_Between")
        },
        {
          value: this.WptConstants.COMPARE_NOT_BETWEEN,
          label: this.$t("Lbl_NotBetween")
        }
      ];
    },

    topBottomOptions() {
      return [
        { value: this.WptConstants.COMPARE_TOP, label: this.$t("Lbl_Top") },
        { value: this.WptConstants.COMPARE_BOTTOM, label: this.$t("Lbl_Bottom") }
      ];
    }
  },

  methods: {
    isItemSelected(item) {
      let testFunc = null;
      const testText = this.filterLabelValue;
      const testText1 = this.filterLabelValue1;
      const field = this.field;

      switch (this.filterLabelType) {
        case this.WptConstants.FILTER_LABEL_FORMULA:
          switch (this.filterLabelOperator) {
            case this.WptConstants.COMPARE_EQUALS: //equals
              switch (field.type) {
                case this.WptConstants.FIELD_NUMBER:
                  testFunc = function(item) {
                    return parseFloat(item.label) === parseFloat(testText);
                  };
                  break;
                default:
                  testFunc = function(item) {
                    return item.label.toLowerCase() === testText.toLowerCase();
                  };
              }
              break;
            case this.WptConstants.COMPARE_NOT_EQUALS: //does not equal
              switch (field.type) {
                case this.WptConstants.FIELD_NUMBER:
                  testFunc = function(item) {
                    return parseFloat(item.label) !== parseFloat(testText);
                  };
                  break;
                default:
                  testFunc = function(item) {
                    return item.label.toLowerCase() !== testText.toLowerCase();
                  };
              }
              break;
            case this.WptConstants.COMPARE_GREAT_THAN: //is great than
              switch (field.type) {
                case this.WptConstants.FIELD_NUMBER:
                  testFunc = function(item) {
                    return parseFloat(item.label) > parseFloat(testText);
                  };
                  break;
                default:
                  testFunc = function(item) {
                    return item.label.toLowerCase() > testText.toLowerCase();
                  };
              }
              break;
            case this.WptConstants.COMPARE_GREAT_EQUAL: //is great than or equal to
              switch (field.type) {
                case this.WptConstants.FIELD_NUMBER:
                  testFunc = function(item) {
                    return parseFloat(item.label) >= parseFloat(testText);
                  };
                  break;
                default:
                  testFunc = function(item) {
                    return item.label.toLowerCase() >= testText.toLowerCase();
                  };
              }
              break;
            case this.WptConstants.COMPARE_LESS_THAN: //is less than
              switch (field.type) {
                case this.WptConstants.FIELD_NUMBER:
                  testFunc = function(item) {
                    return parseFloat(item.label) < parseFloat(testText);
                  };
                  break;
                default:
                  testFunc = function(item) {
                    return item.label.toLowerCase() < testText.toLowerCase();
                  };
              }
              break;
            case this.WptConstants.COMPARE_LESS_EQUAL: //is less than or equal to
              switch (field.type) {
                case this.WptConstants.FIELD_NUMBER:
                  testFunc = function(item) {
                    return parseFloat(item.label) <= parseFloat(testText);
                  };
                  break;
                default:
                  testFunc = function(item) {
                    return item.label.toLowerCase() <= testText.toLowerCase();
                  };
              }
              break;
            case this.WptConstants.COMPARE_BEGIN_WITH: //begins with
              testFunc = function(item) {
                return new RegExp("^" + testText, "i").test(item.label);
              };
              break;
            case this.WptConstants.COMPARE_NOT_BEGIN_WITH: //does not begin with   http://stackoverflow.com/questions/899422
              testFunc = function(item) {
                return new RegExp("^(?!" + testText + ").+", "i").test(
                  item.label
                );
              };
              break;
            case this.WptConstants.COMPARE_END_WITH: //ends with
              testFunc = function(item) {
                return new RegExp(testText + "$", "i").test(item.label);
              };
              break;
            case this.WptConstants.COMPARE_NOT_END_WITH: //does not end with   http://stackoverflow.com/questions/16398471
              testFunc = function(item) {
                return new RegExp(".*(?<!" + testText + ")$", "i").test(
                  item.label
                );
              };
              break;
            case this.WptConstants.COMPARE_CONTAINS: //contains
              testFunc = function(item) {
                return new RegExp(testText, "i").test(item.label);
              };
              break;
            case this.WptConstants.COMPARE_NOT_CONTAINS: //does not contain   http://stackoverflow.com/questions/717644
              testFunc = function(item) {
                return new RegExp("^((?!" + testText + ").)*$", "i").test(
                  item.label
                );
              };
              break;
            case this.WptConstants.COMPARE_BETWEEN: //is between
              switch (field.type) {
                case this.WptConstants.FIELD_NUMBER:
                  testFunc = function(item) {
                    return (
                      parseFloat(item.label) >= parseFloat(testText) &&
                      parseFloat(item.label) <= parseFloat(testText1)
                    );
                  };
                  break;
                default:
                  testFunc = function(item) {
                    return (
                      item.label.toLowerCase() >= testText.toLowerCase() &&
                      item.label.toLowerCase() <= testText1.toLowerCase()
                    );
                  };
              }
              break;
            case this.WptConstants.COMPARE_NOT_BETWEEN: //is not between
              switch (field.type) {
                case this.WptConstants.FIELD_NUMBER:
                  testFunc = function(item) {
                    return (
                      parseFloat(item.label) < parseFloat(testText) ||
                      parseFloat(item.label) > parseFloat(testText1)
                    );
                  };
                  break;
                default:
                  testFunc = function(item) {
                    return (
                      item.label.toLowerCase() < testText.toLowerCase() ||
                      item.label.toLowerCase() > testText1.toLowerCase()
                    );
                  };
              }
              break;
            default:
          }
          if (testFunc) {
            return testFunc(item);
          }
          break;

        case this.WptConstants.FILTER_LABEL_INCLUDES:
          return this.filterLabelIncludes.indexOf(item.id) >= 0;

        case this.WptConstants.FILTER_LABEL_EXCLUDES:
          return this.filterLabelExcludes.indexOf(item.id) >= 0;

        default:
      }
      return false;
    },

    handleItemSelected(selected, item) {
      if (this.filterLabelType === this.WptConstants.FILTER_LABEL_INCLUDES) {
        if (selected) {
          this.filterLabelIncludes.push(item.id);
        } else {
          this.filterLabelIncludes = this.filterLabelIncludes.filter(
            id => id !== item.id
          );
        }
      } else if (
        this.filterLabelType === this.WptConstants.FILTER_LABEL_EXCLUDES
      ) {
        if (selected) {
          this.filterLabelExcludes.push(item.id);
        } else {
          this.filterLabelExcludes = this.filterLabelExcludes.filter(
            id => id !== item.id
          );
        }
      }
    },

    selectAll() {
      if (this.filterLabelType === this.WptConstants.FILTER_LABEL_INCLUDES) {
        this.filterLabelIncludes = this.lookup.map(item => item.id);
      } else if (
        this.filterLabelType === this.WptConstants.FILTER_LABEL_EXCLUDES
      ) {
        this.filterLabelExcludes = this.lookup.map(item => item.id);
      }
    },

    selectNone() {
      if (this.filterLabelType === this.WptConstants.FILTER_LABEL_INCLUDES) {
        this.filterLabelIncludes = [];
      } else if (
        this.filterLabelType === this.WptConstants.FILTER_LABEL_EXCLUDES
      ) {
        this.filterLabelExcludes = [];
      }
    },

    close() {
      this.$store.commit("dialog/close");
    },

    handleConfirm() {
      let newField = { id: this.field.id };

      if (this.sortType !== -1) {
        newField.sort = { filterType: this.filterType };
        newField.sort = {
          sortType: this.sortType,
          sortDescend: this.sortDescend
        };
        if (this.sortType === this.WptConstants.SORT_BY_VALUE) {
          newField.sort.sortValueId = this.sortValueId;
        }
      }

      if (this.filterType !== -1) {
        newField.filter = { filterType: this.filterType };
        switch (this.filterType) {
          case this.WptConstants.FILTER_BY_LABEL:
            newField.filter.filterLabelType = this.filterLabelType;
            switch (this.filterLabelType) {
              case this.WptConstants.FILTER_LABEL_FORMULA:
                newField.filter.filterLabelOperator = this.filterLabelOperator;
                newField.filter.filterLabelValue = this.filterLabelValue;
                newField.filter.filterLabelValue1 = this.filterLabelValue1;
                break;

              case this.WptConstants.FILTER_LABEL_INCLUDES:
                newField.filter.filterLabelIncludes = this.filterLabelIncludes;
                break;

              case this.WptConstants.FILTER_LABEL_EXCLUDES:
                newField.filter.filterLabelExcludes = this.filterLabelExcludes;
                break;

              default:
            }
            break;

          case this.WptConstants.FILTER_BY_VALUE:
            newField.filter.filterValueId = this.filterValueId;
            newField.filter.filterValueOperator = this.filterValueOperator;
            newField.filter.filterValue = this.filterValue;
            newField.filter.filterValue1 = this.filterValue1;
            break;

          case this.WptConstants.FILTER_BY_TOP:
            newField.filter.filterTopValueId = this.filterTopValueId;
            newField.filter.filterTopOperator = this.filterTopOperator;
            newField.filter.filterTopValue = this.filterTopValue;
            break;

          default:
        }
      }

      newField.hideSubtotal = this.hideSubtotal;
      if (this.customName !== this.field.name) {
        newField.name = this.customName;
      }
      this.$store.dispatch("wpt/changeFieldSetting", {
        sheet: this.sheet,
        newField,
        source: this.source
      });

      this.close();
    }
  }
};
</script>

