<template>
  <div class="wpt-side-buttons">
    <wpt-button
      :class="navPanel === 'FIELDS' ? 'wpt-selected' : ''"
      :label="$t('Lbl_FieldsList')"
      :vertical="true"
      type="plain"
      @click="changeNavPanel('FIELDS')"
    />

    <v-popover
      :container="popoverContainer"
      placement="right-end"
    >
      <wpt-button 
        icon="export" 
        type="plain" />
      <template slot="popover">
        <div 
          class="wpt-menu-item" 
          @click="exportToCsv">
          <icon name="export-csv" />
          <span style="margin-left:3px">{{ $t("Lbl_ExportToCsv") }}</span>
        </div>
        <div 
          class="wpt-menu-item" 
          @click="exportToExcel">
          <icon name="export-excel" />
          <span style="margin-left:3px">{{ $t("Lbl_ExportToExcel") }}</span>
        </div>
        <div 
          class="wpt-menu-item" 
          @click="print">
          <icon name="print" />
          <span style="margin-left:3px">{{ $t("Lbl_Print") }}</span>
        </div>
      </template>
    </v-popover>
  </div>
</template>

<script>
import {
  exportDataSetToExcel,
  exportDataSetToCsv,
  printContent
} from "~/services/ExportService";

export default {
  props: {
    navPanel: {
      type: String,
      default: null
    },
    changeNavPanel: {
      type: Function,
      default: () => {}
    }
  },
  computed: {
    popoverContainer() {
      return "."+this.$store.state.system.containerClass;
    },
    drillThrough() {
      return this.$store.state.modal.data;
    }
  },

  methods: {
    exportToExcel() {
      const drillThrough = this.drillThrough;
      const data = [
        drillThrough.fields.map(field => field.id),
        ...drillThrough.data.map(row =>
          drillThrough.fields.map(field => row[field.id])
        )
      ];
      exportDataSetToExcel("drillthrough.xlsx", "drillthrough", data);
    },

    exportToCsv() {
      const drillThrough = this.drillThrough;
      const data = [
        drillThrough.fields.map(field => field.id),
        ...drillThrough.data.map(row =>
          drillThrough.fields.map(field => row[field.id])
        )
      ];
      exportDataSetToCsv("drillthrough.csv", ",", data);
    },
    print() {
      const drillThrough = this.drillThrough;
      const data = [
        drillThrough.fields.map(field => field.id),
        ...drillThrough.data.map(row =>
          drillThrough.fields.map(field => row[field.id])
        )
      ];
      printContent(JSON.stringify(data));
    }
  }
};
</script>

<style></style>



