import FileSaver from "file-saver";
import XLSX from "xlsx";
import TableExport from "tableexport";
import html2pdf from "html2pdf.js";

export const exportDataSetToExcel = (fileName, sheetName, data) => {
  //var data = [[1,2,3],[true, false, null, "sheetjs"],["foo","bar",new Date("2014-02-19T14:30Z"), "0.3"], ["baz", null, "qux"]]

  const datenum = (v, date1904) => {
    if (date1904) v += 1462;
    const epoch = Date.parse(v);
    return (epoch - new Date(Date.UTC(1899, 11, 30))) / (24 * 60 * 60 * 1000);
  };

  const sheet_from_array_of_arrays = data => {
    const ws = {};
    const range = { s: { c: 10000000, r: 10000000 }, e: { c: 0, r: 0 } };
    for (let R = 0; R != data.length; ++R) {
      for (let C = 0; C != data[R].length; ++C) {
        if (range.s.r > R) range.s.r = R;
        if (range.s.c > C) range.s.c = C;
        if (range.e.r < R) range.e.r = R;
        if (range.e.c < C) range.e.c = C;
        const cell = { v: data[R][C] };
        if (cell.v == null) continue;
        const cell_ref = XLSX.utils.encode_cell({ c: C, r: R });

        if (typeof cell.v === "number") cell.t = "n";
        else if (typeof cell.v === "boolean") cell.t = "b";
        else if (cell.v instanceof Date) {
          cell.t = "n";
          cell.z = XLSX.SSF._table[14];
          cell.v = datenum(cell.v);
        } else cell.t = "s";

        ws[cell_ref] = cell;
      }
    }
    if (range.s.c < 10000000) ws["!ref"] = XLSX.utils.encode_range(range);
    return ws;
  };

  function Workbook() {
    if (!(this instanceof Workbook)) return new Workbook();
    this.SheetNames = [];
    this.Sheets = {};
  }

  const wb = new Workbook(),
    ws = sheet_from_array_of_arrays(data);

  /* add worksheet to workbook */
  wb.SheetNames.push(sheetName);
  wb.Sheets[sheetName] = ws;
  const wbout = XLSX.write(wb, {
    bookType: "xlsx",
    bookSST: true,
    type: "binary"
  });

  function s2ab(s) {
    const buf = new ArrayBuffer(s.length);
    const view = new Uint8Array(buf);
    for (let i = 0, len = s.length; i !== len; ++i) {
      view[i] = s.charCodeAt(i) & 0xff;
    }
    return buf;
  }
  FileSaver.saveAs(
    new window.Blob([s2ab(wbout)], { type: "application/octet-stream" }),
    fileName
  );
};

export const exportDataSetToCsv = (fileName, delimiter, data) => {
  let csvContent = "",
    dataString;
  data.forEach((infoArray, index) => {
    dataString = infoArray.join(delimiter);
    csvContent += index < data.length ? dataString + "\n" : dataString;
  });
  FileSaver.saveAs(
    new window.Blob([csvContent], { type: "text/csv;charset=utf-8;" }),
    fileName
  );
};

export const exportContentToTxtFile = (content, fileName) => {
  FileSaver.saveAs(
    new window.Blob([content], { type: "text/plain;" }),
    fileName
  );
};

export const exportContentToHtmlFile = (content, fileName) => {
  FileSaver.saveAs(
    new window.Blob([content], { type: "text/html;" }),
    fileName + ".html"
  );
};

export const exportHtmlToExcel = (elementId, filename) => {
  const element = document.getElementById(elementId);
  const instance = TableExport(element, {
    formats: ["xlsx"],
    filename,
    exportButtons: false
  });
  const exportData = instance.getExportData()[elementId]["xlsx"];
  instance.export2file(
    exportData.data,
    exportData.mimeType,
    exportData.filename,
    exportData.fileExtension
  );
};

export const exportHtmlToPdf = (elementId, filename) => {
  const element = document.getElementById(elementId);
  html2pdf(element, {
    margin: 1,
    filename: filename + ".pdf",
    image: { type: "jpeg", quality: 0.98 },
    html2canvas: { dpi: 192, letterRendering: true },
    jsPDF: { unit: "in", format: "letter", orientation: "portrait" }
  });
};

export const printContent = content => {
  const PW = window.open("", "_blank", "Print content");

  //IF YOU HAVE DIV STYLE IN CSS, REMOVE BELOW COMMENT AND ADD CSS ADDRESS
  //PW.document.write('<link rel="stylesheet" type="text/css" href="CSS-FILE-ADDRESS"/>');

  PW.document.write(content);
  PW.document.close();
  PW.focus();
  PW.print();
  PW.close();
};



