import { mergeRecursive } from "~/services/CommonUtils";
import { defaultWptOptions } from "./state";

export default {
  setLocale(state, locale) {
    state.locale = locale;
  },

  setView(state, view) {
    state = { ...mergeRecursive(state, {wpt: {view}}) };
  },

  setOptions(state, options) {
    state = { ...mergeRecursive(state, options) };
  },

  resetWptOptions(state) {
    state.wpt = JSON.parse(JSON.stringify(defaultWptOptions));
  },

  setWptOptions(state, wptOptions) {
    const wpt = mergeRecursive(JSON.parse(JSON.stringify(defaultWptOptions)), wptOptions);
    state = { ...mergeRecursive(state, {wpt}) };
  },

  changeSourceOptions(state, sourceOptions) {
    state = { ...mergeRecursive(state, {wpt: {source: sourceOptions}}) };
  },

  changeSheetOptions(state, sheetOptions) {
    state = { ...mergeRecursive(state, {wpt: {sheet: sheetOptions}}) };
  }

};



