import utils from "./utils.js";
export default {
  methods: {
    body1Mousewheel(e) {
      const body2 = this.$el.querySelector(
        ".wpt-table-rightview .wpt-table-body"
      );

      const e1 = e.originalEvent || window.event || e;
      const scrollHeight = e1.wheelDelta || e1.detail * -1;
      body2.scrollTop = body2.scrollTop - scrollHeight;
    },
    body2Scroll(e) {
      const view2 = this.$el.querySelector(".wpt-table-rightview");
      const body1 = this.$el.querySelector(
        ".wpt-table-leftview .wpt-table-body"
      );
      const body2 = this.$el.querySelector(
        ".wpt-table-rightview .wpt-table-body"
      );

      if (body1) {
        body1.scrollTop = body2.scrollTop;
      }
      view2.querySelector(".wpt-table-header").scrollLeft = body2.scrollLeft;
    }
  },
  mounted() {
    const body1 = this.$el.querySelector(".wpt-table-leftview .wpt-table-body");
    const body2 = this.$el.querySelector(
      ".wpt-table-rightview .wpt-table-body"
    );

    utils.bind(body1, "mousewheel", this.body1Mousewheel);
    utils.bind(body2, "scroll", this.body2Scroll);
  },

  beforeDestroy() {
    const body1 = this.$el.querySelector(".wpt-table-leftview .wpt-table-body");
    const body2 = this.$el.querySelector(
      ".wpt-table-rightview .wpt-table-body"
    );

    utils.unbind(body1, "mousewheel", this.body1Mousewheel);
    utils.unbind(body2, "scroll", this.body2Scroll);
  }
};



