<template>
  <div
    class="wpt-table-views"
    :style="{'width': width+'px', 'height': height+'px','background-color':tableBgColor}"
  >
    <!--left view-->
    <div
      v-if="frozenCols.length > 0"
      class="wpt-table-leftview"
      :style="{'width':leftViewWidth+'px', 'height': height+'px'}"
    >
      <!--left header-->
      <div
        class="wpt-table-header"
        :style="{'width': leftViewWidth+'px', 'height': headerHeight+'px', 'background-color':headerBgColor}"
      >
        <table border="0" cellspacing="0" cellpadding="0">
            <tbody>

            <template v-if="frozenTitleCols.length > 0">
              <tr v-for="row in frozenTitleCols">
                <td v-for="col in row"
                    :colspan="col.colspan"
                    :rowspan="col.rowspan"
                    @mousemove.stop="handleTitleMouseMove($event,col.fields)"
                    @mousedown.stop="handleTitleMouseDown($event)"
                    @mouseout.stop="handleTitleMouseOut()"
                >
                  <div
                    class="wpt-table-header-cell"
                    :style="{'width':headerColumnWidth(col.fields)+'px','height':headerColumnHeight(col.rowspan)+'px','text-align':col.titleAlign}">
                    {{ col.title }}
                  </div>
                </td>
              </tr>
            </template>

            <template v-else>
              <tr class="wpt-table-header-row">
                <td v-for="col in frozenCols"
                    @mousemove.stop="handleTitleMouseMove($event,col.fields)"
                    @mousedown.stop="handleTitleMouseDown($event)"
                    @mouseout.stop="handleTitleMouseOut()"
                >
                  <div
                    class="wpt-table-header-cell"
                    :style="{'width':col.width+'px','height':headerRowHeight+'px','text-align':col.titleAlign}">
                    {{ col.title }}
                  </div>
                </td>
              </tr>
            </template>
            </tbody>
          </table>
      </div>
      <!--left body-->
      <div
        class="wpt-table-body"
        :style="{'width': leftViewWidth+'px', 'height': bodyHeight+'px', 'padding-bottom': '20px'}"
      >
        <table cellspacing="0" cellpadding="0" border="0">
            <tbody>
            <tr v-for="(item,rowIndex) in tableData" class="wpt-table-row"
                :style="[trBgColor(rowIndex+1)]">
              <td
                v-for="(col,colIndex) in frozenCols"
                :key="colIndex">
                <div
                  class="wpt-table-body-cell"
                  :style="{'width':col.width+'px','height': rowHeight+'px','line-height':rowHeight+'px','text-align':col.columnAlign}"
                >
                                  <span v-if="typeof col.componentName ==='string' && col.componentName.length > 0">
                                      <component :rowData="item" :field="col.field ? col.field : ''"
                                                 :index="rowIndex" :is="col.componentName"></component>
                                  </span>
                  <span v-else-if="typeof col.formatter==='function'"
                        v-html="col.formatter(item,rowIndex,pagingIndex,col.field)"></span>
                  <span v-else>
                                          {{item[col.field]}}
                                  </span>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
      </div>
    </div>

    <!--right view-->
    <div
      class="wpt-table-rightview"
      :style="{'width': rightViewWidth+'px', 'height': height+'px'}"
    >
      <!--right header-->
      <div
        class="wpt-table-header"
        :style="{'width': rightViewWidth +'px', 'height': headerHeight+'px', 'background-color':headerBgColor}"
      >
        <table border="0" cellspacing="0" cellpadding="0">
            <tbody>

            <template v-if="noFrozenTitleCols.length > 0">
              <tr v-for="row in noFrozenTitleCols">
                <td v-for="col in row"
                    :colspan="col.colspan"
                    :rowspan="col.rowspan"
                    @mousemove.stop="handleTitleMouseMove($event,col.fields)"
                    @mousedown.stop="handleTitleMouseDown($event)"
                    @mouseout.stop="handleTitleMouseOut()"
                >
                  <div
                    class="wpt-table-header-cell"
                    :style="{'width':headerColumnWidth(col.fields)+'px','height':headerColumnHeight(col.rowspan)+'px','text-align':col.titleAlign}">
                    {{ col.title }}
                  </div>
                </td>
              </tr>
            </template>

            <template v-else>
              <tr class="wpt-table-header-row">
                <td v-for="(col,colIndex) in noFrozenCols"
                    @mousemove.stop="handleTitleMouseMove($event,col.fields)"
                    @mousedown.stop="handleTitleMouseDown($event)"
                    @mouseout.stop="handleTitleMouseOut()"
                >
                  <div
                    class="wpt-table-header-cell"
                    :style="{'width':col.width+'px','height':headerRowHeight+'px','text-align':col.titleAlign}">
                    {{ col.title }}
                  </div>
                </td>
              </tr>
            </template>
            </tbody>
          </table>
      </div>
      <!--right body-->
      <div
        class="wpt-table-body"
        :style="{'width': rightViewWidth+'px', 'height': bodyHeight+'px'}"
      >
        <div
          v-if="tableData.length === 0"
          :style="{'width': rightBodyTableWidth +'px', 'height': '1px'}"
        ></div>
        <table v-else cellspacing="0" cellpadding="0" border="0">
          <tbody>
          <tr :key="rowIndex" v-for="(item,rowIndex) in tableData" class="wpt-table-row"
              :style="[trBgColor(rowIndex+1)]"
          >
            <td
              v-for="(col,colIndex) in noFrozenCols"
              :key="colIndex">
              <div
                class="wpt-table-body-cell"
                :style="{'width':col.width+'px','height': rowHeight+'px','line-height':rowHeight+'px','text-align':col.columnAlign}"
              >
                                <span v-if="typeof col.componentName ==='string' && col.componentName.length > 0">
                                    <component :rowData="item" :field="col.field ? col.field : ''" :index="rowIndex"
                                               :is="col.componentName"></component>
                                </span>
                <span v-else-if="typeof col.formatter==='function'"
                      v-html="col.formatter(item,rowIndex,pagingIndex,col.field)">
                                </span>
                <span v-else>
                                 {{item[col.field]}}
                            </span>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>

    </div>

    <!--drag line-->
    <div v-show="isDragging" class="wpt-table-drag-line"></div>
  </div>
</template>


<script>
import frozenColumnsMixin from "./frozen-columns-mixin.js";
import dragWidthMixin from "./drag-width-mixin.js";
import titleCellMergeMixin from "./title-cell-merge-mixin.js";
import tableScrollMixin from "./table-scroll-mixin.js";

export default {
  mixins: [frozenColumnsMixin, dragWidthMixin, titleCellMergeMixin, tableScrollMixin],
  props: {
    width: {
      type: Number,
      required: true,
      default: 0
    },
    height: {
      type: Number,
      require: true,
      default: 0
    },
    headerRowHeight: {
      type: Number,
      default: 25
    },
    rowHeight: {
      type: Number,
      default: 25
    },
    tableBgColor: {
      type: String,
      default: "#fff"
    },
    headerBgColor: {
      type: String,
      default: "#efefef"
    },
    oddBgColor: {
      type: String,
      require: false
    },
    evenBgColor: {
      type: String,
      require: false
    },

    columns: {
      type: Array,
      require: true
    },
    titleRows: {
      type: Array,
      require: true,
      default: function() {
        return [];
      }
    },
    tableData: {
      type: Array,
      require: true,
      default: function() {
        return [];
      }
    },
    pagingIndex: {
      type: Number,
      default: 0
    },
    columnWidthDrag: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    frozenCols() {
      return this.columns.filter(x => x.isFrozen);
    },
    noFrozenCols() {
      return this.columns.filter(x => !x.isFrozen);
    },
    leftViewWidth() {
      return this.frozenCols.length === 0
        ? 0
        : this.frozenCols.reduce((total, curr) => total + curr.width, 0);
    },
    rightViewWidth() {
      return this.width - this.leftViewWidth;
    },
    rightBodyTableWidth() {
      return this.noFrozenCols.reduce((total, curr) => total + curr.width, 0);
    },
    headerHeight() {
      return this.titleRows.length > 0
        ? this.headerRowHeight *
            (this.titleRows.length + this.getTitleRowspanTotalCount)
        : this.headerRowHeight;
    },
    bodyHeight() {
      return this.height - this.headerHeight;
    }
  },
  methods: {
    trBgColor(num) {
      if (
        (this.evenBgColor && this.evenBgColor.length > 0) ||
        (this.oddBgColor && this.oddBgColor.length > 0)
      ) {
        return num % 2 === 0
          ? { "background-color": this.evenBgColor }
          : { "background-color": this.oddBgColor };
      }
    },

    headerColumnWidth(fields) {
      let result = 0;
      if (Array.isArray(fields)) {
        var matchItems = this.columns.filter((item, index) => {
          return fields.some(x => x === item.field);
        });

        result = matchItems.reduce((total, curr) => total + curr.width, 0);
      } else {
        console.error("the fields attribute must be a array in titleRows");
      }
      return result;
    },

    headerColumnHeight(rowspan) {
      if (rowspan && rowspan > 0) {
        return this.headerRowHeight * rowspan;
      } else {
        return this.headerRowHeight;
      }
    }
  },
  watch: {
    pagingIndex: {
      handler: function() {
        const body1 = this.$el.querySelector(
          ".wpt-table-leftview .wpt-table-body"
        );
        const body2 = this.$el.querySelector(
          ".wpt-table-rightview .wpt-table-body"
        );

        if (body1) {
          body1.scrollTop = 0;
        }
        body2.scrollTop = 0;
      }
    }
  }
};
</script>

<style>
</style>



