import Worker from "simple-web-worker";
import { executeMdx } from "~/services/OlapService";
import WptConstants from "~/WptConstants";
import { getFilteredData } from "~/services/MemoryService";

export default {
  getDrillThroughData(store, { row, column, i18n }) {
    const data = store.state.data;

    if (!data || !data.loading) {
      store.commit("setData", { loading: true });

      const sheet = store.rootState.wpt.sheet;
      const source = store.rootState.wpt.source;

      try {
        switch (source.mode) {
          case WptConstants.MODE_MEMORY:
            Worker.run(getFilteredData, [
              sheet.pivot,
              source,
              WptConstants,
              true
            ]).then(({ data, fieldsMap }) => {
              const rows = sheet.pivot.rows;
              const columns = sheet.pivot.columns;

              let rowValues,
                colValues,
                arr = [];
              rowValues = row.id.replace(/V[0-9]*/, "").split("-");
              rowValues.splice(0, 1);
              colValues = column.id.replace(/V[0-9]*/, "").split("-");
              colValues.splice(0, 1);

              for (let i = data.length; i > 0; i--) {
                const row = data[i - 1];
                let filterOut = false;
                for (let j = 0; j < rowValues.length; j++) {
                  const field = fieldsMap[rows[j]];
                  if (
                    field.lookup[row[field.id]] !== parseInt(rowValues[j], 10)
                  ) {
                    filterOut = true;
                    break;
                  }
                }
                if (!filterOut) {
                  for (let j = 0; j < colValues.length; j++) {
                    const field = fieldsMap[columns[j]];
                    if (
                      field.lookup[row[field.id]] !== parseInt(colValues[j], 10)
                    ) {
                      filterOut = true;
                      break;
                    }
                  }
                  if (!filterOut) {
                    arr.push(row);
                  }
                }
              }

              store.commit("setData", {
                data: arr,
                fields: source.fields
              });
            });

            break;

          case WptConstants.MODE_OLAP: {
            const limit = store.rootState.options.limit;
            const serverOptions = store.rootState.options.server;

            const pivotData = sheet.sheetData.pivotData;

            const rowItem = pivotData.rowTree.filter(
              item => item.id === row.id
            )[0].fields;
            const colItem = pivotData.colTree.filter(
              item => item.id === column.id
            )[0].fields;

            const statement =
              "DRILLTHROUGH MAXROWS " +
              limit.olapDrillThroughMaxRows +
              " SELECT " +
              (rowItem.length > 0
                ? (rowItem.length > 1 ? "crossjoin( " : " ") +
                  rowItem
                    .map(function(field) {
                      return field.name;
                    })
                    .join(",") +
                  (rowItem.length > 1 ? ")" : "") +
                  " on 0, "
                : "") +
              (colItem.length > 1 ? "crossjoin( " : " ") +
              colItem
                .map(function(field) {
                  return field.name;
                })
                .join(",") +
              (colItem.length > 1 ? ")" : "") +
              " on " +
              (rowItem.length > 0 ? 1 : 0) +
              " FROM  [" +
              source.data.cubeName +
              "]";

            store.dispatch(
              "system/loadingMessage",
              i18n("Msg_DrillingThrough"),
              {
                root: true
              }
            );

            const drillThrough = { fields: [], orders: [] };

            executeMdx(serverOptions, statement, source, (error, response) => {
              if (error) {
                store.dispatch("system/errorMessage", error, {root: true});
                store.commit("setData", { loading: false, error });
              } else {
                store.dispatch("system/clearMessage", null, {root: true});

                if (response.data) {
                  // mondrian olap drillthough will return an extra row on top,
                  // all value of props in this row are all the same
                  // remove it if this is the case
                  const data0 = response.data[0];
                  let value = null;
                  let countLine = true;
                  for (let prop in data0) {
                    if (value === null) {
                      value = data0[prop];
                      continue;
                    }

                    if (value != data0[prop]) {
                      countLine = false;
                      break;
                    }
                  }
                  if (countLine) {
                    drillThrough.data = response.data.slice(1);
                  } else {
                    drillThrough.data = response.data || [];
                  }
                } else {
                  drillThrough.data = [];
                }

                drillThrough.fields = response.fields || [];

                if (drillThrough.fields.length === 0) {
                  // drillThrough.fields = response.fields.map(function(field, index) {
                  //   return {
                  //     index: index,
                  //     selected: true
                  //   };
                  // });
                  if (drillThrough.data.length > 0) {
                    for (let prop in drillThrough.data[0]) {
                      drillThrough.fields.push({
                        id: prop,
                        name: prop
                      });
                    }
                  }
                }
                store.commit("setData", drillThrough);
              }
            });

            break;
          }

          default:
        }
      } catch (error) {
        store.dispatch("system/errorMessage", error, { root: true });
        store.commit("setData", { loading: false, error });
      }
    }
  }
};



